export default {
  data: () => ({
    domain: "http://mementovivere.life/",
    translations_array: null,
    translations: {
      Bosnian: {
        title: "Jasmin Jusuf Jusufović",
        subtitle: "Mali Princ",
        language: "Bosnian",
        paragraphs: [
          'U zoru tog 8. jula drobilo je negdje u brdima, ali dovoljno glasno da nas je probudilo. Tad sam se premjestio u svoje sklonište kod zamrzivača, gdje je bio najdeblji zid. Tih nekoliko dana smo nebrojeno mnogo puta trčali do najdebljeg zida u kući, a njega smo ocjenjivali u trku. Da skratim vrijeme u svom leglu ispred zamrzivača, od tog devetog jula brojao sam granate koje su se čule po cijeli dan. Znalo ih je nekad biti po trideset odjednom. Ljudi su izlazili po ulici, ali samo uz pravilo "prva ubija". Ulicom je tutnjao Ramo Hljebara s ranjenima u tamiću.',
          'Onda se oko podne utišalo. Ja sam otišao da vidim šta mi rade Edo i Dado. A znao sam da je i babo negdje u komšiluku. Kad sam sišao niz Miloševu pomislio sam da je babo možda otišao kod kovača preko rijeke. Eksplozija granate me je našla nasred čistine pored Naserove brane, ali najveća mi je briga bila ustrčati nazad majci da barem vidi da sam ja OK. Uletio sam usput dajnici u kuću i viknuo "odoh ja mami da se ne sikira", a dajnica me je jedva zadržala u skloništu Edinom i Dadinom pod stolom, rekavši da mama sigurno zna da sam sa njima.',
          'I pored svega smo se inadili da život ide normalno. Desetog izjutra popesmo se na sprat da poradimo s nanom njenu sobu. Uzeo sam neku ponjavu i izišao na terasu da ju otresem. Granata je pala iznad kuće. Zemlja je padala po krovu ko kiša, a ja sam desetak sekundi visio na zidu. U svoj toj frci pod granatiranjem, kad se oko podne utišalo, babo i mama su odlučili da idemo strini da mi saljeva stravu, jer od one granate više nisam progovorio. Mislili su da mi je nešta bilo. Tamo sam zavirivao u činiju s vodom i olovom i govorio strini "đe ba vidiš lava?”',
          "Strina je živjela u Kazanima, a mi u čaršiji, što je bilo daleko za hodanje pod opasnošću granata. Ni dan danas mi neće biti jasno kako su oni iskomunicirali da se nađemo u kući kod moje tetke na autobusnoj stanici, na pola puta, bez telefona i bez ikakvih veza. Kad smo obavili stravu, ja se nisam osjećao nimalo manje uplašenim. Onda je opet počelo granatirati. Kod nas su bili Edo i Dado, Edo sa mnom kod zamrzivača, a Dado kod drugog debljeg zida. Neko je viknuo da pada po Starom gradu. Ja se počnem brinuti za svog druga Kemu.",
          "Ujutro tog 11. jula, zarana je već počela pucnjava, bliže nego ikad tih dana. Bilo je vruće. Mama i babo su pakovali u cekere stvari, valjda, ako već budemo išli da ne idemo kao onda iz Drinjače praznih ruku. Ja sam skočio i u svoj ruksačić strpao “Malog princa” i “1000 zašt/zato”. Granatiranje nije prestajalo cijelu zoru i jutro. Niz ulicu se sve više i više glava počelo kretati iz čaršije, trčeći. Mi smo nešto uludo čekali, a onda je neko iz te gomile naroda viknuo: “Četnici sišli na pijacu!” Zgrabili smo one ruksake, otac je zgrabio i mene kao ruksak, pa sam preletio s brda na kojem je bila naša kuća u onu gomilu naroda koja je bježala. Iza nas smo čuli rafale i pucnje. Trčeći niz ulicu, na ogradi koju je razvalila granata što je pobila djecu dok su igrali fudbalski turnir ispred naše škole davno prije, poderao sam rukav na desnoj ruci. Možda sam poderao i ruku, ali tad to nisam osjetio. Samo sam vidio kako me je mati strgnula od ograde, a onda smo utrčali u kuću tetke Munevere na autobusnoj stanici. Zbog nečega smo se zaustavili. Babo je otrčao nazad prema čaršiji, a majka je meni s leđa skinula moj ruksak, iz njega istresla knjige, pogledala me, ja slegnuo ramenima. Tako je ostao moj Mali princ. Kad me je vidjela sleđenog nad knjigama, majka me je tješila kako bi mi knjige bile preteške na leđima dok budemo bježali i trčali... Ja sam šutio, mislio sam, možda i ne bi, ali vidio sam da više ništa nema ni smisla, ni fajde",
          "Kasnije je u kuću ušao i babo, s vrećom bijelog brašna na leđima. Reče da je narod obio Naserov magacin. Iz vreće nam je tetka Munevera svima napravila uštipke. Ja sam bio zaboravio da do tih uštipaka ništa danima nisam jeo i zaboravio sam da sam bio gladan. Pojedoh jedva jedan. Ubrzo smo se rastali. Babo me je zavaravao da ide nešto donijeti, ali je ustvari otišao preko šume. Mi smo se našli sa dajnicom Senom i krenuli smo dalje. Oko bolnice se stvorila gužva. Vidio sam ljude kako skaču preko balkona u okolnim zgradama. Mi smo uskočili u jednu od njih.",
          "Nismo dugo ostali u tami te zgrade, krenuli smo dalje, ali dalje od bolnice nismo mogli. Narod je pokušavao da uđe u kamp UN-a u Vezionici, a neko je rekao da Zulfo Tursun vraća dječake od bolnice nazad. Tako smo se izgubili i sa dajnicom. Majka, nana i ja smo se zaustavili ispod jedne od zgrada kod bolnice, a iznad Vezionice. Negdje u blizini je pala granata. Prvo se na mene bacila nana, pa onda majka, a na nas se sasulo neko staklo koje je preživjelo na toj zgradi. Sjećam se da sam jedva disao ispod nane i majke.",
          'Poslije granate su žene još više navalile na kapiju i ogradu Vezionice. I nas smo se troje spustili u gomilu na cesti. Kroz galamu sam prepoznao glas: “Husnija!!!” Babo se vratio iz šume. "Ja ću sa vama pa šta bude…" Negdje je u jednom dijelu provaljena ograda Vezionice. Babo me je nekako unio u krug kampa, i otišli smo u garažu UN-ovih vozila i transportera. Onda je pala još jedna granata. Geleri su po limu garaže probijali rupice kroz koje je počela da se protiskuje svjetlost u trakama. Izgubili smo majku. Izgubili smo nanu. Nije bilo ni dajnice, ni Ede ni Dade, ni keke, ni dajdže Kike... bili smo samo nas dvojica. Babo je vidio kako se UN-ovci izvlače ispod Vezionice u kamionima, zgrabio me I, dotrčavši do jednog kamiona, bacio karoseriju.',
          "Pomogao je još mnogima da se uspnu na kamion, i sva ta rulja me je pritisnula uz stranice kamiona. Uspio sam samo da okrenem glavu prema vanjskoj strani da mogu disati. Kroz gomilu ljudi sam osjetio da smo babo i ja, od gužve i stiske na kamionu, ispustili ruke. Ljudi su se putem kačili po kamionu i meni je nestajalo vazduha ondje gdje sam ga malo imao. Kamion je sporo, došao u drugu bazu, u fabriku Akumulatorku. Kroz kordon ljudi i vojnika UN-a babo i ja smo završili u tami hale. Usput smo vidjeli tetka Ibru, na nosilima.",
          "Naroda je u fabrici bilo sve više i više. Babo se ishrvao s nekim da nam nađe jedna nosila da me na njih posadi da sjednem. Sjeo sam uz neki zid. Pored provaljenog ulaza. Nosila su bila mokra. Od krvi. Sjedili smo na nosilima, a ja sam uperio oči na ona vrata kroz koja smo došli ne bi li se na njih odnekud pojavila majka. Vrijeme je izgubilo svaki kontekst. Odjednom je na vrata ušla i majka zajapurena, uplakana, kao da se s nekim upravo hrvila. Odmah nas je ugledala. Rekla je da joj UNPROFORCI nisu bili dali da uđe u fabriku pa se s jednim vojnikom svađala i borila, pa se nekako uvukla. Babo je kasnije otišao da nam nađe vode. Donio je neku flašu koja je smrdila na benzin i u njoj malo vode. Rekao je da je našao dajnicu, Edu i Dadu i dajdžu Kiku, ali nije bilo nane. Majka je dodala da je nana ostala u drugoj fabrici s našom, trudnom, kekom.",
          "Nisam više mogao slušati galamu, vrisku, gledati u okrvavljena lica i sivilo fabričke hale. Našao sam neku rupu u zidu iza leđa, i tu stavio glavu i nekako, u svemu tome, kao da sam zaspao. Prenuo sam se koji minut kasnije, mati pita što ne spavam, hoću kod dajnice, ići ćemo kasnije. Hoću onda u wc. Odvela me kroz ulaz do nas iza fabrike, tuda sam vidio opet okrvavljene i poderane ljude, mnogo izmeta i krvavih krpa zavoja. Rekao sam ne mogu u wc, i više nisam ni išao.",
          "U neka doba majka je panično počela da pretura naše torbe. Uzela je snop nekog escajga i otrčala da to baci. U fabrici je narod prepričavao da četnici patroliraju po fabrici odjeveni u UN uniforme i samo traže razlog da nekoga izvedu i strijeljaju.",
          "“Šta će ti ženo Božja to?” pitaše ju babo. “Otkud bolan znam đe ćemo poslije ovog i kako ćemo završiti. Mislila sam zatrebaće.” Nakita odavno nije imala, a tad je uzela i pobacala sve što smo imali od metala. Vidjela je da više ne može da me odbije, pa smo nekad kad je narod prestao da ulazi, otišli da nađemo dajnicu i dajdžu Kiku. Dajdža Kiko je bio strašno blijed. On je bio osoba sa posebnim potrebama, ali nikad mi kao do tad nije bio tako uplašen. Majka je pitala dajnicu gdje joj je Senaid, moj Mladi Dajdža, rekla je da je otišao preko šume. Ja sam prišao Edi i Dadi, sjeo kod njih na neku dekicu prostrtu po betonu. Bilo je tvrdo i hladno. Toliko mnogo lica koje nikad nismo prije vidjeli sada je bilo oko nas u hali. Nikad ih nismo vidjeli, ali kao da smo ih oduvijek poznavali. Halom su samo prolazili talasi panike. Do ušesa su dolazile vijesti da izvode iz hale i kolju, i siluju, i strijeljaju. Mama i babo su domunđavali sa ljudima oko nas da će se sve završiti brzo, da bismo trebali biti OK tu sa UNPROFOR-cima, ali nam je, dok se smrkavalo, postalo sasvim jasno da smo u tim fabrikama, mi ustvari u logoru.",
          "Nekako kroz noć smo progurali... Ja sam opet glavu uvukao u onu svoju rupu u zidu, majka se tijelom prislonila uz mene kao da me stisne, da se osjećam sigurnije, sve što sam čuo svu noć bili su uzdasi, dječiji plač, i negdje u daljini iza fabrike kroz onaj ulaz, vriske. Da li sam sanjao il’ sam se prosto odselio iz te hale duhom, ne znam, ali svu noć mi je pred očima bila Drinjača, toplo mlijeko iz Pahljevića, maline iz Burnica, zalazak sunca u Konjević Polju... Sve mi se miješalo naizmjenice. Dok mi je logor burgijao svoj miris i mrak u sjećanje.",
          "Gungula i galama je počela vrlo rano ujutro. Nisu do nas nikakvi glasovi dolazili o čemu se radi, ali je narod šio po hali tamo-amo. Babo je skočio i otišao kroz ona vrata gdje smo ušli, “odoh da vidim šta se dešava.” Kasnije je babo došao sa nekim limenim tanjirom, u njemu je donio nešto malo neke supe i krišku providnog hljeba. Supa je izgledala kao prljava voda, a tanjir je bio plitak, escajg smo bacili, a nismo ju mogli ni posrkati. Htjeli smo pokupiti supu hljebom, ali se u njoj on istopio.",
          'Dok sam mozgao kako ću pojesti ono malo supe, babo je mami govorio kako je bio kod tetka Ibre i kako on još ne može da ustane sa onih nosila na kojima smo ga jučer vidjeli. Kazao je da je jako blijed i da se "osušio". "Nema niko ni infuziju da mu dâ". Tražio sam od mame i babe da me puste da odem prohodati. S desne strane u daljini sam vidio da nije bilo zida i dopirala je svjetlost. Kad sam to tog otvora došao, vidio sam poljanu punu naroda, istog onakvog kakav je bio unutra u hali, koji se prži na goleti i suncu.',
          'Kad sam se vratio, nisam uspio ispričati šta sam vidio. Majka mi je naredila da sjednem, i onda je ispred nas čučnuo neki čovjek. Pitao je za imena, babino i moje. "Rekli nam UNPROFOR-ci da popišemo samo muške", kazao je. Babo je rekao da sam ja Jasmin Jusufa Jusufović i on upisa. Kako sam samo bio ponosan kad babo onako izgovori moje ime. Mislio sam, jeste ja sam svoga babe sin, i ne može mi niko ništa. Kad je popisivač otišao, negdje s lijeve strane je neki čovjek doviknuo: "Kak’i UNPROFOR-ci... četnici!" ',
          "“A što babo onaj čiko upisa samo moje i tvoje ime?” ",
          "“Ne znam. Možda da zna koliko naroda ima ovdje…”",
          "“A, što onda nije upisao mamino? Hoće li upisati Nicu? Je li upiso Keku i nanu? Ko će upisati Kiku?”",
          "“Ne znam dijete drago, ne znam.”",
          "Majka i babo su pričali sa ljudima oko nas. Do nas je u naš ćošak došla i vijest da su pustili četnike među nas, i da oni izvode narod u neku bijelu kuću kod fabrike da siluju i strijeljaju. Zato mi majka više nije davala da idem sam tamo prema onoj livadi i na zrak. Hajde majko molim te da idemo tamo na zrak. Neću, sjedi. Molim te mama, samo da malo vidim sunce. Neću, sjedi! Ali, mama, hajd onda da idemo Nici tamo da vidimo Edu i Dadu, eto hajmo skupa. Kad smo došli... Haj mama da ja samo malo ondje na ćošak, eto vidjećeš me od nice. Svakih pola minute sam se okretao majci da joj mahnem i da potvrdim da se gledamo. Onda sam čuo s kraja ograde vrisku i vikanje i raspoznao sam šta jedan ženski glas viknu. Otrčao sam majci, brzinom kojom sam mislio da ću premotati vrijeme.",
          "“Šta je bilo, kakva je ono vriska?”",
          "“Ne znam viče jedna žena ‘Zgazi joj dijete’! “",
          "“Ko je kome zgazio dijete?” ",
          "“Ne znam mama, samo sam čuo ženu kako viče ‘Zgazi joj dijete’! “",
          "Odjednom je po cijelom tom narodu pala grobna tišina. Svi su se ušutili, ni šaputanja na trenutak nije bilo. Kasnije je od uha do uha došla vijest da je četnik ženi koja se pored ograde tek porodila uzeo bebu i zgazio. Nisam se više usuđivao da spomenem da nekuda hoću. Nisam više smio tražiti ni da idemo do dajnice... Samo sam tražio onu svoju rupu u zidu da sklonim glavu i dozovem Drinjaču. Kad se opet zanoćalo, izustio sam, više onako misleći da sam sam, i sebi pod nos, kad ćemo ići odavdje? Babo me je čuo, i misleći da sam njega upitao kaza: “Sutra ćemo ići. Haj' sad lezi!” Panika u hali 13. jula me je probudila. Pola hale je ustalo na noge, majka je oko mene ponovo skupljala ono malo naših stvari, i meni u krilo tutnula moj ruksačić, u kojem je ostala samo ona flaša što je smrdila na benzin “Je l' to idemo?” Pitah. Niko me nije čuo.",
          'Žamor koji sam prethodnih dana mogao ignorisati kao onaj stalni šum, to jutro je bio glasniji. Ljudi su donosili nove vijesti, neku su ženu dozivali svijesti malo podalje lijevo, jednog su djeda osovljavali na noge, jedna je majka nastojala da ušutka dijete koje vrišti. Ja sam sebi govorio, neću ja biti takav. Ja sam fin, ja neću plakati i vrištati da se mama i babo sikiraju. Svi su bili na gotovs da krenemo negdje, ali se nismo pomicali. Tako su se ljudi, oni koji su ustali, onako u mjestu klatili i okretali. Nedugo, u dubini hale, na istoj onoj strani sa koje smo ušli, otvorila su se velika vrata, i narod je kao rijeka krenuo sav na ta jedna vrata. Mi nismo krenuli pravo prema vratima, već nas je gužva pronosala po sredini hale, na mjesto gdje je sjedila dajnica, a njih nije bilo. Odjednom smo, kod nekog zelenog kamiona stali, ali se narod i dalje natiskivao prema vratima. Cimnuo sam oca za ruku i pokušao da mu viknem: "Babo, ne mogu da dišem!".  On me je uzeo i stavio na ramena. Bio sam u ravni sa kamionom. I na kamionu je bilo ljudi. Kad smo konačno krenuli, izašavši napolje, sunce i toplota su me oslijepili. Uhvatio sam se za oči. Otac je primijetio da se više ne držim za njega na ramenima, pa me je skinuo između sebe i majke i uzeli su me za ruke. Ja sam oborio glavu u noge da mi se oči priviknu na svjetlo.',
          "Hodali smo kroz dugačak kordon vojnika. Vidio sam samo prave čizme i noge u vojnoj odjeći i cijevi pušaka. Tjerali su nas, vikali, morali smo početi trčati. Nisam se na sunce navikao sve dok nismo izašli na cestu. Ispred nas se pružila kolona kamiona i autobusa i vojske s lijeva. Otac je majku i mene gurao ispred sebe, majka je mene držala čvrsto uza se, zamotavajući me u dimije. Bilo je teško tako trčati. Prolazili smo pored jednog, pa drugog autobusa i kamiona, sve dok nije neko viknuo “ULAZI”! i ja se nađoh ispred drvenih merdevina naslonjenih na kamion.",
          'Mislio sam, hajde da što prije ustrčim uz merdevine, prije ćemo se nas troje skloniti na kamion i ovo će sve završiti. Na pola merdevina sam iza leđa čuo naredbu: "Ti, silazi! Ovamo!" i čuo sam neko komešanje. Uspeh se onih nekoliko stepenika što mi je ostalo i okrenem se. Majka je stajala ispred merdevina, kršeći ruke, a cestom je vojnik puškom odgurivao moga babu. Nisam više vidio ništa, osim nas troje i tog vojnika. Ukočio sam se. Babu je onaj vojnik tjerao na jarak pored puta, a onda je drugi na majku viknuo da se penje. Majka me je okamenjenog zgrabila i prenijela dublje u kamion. Ja sam i dalje gledao u babu. Stao je na jarak. Okrenuo se prema nama da nas isprati. Kad je vidio da ga gledam, stavio je prst preko usta kao kad mi govorio "šššš", i odmahnuo rukom kao kad je govorio "idi".',
          'Nisam čuo, možda smo i vikali, ali ja nisam čuo. Sjećam se samo da ničim nisam mogao da se pomjerim. Oči sam ostavio na ocu. Znam da sam jako stisnuo zube, a iznutra sam htio da puknem. Od toga su mi na oči dolazile suze koje su mi maglile pogled na babu. Uspio sam samo malo da dignem jednu ruku, u glavi sam mislio da mu mašem. On je samo ponovio onu svoju kretnju "šššš”! Idi". Nije imao svoju uobičajenu beretku na glavi, mislio sam biće mu vruće na onom suncu.',
          'Nosio je na sebi majicu kratkog rukava, krem boje, sa azurnim širokim prugama. Iznošen kožni prsluk. Crne pantalone i iznošene cipele, jedine koje je imao u Srebrenici. Pod miškom je nosio moju crvenu jaknu sa kapuljačom. I tako je ostao, gledajući u nas, "šššš! idi". Mene onda kao da je neko uzeo i iznio iz kamiona i odnio u nebesa. Vidio sam da na kanalu oko mog oca stoji još mnogo ljudi. Iza njih sam vidio zrelu pšenicu kako igra na suncu i vjetru, iz hale je narod još uvijek izlazio, neki su se penjali u autobuse. Vidio sam i mamu na kamionu kako mene pritišće uza se, umotava me u dimije i plače.',
          'Odnekud je neki momak uskočio u dnu kamiona. Zavukao se iza nekih buradi koja su tu bila, a onda su žene po njemu pobacale naramke što su sa sobom nosile. Kad se kamion napunio narodom, zatvorili su nas ceradom, i nastao je potpuni mrak od kojeg sam ponovo oslijepio. Kad je kamion krenuo, šapnuo sam sebi "babo će u drugom kamionu..."',
          "Kamion nas je dugo i sporo vozio negdje. Ispod cerade nam je nestajalo zraka. Odjednom smo se zaustavili. Svi smo se ušutili. Čuo sam vrata kamiona, i onda sam samo vidio dugačku letvu kako nam otvara jedan dio cerade vičući: “Šta ste se zatvorili, pogušićete se, jebo vas bog!” Kroz onaj otvoreni dio smo vidjeli kako sa ceste gdje smo mi, odnekud ispred nas, granatiraju po šumi. Sjetio sam se svoj Mladog Dajdže koji je otišao preko šume. Ona letva je opet progovorila: “Eto malo gledajte da vam nije dosadno.” U to malo svjetla oko sebe sam vidio jednu nanu glave umotane u neki krvav zavoj, i vidio sam da su joj tim zavojem umotane i oči, i mnogo žena. Ne sjećam se da sam vidio ijedno dijete. Opet nas je ona letva zatvorila pod ceradu i nastavili su nas voziti. Dugo poslije kamion je opet stao, a mene je majka stavila na pod, prekrila me stvarima. Još neke žene oko nas su bacile po naramak na mene i onda sam na to sve osjetio nešto teško. Čuo sam poslije glas.",
          '“Ima li ovdje muških?” Žene su odgovarale da nema, a onda je nastala vriska. "Nema više niko, samo je on bio! Nema više niko!” Kamion je nedugo zatim opet krenuo. Kad smo se opet zaustavili, istovarili su nas iz kamiona na jednoj cesti, ispod koje je bila livada i rijeka, a iznad druga livada i kuća i vojska. Pokazivali su nam uz cestu i govorili, hajte sad gore, gore su vaši... majka i ja smo krenuli cestom... bili smo samo ona i ja. Iz naroda smo čuli da se mjesto gdje smo došli zove Tišča. Put kojim su nas usmjerili vodio je prema Kladnju. Hodali smo satima i satima, zastajkujući ponekad da dođemo, iznureni, do daha. Putem su bila poobarana drveća pa smo se preko nekih morali penjati, ispod nekih propuzati. Usput su nam s ivice ulice govorili, samo pravo, pravo do tunela, iza tunela su vaši...',
          'Tunela nije bilo nigdje ni na vidiku, iza svake okuke dolazila je samo druga okuka, ispod ceste rijeka, iznad ceste klisura, i u procjepu gore malo neba. Već je počeo da pada i prvi sumrak, i konačno smo ugledali tunel. Prošavši kroz tunel, malo dalje, naiđosmo na moju tetku Safetu, moju Nisku i Nihu. Kad je mene ugledala, Niska je vrisnula. Od njenog vriska se klisura prolomila, a ja se sledio. Ne sjećam se da me je iko tako čvrsto zagrlio i da se uz mene toliko tresao kao Niska. Samo je kroz plač ponavljala "nema mi babe, nema ni mog babe"... Pomislio sam, znači i tetak Muhamed je u drugom kamionu... I onda sam prokinuo plakati. Bez glasa. Samo su se suze prolijevale.',
          "Odatle smo dalje hodali samo što smo morali. Nisam bio prisutan u sebi. Tek kroz mahove se prisjećam okvira prozora na autobusu koji nas je vozio na Dubrave, i u ramu tog prozora crvenog neba. Na Dubravama smo došli već u mrak. Bauljali smo majka i ja u moru nepoznatih silueta. Sudarali se s ljudima. Neki su trčali prema cisternama, neki prema šatorima. Onda je majka ugledala neku banderu sa sijalicom, i tu smo sjeli, na humku od šljunka. Izvan kruga kojeg je obasjavala ta sijalica, odjednom su mi svi potonuli u tamu. I kao da više nikog na svijetu nije bilo. Samo majka i ja. Sjedimo na humci od šljunka i čekamo. Majka mi je govorila: “Ovdje će nas najprije vidjeti.” Nismo znali ni ko ni hoće li zaista. Ja sam se već počeo gubiti... Odjednom je odnekud neko viknuo naša imena: “Husnija! Jasko!”",
          'Nismo se obazirali, nismo vjerovali. A iz one tame prema nama je odjednom izronilo lice mog tetiča Emira. "Ja sam, Husnija! Emir! Zar me ne poznate!" to je govorio i plakao. Dao nam je vode da se napijemo. A mene, čas je bilo, čas me nije bilo. Sjećam se da sam se budio u njegovom autu, i da sam pored njega, ispred nas, vidio još jednu ženu, koju nisam mogao prepoznati. Kroz ta moja lutanja iz budnosti, primijetio sam i da su nas doveli do kuće. Žena s prednjeg sjedišta me je povela uz neke stepenice. Kad me je prenula svjetlost iz prostorija na vrhu stepenica, vidio sam unutra dva dječaka, i prepoznao sam da me za ruku vodi moja tetična Kada.',
          "Ne znam kako, ni kada, odjednom se nađoh presvučen u drugu odjeću, i posađen ispred sofre ne kojoj je bilo pečeno pile. Zinuo sam, gubio se, nisam znao šta da radim. Gledam u Kadu, pa u ono pile... A Kada uze, s pileta otkinu komad, uze mi ruke, stavi, i kaza – “de, pile moje, jedi.” To pečeno pile, i onaj komad mesa u mojim rukama s kojim nisam znao šta da radim, posljednje je što se sjećam toga dana. Znam da jesam spavao, kako gdje i kada sam zaspao toga se ne sjećam. Ja više nisam mogao biti prisutan u ovom svijetu taj dan.",
          "Prvi sam put se vratio u Srebrenicu '99. godine. Dječija Ambasada je organizovala tu posjetu i ja sam odjednom se obreo ispred vijećnika u opštini, za mikrofonom kao trebao bih nešto reći nakon uvredljivog podbacivanja jedne vijećnice.",
          "Rekao sam: “Gledaj mene! Niste nas sve pobili!” Sjećam se da se Duško Tomić, sa kojim sam došao uštrocao, a novinari i kamere su se odjednom sve na mene okomile. Dodao sam: “Ja sam poslije svoga oca ostao živ, božjom voljom, da svjedočim o svom ocu i šta je nama učinjeno, sviđalo se to vama ili ne.” Ne znam odakle mi hrabrost.",
          "Nakon ovoga su me spodbili i otuda smo otišli brže nego što smo došli. Nisam imao vremena da ih zamolim da malo stanemo, da vidim ima li gdje koja moja knjiga, ima li gdje šta babino, ima li ikoga u onoj našoj kući, jesu li mi zapalili sanke.",
          "Jasmin Jusuf Jusufović"
        ]
      },
      Macedonian: {
        title: "Јасмин Јусуф Јусуфовиќ",
        subtitle: "Мали принц",
        language: "Macedonian",
        paragraphs: [
          "Во мугрите на тој 8-ми јули, грмеше негде во ридовите, но доволно гласно да нè разбуди. Тогаш се преместив во моето засолниште кај замрзнувачот, каде што беше најдебелиот ѕид. Тие неколку дена неброенo пати трчавме до најдебелиот ѕид, а кој е најдебел оценувавме во трката. За да го скратам времето во своето легло покрај замрзнувачот, од тој 9-ти јули ги броев гранатите кои се слушаа преку целиот ден. Понекогаш беа по триесет одеднаш. Луѓето излегуваа по улиците, но само по правилото “Првата убива”. Низ улицата громогласно поминуваше Рамо лебарот со ранитете во “тамчето”. ",
          "",
          "Потоа околу пладне настапи тишина. Јас отидов да видам што прават Едо и Дадо. А знаев дека и тато е негде во комшилакот. Кога се спуштив низ Милошева помислив дека тато можеби отишол кај ковачите преку реката. На сред чистинката кај Насеровата брана ме снајде експлозија од граната, но најголемата грижа ми беше да истрчам назад до кај мајка ми барем да види дека сум ОК. Влетав кај вујна ми дома и викнав “Одам кај мама да не се грижи”, а вујна едвај ме задржа во засолништето кај Едо и Дадо под масата, велејќи ми дека мама сигурно знае дека сум со нив.",
          "",
          "И покрај сè, инсистиравме дека животот се одвива нормално. Утрото на 10-ти јули се качивме на спрaт со баба за да поработиме на нејзината соба. Зедов некоја черга и излегов на терасата да ја истресам. Гранатата падна над куќата. Земјата паѓаше по кровот како дожд, а јас десетина секунди висев на ѕидот. Во целиот тој хаос и фрка под гранатите, на пладне кога малку се смири, тато и мама одлучија да одиме кај стрина ми да би бае, бидејќи од гранатите веќе не прозборев. Мислеа дека ми е нешто. Таму, зјапав во чинијата со вода и олово и ѝ велев на стрина ми “Каде ба, гледаш лав?”",
          "",
          "Стрина живееше во Казани, а ние во чаршијата, што беше доволно далеку за одење поради опасноста од гранати. До ден денес нема да ми биде јасно како успеаа да искомуницираат да се најдеме во куќата на тетка ми на автобуската станица, на пола пат, без телефони и без никакви врски. Кога го завршивме делот со баење, јас воопшто не се чувствував помалку исплашен. Потоа, повторно почна гранатирањето. Кај нас беа Едо и Дадо, Едо со мене кај замрзнувачот, а Дадо кај другиот дебел ѕид. Некој викна дека паѓаат гранати по Стариот Град. Почнав да се грижам за другар ми Кемо.",
          "",
          "Утрото на 11-ти јули, уште од рано почна да се пука, поблиску од кога и да е во тие денови. Беше жешко. Мама и тато пакуваа некои ствари во торби, најверојатно ако веќе заминуваме да не заминеме како оној пат од Дрињача со празни раце. Јас скокнав и во моето ранче ги пикнав Малиот принц и 1000 зошто 1000 затоа. Гранатирањето не запре ни во зората ни цело утро. По должината на улицата, сè повеќе и повеќе глави почнаа да се движат од чаршијата, трчајќи. Ние нешто чекавме, и тогаш некој од толпата народ викна: “Четниците се спуштија на пазарот!” Ги зграбивме ранците, татко ми ме грабна и мене како ранец, па прелетавме од ридот кај што ни беше куќата, во толпата народ кој бегаше. Позади нас слушавме рафали и пукотници.",
          "Трчајќи низ улицата на оградата каде што одамна претходно падна граната и уби неколку деца кои играа фудбал пред нашето школо, го искинав десниот ракав. Можеби ја изгребав и раката, но тогаш тоа не го почувствував. Само видов како мајка ми ме тргна од оградата и како втрчавме во куќата кај тетка Муневра на автобуската станица. Поради нешто застанавме. Тато отрча назад кон чаршијата, а мама ми го зеде ранецот и ги истресе книгите, ме погледна, а јас ги спуштив рамената. Така остана мојот Мал принц. Кога ме виде заледен над книгите, мама ме тешеше дека книгите се претешки а ние треба да бегаме и да трчаме...јас молчев, мислев, можеби и не, но видов дека нема повеќе ни смисла ни фајде.",
          "",
          "Подоцна во куќата дојде и тато, со торба со бело брашно на грбот. Ни кажа дека луѓето го обиле магацинот на Насер. Од брашното тетка Муневра ни направи уштипци на сите. Јас имав заборавено дека до тие уштипци немав ништо јадено со денови и имав заборавено колку сум гладен. Изедов едвај еден. Набрзо се разделивме. Тато ме измами дека оди нешто да донесе, но всушност отиде во шумата. Ние се најдовме со вујна Сена и продолживме понатаму. Околу болницата се беше створила толпа. Видов луѓе како скокаат преку балконите во околните згради. Ние скокнавме во една од нив.",
          "Не останавме долго во темнината на таа зграда, туку тргнавме понатаму, но подалеку од болницата не можевме да отидеме. Луѓето се обидуваа да влезат во логорот на ОН во фабриката Везионица, а некој рече дека Зулфо Турсун ги враќа момчињата од болницата назад. Така и се изгубивме со вујна. Мама, баба и јас застанавме под една од зградите на болницата, над Везионица. Некаде во близина падна граната. Прво врз мене се фрли баба, па после и мама, а врз нас се скрши некое претходно преживеано стакло од зградата. Се сеќавам дека едвај дишев под баба и мама.",
          "",
          "По гранатите, жените уште повеќе навалија на капијата и оградата на Везионицата. И ние тројцата се спуштивме со останатите на улицата. Низ галамата препознав глас: “Хуснија!!!!” Тато се врати од шумата. “Јас ќе сум со вас па што сака нека биде”. Негде во еден дел беше провалена оградата на Везионица. Тато некако ме внесе во кругот на кампот и отидовме во гаражата на OН возилата и транспортерите. Тогаш падна уште една граната. Шрапнелите по лимот од гаражата пробиваа дупки низ кои се провлече светлината во сноп траки. Ја изгубивме мама. Ја изгубивме баба. Ја немаше ни вујна, ни Едо, ни Дадо, ни Кека, ни вујко Кико...бевме само ние двајца. Татко виде како ОН војниците се извлекуваат под Везионицата во камионите, ме зграби и дотрчувајќи до еден камион, ме фрли на каросеријата. ",
          "",
          "Им помогна уште на многумина да се качат во камионот, и целиот тој чопор ме притисна во страниците на камионот. Успеав само да ја свртам главата кон надворешната страна за да можам да дишам. Низ таа толпа од луѓе забележав дека тато и јас, поради гужвата и стисканиците  во камионот, ги испуштивме рацете. Луѓето по патот исто така почнаа да се качуваат на камионот и полека ми исчезнуваше и оној малку воздух што го имав. Камионот бавно дојде до друга база, во фабриката Акумулаторка. Низ кордонот од луѓе и војници од ОН јас и тато завршивме во темнината на халата. Патем го видовме тетин Ибро, на носилки.",
          "Во фабриката имаше сè повеќе и повеќе луѓе. Тато се избори со некого да ни најде една носилка за да можам да седнам. Седнав до еден ѕид. Покрај искршениот влез. Носилките беа мокри. Од крв. Седевме на носилките, а јас ги вперив очите кон вратата низ која дојдовме за случајно од таму да не се појави мама. Времето изгуби секаков контекст. Одеднаш низ вратата дојде мама, сета засрамена, уплакана, како штотуку со некој да се борела. Веднаш нè здогледа. Ни кажа дека УНПРОФОРЦИТЕ не ѝ дозволиле да влезе во фабриката па со еден војник се скарала и борела, па некако успеала да се протне. Подоцна тато отиде да ни најде вода. Донесе некое шише кое смрдеше на бензин и во кое имаше само малку вода. Рече дека ги нашол вујна, Едо и Дадо и вујко Кико, но не ја видел баба. Мама кажа дека баба останала во другата фабрика со нашата, бремена, Кека. ",
          "",
          "Не можев веќе да ја слушам галамата, врисоците, да ги гледам раскрвавените лица и сивилото на фабричката хала. Најдов некоја дупка во ѕидот зад мене, таму ја ставив главата и некако, во сето тоа, како да заспав. Се сепнав некоја минута подоцна, мама ме прашува зошто не спијам, јас сакам да одиме кај вујна, подоцна ќе одиме. Сакам да одам во тоалет. Ме однесе покрај влезот до нас, позади фабриката, тука повторно видов раскрвавени лица и изгребани луѓе, многу измет и крвави крпи и завои. Реков не можам да одам овде, и повеќе не ни излегов.",
          "",
          "Во некое доба мама панично почна да претура по нашите торби. Го најде приборот за јадење и отрча да го фрли. Во фабриката луѓето зборуваа како четниците патролираат низ фабриката облечени во ОН униформи и само бараат причина некого да однесат надвор и да го стрелаат. “Што ќе ти е тоа жено?” Ја праша тато.",
          "",
          "“Не знам болан кај ќе сме после ова и како ќе завршиме. Мислев дека ќе ни затреба.”",
          "",
          "Накит одамна немаше, а тогаш зема и фрли сè што имавме од метал.",
          "",
          "Виде дека повеќе не може да ме одбие, па кога престана да влегуваат луѓе, отидовме да ги најдеме вујна и вујко Кико. Вујко Кико беше страшно блед. Тој беше лице со посебни потреби, но никогаш како до тогаш го немав видено толку уплашен. Мама ја праша вујна кај ѝ e Сенад, мојот Млад Вујко, таа рече дека отишол низ шумата. Јас им пријдов на Едо и Дадо, седнав кај нив на некое кебенце пространо по бетонот. Беше тврдо и ладно.",
          "",
          "Толку многу лица кои никогаш порано ги немав видено, сега беа околу нас во халата. Никогаш не сме ги виделе, но како отсекогаш да сме ги познавале. Низ халата само поминуваа бранови на паника. До ушите стигнуваа вести дека вадат од халата и колат, и силуваат и стрелаат. Мама и тато шепотеа со луѓето околу нас дека сè брзо ќе се заврши, дека би тебало да бидеме ОК тука се УНПРОФОРците,  но нам, додека се замрачуваше, ни стана сосема јасно дека ние во тие фабрики, сме всушност во логори.",
          "",
          "Некако ја истуркавме ноќта...јас повторно ја завлеков главата во својата дупка на ѕидот, мама со телото се потпре кон мене да ме стисне, да се чувствувам посигурно, сè што чув таа ноќ беа воздишки, детски плачења, и негде во далечината зад фабриката низ оној влез, врисоци. Дали сонував или просто се преселив со духот од таа хала, не знам, но цела ноќ пред очите ми беше Дрињача, топло млеко од Пахљевича, малините од Бурница, залезот на сонцето во Коњевиќ Поље...сè ми се мешаше наизменично. Додека логорот како со бургија го всадуваше својот мирис и мрак во мојот мозок.",
          "",
          "Гунгулата и галамата почна уште од рано наутро. До нас не дојдоа никакви гласови за тоа што се случува, но народот одеше низ халата таму-ваму. Тато рипна и отиде низ онаа врата од каде што влеговме, јас отидов да видам што се случува. Подоцна тато дојде со некоја лимена чинија, во која донесе нешто малку супа и кришка проѕирен леб. Супата изгледаше како валкана вода, а чинијата беше плитка, приборот за јадење го фрливме, а не можевме ниту да ја исркаме. Сакавме да пробаме да мацнеме со лебот, но и тој во супата се истопи.",
          "",
          "Додека мислев како да го изедам и тоа малку што остана од супата, тато и кажуваше на мама како бил кај тетин Ибро кој сè уште не може да стане од носилките на кои го видовме вчера. Рече дека е многу блед и дека се “исушил”. “Нема никој ниту инфузија да му даде.” Барав од мама и тато да ме пуштат малку да проодам. Од десната страна во далечината видов дека немаше ѕид и доаѓаше малку светлина. Кога дојдов до тој отвор, видов ливада преполна со народ, исто како што беше и внатре во халата, но овој народ се пржи на сонцето.",
          "",
          "Кога се вратив, не успеав да раскажам што видов. Мама ми нареди да седнам, и тогаш пред нас клекна еден човек. Нè праша за имињата, на тато и моето. “Ни рекоа УНПРОФОРците да направие попис само на машките” ни рече. Тато кажа дека јас сум Јасмин Јусуф Јусуфовиќ и тој запиша. Колку само бев горд кога тато онака го изговори моето име. Мислев, точно е дека јас сум на мојот татко син, и мене никој ништо не ми може. Кога попишувачот замина, некој од левата страна довикна: “Ма какви унпрофорци....четници!”",
          "",
          "“А зошто тато оној чичко ги запиша само нашите имиња” “Не знам. Можеби за да знaе колку народ има овде...”",
          "",
          "“Па зошто тогаш не ја запиша и мама? Ќе ја запише ли и Ница? Ги запиша ли Кека и баба? Кој ќе го запише Кико?”",
          "",
          "“Не знам, дете драго, не знам.”",
          "",
          "Мама и тато разговараа со луѓето околу нас. До нашиот ќош стигна и веста дека меѓу нас пуштиле и четници, и дека тие вадат луѓе и ги носат во некоја бела куќа кај фабриката каде силуваат и стрелаат. Затоа мама повеќе не ми даваше да одам сам кон таа ливада и на воздух. “Ајде мамо те молам да идеме таму малку на воздух.",
          "",
          "Не сакам, седи. Те молам мамо, само малку да го видам сонцето. Не сакам, седи!”",
          "",
          "“Но, мамо, ајде тогаш да одиме кај Ницa да ги видиме Едо и Дадо, ете ајде да одиме заедно.”",
          "",
          "Кога дојдовме...",
          "“Ајде мамо јас само малку онде кај ќошот, еве ќе ме видиш од кај Ницa.”",
          "",
          "Секоја пола минута се вртев назад да ѝ мафнам на мама и да потврдам дека се гледаме. Тогаш од кај крајот на оградта слупнав врисок и викање и распознав што кажа еден женски глас. Открчав кај мајка ми, со брзина која мислев дека ќе го премота времето.",
          "",
          "“Што се случи, каков беше тој врисок?”",
          "",
          "Не знам, вика една жена “ѝ го згазија детето” Кој кому му го згази детето? Не знам мамо само чув жена како вика “ѝ го згазија детето”",
          "",
          "Одеднаш падна гробна тишина. Сите се стишија, на момент немаше ни шепотења.",
          "",
          "Подоцна од уво до уво дојде вест дека четник го зел бебето од жена која штотуку се породила и го згазил.",
          "",
          "Повеќе не се осудув да споменам дека сакам некаде да одам. Не смеев повеќе да барам ни да одиме до кај вујна...само ја барав онаа моја дупка во ѕидот да ја наслонам главата и да ја повикам Дрињача.",
          "",
          "Кога повторно се стемни, изустив, повеќе мислејќи дека сум сам и сам на себе под носот, кога ќе си одиме од овде?",
          "",
          "Тато ме слушна, и мислејќи дека него го прашав, рече – “Утре ќе одиме. Ајде сега лежи!”",
          "",
          "Паника во халата ме разбуди на 13-ти јули. Пола хала беше станата на нозе, мама околу мене ги собра оние малку ствари кои ни останаа и во скут ми го тутна моето ранче, во кое остана само шишето кое смрдеше на бензин – “Дали одиме?” Прашав. Никој не ме слушна.",
          "",
          "Џагорот кој претходните денови можев да го игнорирам како постојан шум, тоа утро стана погласен. Луѓето носеа нови вести, некоја жена ја довикуваа при свест малку подалеку в лево, некој дедо го стануваа на нозе, една мајка настојуваше да го замолчи детето кое врскаше.",
          "",
          "Јас сам на себе си велев, нема јас да бидам таков. Јас сум фин, јас нема да плачам и да врискам за мама и тато да не се грижат. Сите беа на готовс да тргнат некаде, но никој не се поместуваше. Луѓето кои беа станати, само се клатеа и вртеа во место. За некое време, во длабочината на халата, од истата страна каде што влеговме, се отвори голема врата, и народот како река тргна кон таа една врата. Ние не тргнавме право кон вратата, туку гужвата нè пренесе кон средината на халата, кон местото каде што седеше вујна ми, но нив ги немаше.",
          "",
          "Одеднаш застанaвме кај некој зелен камион, но народот и понатаму се тискаше кон вратата. Го допрев татко ми за рака и пробав да му викнам: “Тато, не можам да дишам!” Тој ме зеде и ме стави на рамена. Бев на висина на камионот. И на каминот имаше луѓе. Кога конечно тргнавме, излегувајќи надвор, сонцето и топлината ме заслепија. Се фатив за очите. Тато забележа дека повеќе не се држам за него на рамената, па ме симна помеѓу него и мама и ме фатија за раце. Јас ја наведнав главата кон нозете за очите да ми се привикнат на светлото.",
          "",
          "Одевме низ долг кордон од војници. Видов само прави чизми и нозе во војничка облека и цевки од пушки. Нè бркаа, викаа, моравме да почнеме да трчаме. Не се навикнав на сонцето сè додека не излеговме на улица. Пред нас имаше колона од камиони и автобуси и војска од лева страна. Тато нè туркаше мене и мама пред него, мама ме држеше црвсто покрај себе, замотувајќи ме во димиите. Беше тешко така да се трча. Поминувавме покрај еден, па друг автобус и камиони, сè додека некој не кажа ВЛЕГУВАЈ! И јас се најдов пред дрвени скали потпрени на камион.",
          "",
          "Мислев, ајде што побрзо ги истрчам скалите, толку побргу сите тројца ќе се качиме во камионот и сето ова ќе заврши. На пола скали чув наредба од зад грб “Ти, симнувај се!Ваму!” и слушнав уште нешто неодредено не непријатно. Се качив на уште неколкуте скалила што ми преостанаа и се свртев.",
          "",
          "Мама стоеше пред скалите, кршејќи си ги рацете, а по улицата еден војник со пушката го туркаше татко ми. Повеќе не видов ништо, освен нас тројцата и тој војник. Се вкочанив. Оној војник го тераше татко ми кон еден дол покрај патот, а друг војник ѝ викна на мајка ми да се качи во камионот.  Мене таков вкочанет мама ме зграби и ме пренесе подлабоко во каминот. Јас и понатаму гледав во тато. Застана над долот. Се сврте кон нас да нè испрати. Кога виде дека го гледам, го стави прстот преку уста како кога ми велеше “шшшшшш”, и мафна со раката како кога велеше “оди”. ",
          "",
          "Не слушнав, можеби и викавме, но јас не слушнав. Се сеќавам дека не можев со ништо да мрднам. Очите си ги оставив кај тато. Знам дека силно ги стиснав забите, а одвнатре сакав да пукнам. Од тогаш почнаа да ми доаѓаат солзи на очите кои ми го помаглуваа погледот кон тато. Успеав само малку да ја подигнам едната рака, во главата мислев дека му мафтам. Тој само го повтори она свое движење “шшшшшш!Оди”. Ја немаше својата вообичаена беретка на главата, мислев дека ќе му биде жешко на сонцето.",
          "",
          "Носеше на себе маица по кратки ракави, крем боја, со азурни широки пруги. Износен кожен елел. Црни панталони и износени чевли, единствените кои ги имаше во Сребреница. Под мишка ја носеше мојата црвена јакна со капче. И така остана, гледајќи во нас, “шшшш!Оди”.",
          "",
          "Тогаш мене како некој да ме зеде и да ме изнесе од камиониот и ме однесе во небеса. Видов дека на каналот околу мојот татко стојат уште многу луѓе. Зад нив видов зрела пченица како игра на сонцето и ветрот, од халите сè уште излегуваше народ, некои се качуваа во автобусите. Ја видов и мама во камионот како се притиска кон мене, како ме мота во димиите и плаче. ",
          "",
          "Однекаде едно момче скокна во дното на камионот. Се вовлече позади некои буриња кои беа таму, а потоа жените врз него нафрлаа марами и сè што носеа со себе. Кога камионот се наполни со народ, нè затворија со церада и настана целосен мрак од кој повторно ослепев. Кога камионот тргна, си шепнав сам на себе “Тато ќе е во другиот камион”.",
          "",
          "Камионот нè возеше некаде, долго и бавно. Под церадата ни исчезуваше воздух. Одеднаш застанавме. Сите се затишавме. Слушнав вратата на каминот како се отвора, и тогаш видов еден висок и испијанет човек како летва што ни отвори еден дел од церадата, викајќи: “Што сте се затвориле, ќе се угушите, јебо вас бог.” Од отворениот дел видовме како од улицата кај што сме ние, од некаде пред нас, гранатираат кон шумата. Се сетив на мојот Млад Вујко кој отиде преку шумата. Летвата повторно проговори: “Еве гледајте малку да не ви е досадно.”",
          "",
          "Во тоа малку светло околу себе видов една баба со замотана глава во некој крвав завој, и видов дека со истиот завој и се замотани и очите, и уште многу жени. Не се сеќавам дека видов макар едно дете. Повторно онаа летва нè затвори под церадата и продолжија да нè возат. Долго потоа камионот повторно застана, а мене мајка ми ме стави на под и ме покри со ствари. Уште некои жени околу нас фрлија уште по нешто врз мене и потоа почувствував нешто тешко. Потоа слушнав глас.",
          "",
          "“Има ли машки овде?” Жените одговараа дека нема, а потоа се слушна врисок. “Нема веќе никој, само тој беше! Нема веќе никој!” Недолго потоа камионот повторно тргна.",
          "",
          "Кога повторно застанвме, нè истоварија од камионот на една улица, под која имаше ливада и река, а над нас друга ливада  и куќа и војска. Ни покажуваа кон улицата и ни говореа, ајде сега горе, горе се вашите...мама и јас тргнавме по улицата...бевме само таа и јас.",
          "",
          "Од народот чувме дека местото до кое дојдовме се вика Тишча. Патот по кој нè умсерија водеше кон Кладањ. Одевме со часови, така истоштени застанувавме понекогаш да земеме воздух. По патот имаше испопаѓани дрвја па преку некои моравме да се качуваме а под некои да ползиме.",
          "",
          "Низ патот од ивиците на улиците ни велеа, само право до тунелот, позади тунелот се вашите...",
          "",
          "А тунел немаше никаде на видик, позади секоја кривина имаше само друга кривина, под улицата река, над улицата клисура, и во процепот горе малку небо.",
          "",
          "Веќе почна да паѓа и првиот самрак, и конечно го здогледавме тунелот. Поминувајќи низ тунелот, малу подалеку, ја најдовме тетка ми Сафета, моите Ниска и Ниха. Кога ме здогледа Ниска вресна. Нејзиниот врисок ја исполни клисурата, а јас се заледив. Не се сеќавам дека некој така цврсто ме прегрнал и толку да се има тресено покрај мене као Ниска. Низ плач ми повторуваше “Го нема тато, ми го нема тато”… Помислив, значи и тетин Мухамед е во другиот камион....и тогаш почнав да плачам. Без глас. Само се слеваа солзите. Од таму одевме понатаму само затоа што моравме. Не бев присутен сам на себеси. Како низ магла се сеќавам на прозорот на автобусот кој нè возеше на Дубрава, и во рамката на тој прозор на црвеното небо.",
          "",
          "На Дубрава дојдовме веќе во мрак. И јас и мама зјапавме во морето од непознати силуети. Се судривме со неколку луѓе. Некои трчаа кон цистерните, некои кон шаторите. Тогаш мама здогледа некоја бандера со сијалица, и тука седнавме, на една могила од чакал. Надвор од кругот кој го осветлуваше сијалицата, одеднаш сите ми потонаа во тамнина. И како повеќе да немаше никој на овој свет. Само мама и јас. Седиме на могилата од чакал и чекаме. Мама ми велеше: “Овде напред ќе нè видат.” Не знаевме ниту кој ниту дали воопшто.",
          "",
          "Јас веќе почнав да се губам....Одеднаш некој ги извика нашите имиња: “Хуснија! Јаско!”",
          "",
          "Не се свртевме, не верувавме.",
          "",
          "А од таа темнина кон нас одеднаш се појави лицето на тетин ми Емир. “Јас сум Хуснија! Емир! Зарем не ме познавате!” Тоа велеше и плачеше.",
          "",
          "Ни даде вода да се напиеме. А мене, час ме имаше, час ме немаше. Се сеќавам дека се будев во неговиот автомобил, и дека покрај него, пред нас, видов уште една жена, која не можев да ја препознаам.",
          "",
          "Низ тие мои лутања низ јаве и сон, забележав дека нè донесоа до една куќа. Жената од предното седиште ме повде низ едни скали. Кога допре до мене светлото од просторијата, видов внатре две момчиња, и препознав дека за рака ме води тетка ми Када.",
          "",
          "Не знам ни како ни кога, одеднаш се најдов пресоблечен во друга облека, и посаден пред софра на која имаше печено пиле. Зинав, се губев, не знаев што да правам. Гледам во Када па во она пиле.... а Када зема од пилето откина парче, ми ги зеде рацете и ми го стави парчето и кажа – “Де, пиле, јади.”",
          "",
          "Тоа печено пиле, и она парче месо во моите раце со кои не знаев што да правам, е последното на кое се сеќавам од тој ден. Знам дека спиев, како каде и кога сум заспал, на тоа не се сеќавам. Тој ден јас повеќе не можев да бидам присутен на овoј свет.",
          "",
          "Прв пар се вратив во Сребреница 1999 година. Детската Амбасада ја организираше таа посета и јас одеднаш се најдов пред советникот во општина, пред микрофон, за да кажам нешто после навредливото подвивање од страна на една советничка.",
          "Реков: “Гледај ме мене! Не нè убивте сите!”",
          "",
          "Се сеќавам дека Душко Томиќ, со кој што дојдов, се штрекна, а новинарите и камерите одеднаш се навртеа на мене. Додадов",
          "",
          "“Јас после мојот татко, останав жив, со божја волја, да сведочам за мојот татко и што ни е сторено, ви се допаѓало тоа или не.”",
          "",
          "Не знам од каде ми дојде таа храброст.",
          "",
          "После ова со неодобрување набрзо заминавме, побргу отколку што дојдовме. Немав време да ги замолам малку да застанеме, да видам дали има некоја моја книга останато, дали има нешто од татко ми, има ли некој во нашата куќа, дали ми ги запалиле санките.",
          "Превод: Александра Богдановска"
        ]
      },
      English: {
        title: "Jasmin Jusuf Jusufović",
        subtitle: "Little Prince",
        language: "English",
        paragraphs: [
          "At dawn, that July 8th, it was thundering somewhere in the woods loud enough to wake us up. That is when I moved to my shelter, next to the freezer, where the thickest wall was. During those few days, we ran so many times towards that thickest wall in the house, and while running we used to gauge the grenades. From that July 9th, in order to shorten the time in my nest, next to the freezer, I spent time counting the shells which were detonating during the whole day. At times there were thirty at once. The people went out in the streets, but only with the rule “The first one kills”. Ramo hljebara ( Ramo the bread salesman) thundered down the streets with the wounded in his TAM truck.",
          "",
          "The shells stopped and it all became silent at noon. I went out to see what Edo and Dado were doing. I knew my babo (babo = dad) was somewhere in the neighborhood. When I came down the Miloševa street, I thought babo had maybe gone to the blacksmith’s across the river. A shell detonation went off while I was at the center of a clearance, next to Naser’s dam. The greatest of my concerns at that moment was to run back to my mother so she could at least see that I was OK. While running back home I ran into my aunt’s house and yelled: “I’m going to my mother so that she doesn’t worry!” My aunt barely succeeded in keeping me in the shelter under the table, with Edin and Dadin, assuring me my mother surely knew I was at their place.",
          "",
          "Despite everything we were stubborn enough to keep living our lives normally. In the morning of July the 10th we went upstairs to help nana clean her room. I took a stretch and went outside onto the terrace to dust it off. A shell detonated behind the house. The earth was falling on the roof like rain, and I spent about ten seconds hanging from the wall. When all the confusion amidst the shelling calmed down at noon, my parents decided to go to my aunt’s house to perform a ritual of “lead pouring”, which is an old Balkan tradition believed to help with fear and emotional trauma, due to the fact that I hadn’t spoken a word since that shell detonated. They thought something had happened to me. During the ritual I looked inside the bowl with lead and water and kept asking my aunt: “Where in the world do you see a lion?”",
          "",
          "My aunt lived in Kazani, while we lived in town, which was pretty far off to be walking under the danger of shelling. To this day, it is not clear to me how they managed to communicate without a phone or any other link that we were supposed to meet at my other aunt’s house near the bus station, halfway to their house. When the lead pouring ritual was finished, I didn’t feel any less scared. Then the shelling started again. Edo and Dado were at our house. Edo and I were at my shelter by the freezer and Dado at the other thick wall. Somebody said the shells were detonating all over the Old town, and I started to worry about my friend Kemo.",
          "",
          "On the morning of that Jully 11th, the shooting started early, closer than ever before. It was a hot day outside. Mom and babo were packing up our stuff in cloth shopping bags, so as, if we were to leave, we do not go empty handed as when we left Drinjača. I jumped and packed my schoolbag with the Little Prince and the encyclopedia “1000 zašto – 1000 zato” (1000 why's - 1000 because). The shelling continued all morning. More and more people started moving out of the town, running. We were foolishly waiting for something. Then someone from the crowd yelled: “The Chetniks have come down to the marketplace!” We grabbed our bags, my father grabbed me as he would a bag as well, and we fled from the hill, on which our house was located, to join the crowd of people fleeing. Behind us we could hear burst shooting.",
          "",
          "While running down the street, I tore my right sleeve on a fence. The same fence which was blown apart by a grenade that also killed children playing a football tournament in front of our school. I may have cut my arm as well, but I didn’t feel it at the time. The only thing I saw is how my mother tore me from that fence and we ran into my aunt Munevera’s house at the bus station. We stopped for some reason. Babo ran back towards the town and mother took the backpack of my back, shook out my books and looked at me. I just shrugged. That’s how my Little Prince got left behind. When she saw me frozen above my books, she consoled me by saying they would have been too heavy on my back while we were fleeing. I was thinking to myself, maybe they wouldn’t have been too heavy, but I kept my silence, nothing seemed to make sense anymore.",
          "",
          "A little bit later, my father came to the house carrying a sack of white flour on his back. He told us that people have broken into Naser’s warehouse. Aunt Munevera made fritters for us. I completely forgot that I hadn’t eaten for days and that I was hungry. I barely ate one. Soon, we separated. Father misled me by saying he was leaving to get some stuff and then he'd meet up with us, but instead he went through the forest towards Tuzla. We met up with aunt Sena and moved on. There was a crowd around the hospital. I saw people jumping from balconies in nearby buildings. We went into one of them.",
          "",
          "We didn’t stay in the darkness of that building for too long. We moved forward, but we couldn’t go further than the hospital. People were trying to get into the UN camp in Vezionica and someone said Zulfo Tursun is turning boys back from the hospital. That’s how we lost my aunt. Mother, grandmother and I stopped below one of the buildings near the hospital, above Vezionica. A grenade exploded nearby. First my grandmother and then my mother covered me, trying to protect me from pieces of glass falling from the building. I remember being barely able to breathe underneath them.",
          "",
          "After the shell exploded, women tried to force their way through the gates and fences of Vezionica. The three of us merged with the crowd in the street. Suddenly, through the noise I heard a familiar voice: Husnija!!! Father came back from the forest. “I’ll go with you, whatever happens, happens.” One part of Vezionica fence was finally broken into. Father somehow carried me into the camp and we went into a UN vehicles and transporters garage. Another shell exploded. Shrapnel tore little holes through the tin walls of the garage. Sunlight seeped in. We lost mother. We lost aunt. There was no aunt, nor Edo, nor Dado, nor Keka, nor uncle Kiko. Just the two of us. Father saw the UN soldiers getting out of Vezionica in their trucks. He grabbed me and threw me into the trailer of the truck.",
          "",
          "He helped many others climb onto the truck. The mob pushed me against the sides of it. I barely managed to turn my head towards the outside so I could breathe. I realized that babo and I were not holding hands anymore. The mob had separated us. People were constantly climbing onto the truck and I had less and less air. The truck slowly arrived to another base, “Akumulatorka” factory. Babo and I ended up in the darkness of the warehouse. Along the way, we saw uncle Ibro. He was on stretchers.",
          "",
          "People started pouring into the factory. Babo somehow managed to find a stretcher so I could sit for a while. I sat up against the wall, next to the smashed entrance. The stretcher was wet. Wet with blood. As I sat there, my eyes were fixated on the door, hoping to see my mum walk through them. I lost all sense of time. Suddenly, mother appeared. She was flushed, as if she had just wrestled someone. She spotted us right away. She told us that UNPROFOR didn’t want to let her in, so she had to argue and fight with some soldier. Somehow, she managed to get in. Babo went to find us some water. He brought a bottle that reeked of petrol, which had a little bit of water in it. He told us that he had found aunt, Edo and Dado, but Nana was nowhere to be found. Mother said that Nana stayed in another factory with our pregnant Keka.",
          "",
          "I couldn’t bare to listen to the shouting and screaming, or to look at the bloodied faces and the grayness of the factory hall anymore. I leaned my head back into a hole in the wall and I think I kind of fell asleep. I woke up a few minutes later. Mother asked why I was not sleeping. I want to see my aunt. We’ll go later. I want to go to the toilet then. She took me behind the factory. There were bloodied and lacerated people, a lot of feces and bandages soaked with blood everywhere. I said I can’t go to the toilet, and I didn’t go anymore.",
          "",
          "At some point, mother started going through our bags in panic. She took a bundle of silverware and ran off to throw it away. Inside the factory, the word spread that chetniks were patrolling the factory dressed as UN soldiers and were just looking for excuses to take people out and slaughter them.",
          "",
          "Babo asked her why in the God’s name she had that silverware with her.",
          "",
          "“How should I know where we’ll go after this and how we’ll end up. I thought we could need them. “",
          "",
          "All her jewelry was long gone, and now she threw away everything metallic we owned.",
          "",
          "She understood that she couldn’t refuse my requests any more, so when the people stopped entering we went to find aunt and uncle Kika. Uncle Kika was frighteningly pale. He was a person with special needs, but he was never as frightened as he was then. Mother asked auntie where Senaid, my Young Uncle, was. She said that he had left through the forest. I approached Edo and Dada, sat next to them on a blanket that covered the concrete, it was hard and cold.",
          "",
          "So many faces that we’d never seen before were now around us in the hall. We’d never seen them, but felt like we’d always known them. Through the halls the waves of panic echoed. Ears picked up news that they were taking people out and slaughtering, raping and shooting them. Mother and father were murmuring with the people around us that everything will be over soon, that we should be OK there with the UNPROFOR troops, but, as the day grew dark, it became clear that being inside these factories we were actually in a camp.",
          "",
          "We made it somehow through the night… I dragged my head into my hole in the wall again, mother leaned her body against me, as if to blanket me, to make me feel safer, all I heard throughout the night were moans, the cries of children, and somewhere in the distance behind the factory through that entrance, screams. Did I dream or did I merely vacate that hall in spirit, I don’t know, but all night long in front of my eyes was Drinjacha, warm milk from Pahljevichi, raspberries from Burinca, the sunset in Konjevic Polje… everything was mixing about in my head at random, as the camp drilled it’s scent and darkness into my memories.",
          "",
          "The commotion and noise started very early in the morning. No voices came to us to say what it’s about, but the people were buzzing around the hall, going here and there. Father jumped and went through the doors that we came through, to see what was going on. Later father arrived with a tin plate, in it some soup, and a slice of see-through bread. The soup looked like dirty water, the plate was shallow, we had thrown our cutlery away, and we couldn’t even drink it. We wanted to pick the soup up with the bread, but it had melted in it.",
          "",
          "As I was puzzling how to eat that bit of soup, father was telling mother how he was with uncle Ibro, and how he still could not stand up from the stretcher we saw him on yesterday. He said that he was very pale and had ‘dried up’. “No one had an infusion to give him”. I asked my mother and father to let me go for a walk. On the right side, in the distance, I saw that there was no wall and that the light was shining through. When I arrived to that gap I saw a field full of people, just like the people inside the hall, baking in the sun on the open field.",
          "",
          "When I returned I couldn’t manage telling them what I had seen. Mother ordered me to sit, and then in front of us a man squatted down. He asked for names, father’s and mine. “The UNPROFORs told us to only write down the men” he said. Father said that I am Jasmin Jusufa Jusufovic, and he wrote it down. Oh how proud I was when father pronounced my name like that. I thought, I am the son of my father, and no one can do a thing to me. When the surveyor left, from the side some man exclaimed: “Not UNPROFORs… chetniks!”",
          "",
          "Father, why did that man write down only mine and your names?",
          "",
          "I don’t know. Maybe to know how many people are here..",
          "",
          "But, why didn’t he write down mother's name as well? Will he write down Nica? Did he write down Keka and Nana? Who will write down Kika?",
          "",
          "I don’t know my dear child.",
          "",
          "Mother and father were talking to the people around us. To us in the corner the word arrived that they had sent chetniks among us, that they were taking people out to a white house near the factory, to rape and shoot. That is why my mother didn’t let me go alone to that field, out into the air.",
          "",
          "Please mother, let us go outside, to get some air.",
          "",
          "I don’t want to, sit down.",
          "",
          "Please mama, just to see the sun for a bit.",
          "",
          "I don’t want to, sit down!",
          "",
          "But mama, let us go to Nica and see Edo and Dado, let us go together.",
          "",
          "When we get there…",
          "",
          "Come on mama, I just want to go for a bit to that corner over there, you will see me from Nica's spot.",
          "",
          "Every half a minute I would turn toward mother to wave and confirm that we could still see each other. Then I heard from the end of the fence screams, and yelling, and managed to make out what a female voice was shouting. I ran to my mother, so quickly that I thought I’d be able to turn back the passage of time.",
          "",
          "“What happened, what was that screaming about?”",
          "",
          "“I don’t know, a woman was shouting: `He stomped on her child!`.”",
          "",
          "“Who stomped on whose child?”",
          "",
          "“I don’t know mama, I just heard a woman shouting: `He stomped on her child!`.”",
          "",
          "All at once a deathly silence fell over the people. All of them were mute, for a moment there wasn’t a whisper to be heard.",
          "",
          "Later, by word of mouth, arrived the news that a chetnik had taken a newborn baby from a woman that had just given birth next to the fence, and had stomped on it.",
          "",
          "No longer did I dare  mention that I would like to go somewhere. I couldn’t ask to go to see auntie any more. I only looked for that hole of mine in the wall, to hide my head and bring back the calming images of Drinjaca to my mind.",
          "",
          "When it started to get dark again, I asked, thinking aloud to myself and talking under my chin, when will we get away from here?",
          "",
          "Father had heard me and thinking I was asking him replied:",
          "",
          "“Tomorrow we go. Now go sleep!”",
          "",
          "Panic in the hall on the 13th of July woke me up. Half the hall was on their feet, mother was collecting around me the few things we had, and put into my lap my little backpack, in which the only thing remaining was that bottle that smelled like gasoline.",
          "",
          "“Are we leaving?”, I asked. No one had heard me.",
          "",
          "The murmur that I could ignore the previous days, as an ever present hum, had become louder that morning. People were bringing in news, some woman who had fainted was being brought back to consciousness a bit further to our left, a grandfather was being helped to his feet, a mother was trying to silence a screaming infant.",
          "",
          "I was telling myself, I am not going to be like that. I am nice, I will not cry and scream, and worry my mother and father. Everyone was ready to go somewhere, but we were not moving. The people, who had managed to get up, were only shifting and turning in place. Soon after, in the depths of the hall, the same side we had entered by, the large doors opened, and the people, like a river started going through that one door. We did not go straight towards it, but the crowd carried us along the middle of the hall, to the place where auntie was sitting, but they were already gone.",
          "",
          "Suddenly we stopped, next to a green truck, but the people were still squeezing towards the doors. I nudged my father's hand and tried to shout to him:",
          "",
          "“Babo, I can’t breathe!” he took me and put me on his shoulders. I was at the same height as the truck. The truck had people on it. When we finally started, when we got outside I was blinded by the sun and the heat. I clutched my eyes. Father noticed that I am no longer holding onto his shoulders, so he removed me from himself, and placed me between him and mother, and they both took me by my hands. I threw down my gaze towards my legs to let my eyes adjust to the light.",
          "",
          "We were walking through a long line of soldiers. I only saw real boots, and legs in military clothing and the barrels of rifles. They were forcing us, yelling, we had to start running. I hadn't gotten used to the sun until we went out onto the road. In front of us was a line of trucks, buses and soldiers to the left. Father was pushing mother and me in front of himself, mother was holding me tight against her, wrapping me up in her clothes. It was hard to run like that. We were passing one, then another truck and bus, all until someone shouted GET IN! And I found myself in front of a wooden ladder which was leaned onto the truck.",
          "",
          "I thought, the sooner I climbed up the ladder, the sooner all three of us would be on the truck, and all of this would come to an end. Halfway up the ladder I heard an order shouted behind me: “You, get down! Over here!” and I heard a commotion. I climbed up the few remaining steps, and turned around.",
          "",
          "Mother was sitting in front of the ladder, waving her hands, and down the road a soldier was pushing my father with a rifle. I couldn’t see anything else, besides the three of us and that soldier. I went stiff. Father was being forced by the soldier into a ditch by the road, while another was yelling at mother to make her climb up. Mother grabbed me, completely petrified and carried me deeper into the truck. I was still looking towards father. He was standing in the ditch. He turned around towards us to see us out. When he saw I was looking at him, he put his finger over his mouth as to tell me “shhhhhh”, and waved his hand as if to say “go”.",
          "",
          "I didn’t hear, maybe we were shouting, but I didn’t hear. I remember that I couldn’t move a thing. I left my eyes on my father. I only know I clenched my teeth hard, and inside I wanted to explode. It made tears come to my eyes, that were fogging up my view of my father. I only managed to raise up a hand a little bit, and in my mind I thought I was waving to him. He only repeated that motion of his “shhhh! Go”. He didn’t have his usual beret on his head, I thought that maybe he would be hot out in the sun.",
          "",
          "He was wearing a t-shirt on him, creme colored, with wide azure stripes. A worn out leather vest. Black pants and worn-out shoes, the only ones he had in Srebrenica. Under his arm he was carrying my red hooded jacket. And so he stayed, watching us, “shhhhh! Go”.",
          "",
          "It was as if someone had taken me, carried me out of the truck and into the heavens. I saw that on the ditch around my father were many more men. Behind them I saw ripe wheat as it was playing around in the sun and wind, behind the hall people were still going out, some were climbing into buses. I saw mother on the truck, squeezing against me, wrapping me up in her long, wide dress, and crying.",
          "",
          "From somewhere a teenager jumped into the bottom of the truck. He crawled behind some barrels that were there, and then the women threw the bundles they were carrying with them onto him. When the truck was filled up with people, they closed the tarp, and we were surrounded by complete darkness that made me go blind again. When the truck started, I whispered to myself “father will be in another truck…”",
          "",
          "The truck drove slowly, for a long time, taking us somewhere. Under the tarp we were running out of air. Suddenly we stopped. We all went quiet. I could hear the doors of the truck, and then I saw a long wooden beam opening a piece of the tarp for us, yelling: Why have you closed yourselves, you are going to suffocate, god fucking damn you. Through the open part we saw from the part of the road that we were at, how they were, from somewhere in front of us, shelling the forest. I remembered my Young Uncle who went through the forest. The wooden beam talked again: There, watch a bit, so you don’t get bored.",
          "",
          "In the little bit of light around myself I saw a grandmother, with her head wrapped in some kind of bloody bandage, and here eyes were also covered by the bandage, and a lot of women. I don’t recall seeing a single child. The board closed down on the tarp again, and they kept on driving. Long after the truck stopped again, and mother put me on the floor, and covered me with some things. The other women around me threw their things on me, and then I felt something heavy being put over everything that was on top of me. Afterwards I heard a voice.",
          "",
          "“Are there any men here?”, The women were saying that there weren’t, and then they started screaming.",
          "",
          "“There isn’t anyone else, there was just him! There is no one else!”",
          "",
          "The truck started again soon after.",
          "",
          "When we stopped again they unloaded us from the truck on a road, under which there was a meadow and a river, and above it another meadow, a house and the army. They were showing us to go along the road and saying, go there, your people are there… mother and I went along the road… the two of us were alone.",
          "",
          "We heard from the people that the place we came to is called Tišča. The road they pointed us at led towards Kladanj. We walked for hours and hours, stopping on occasion to catch our breaths. Along the way there were knocked down trees, so we had to climb over some, and crawl under other.",
          "",
          "Along the way the people from the edge of the street told us, just keep on straight towards the tunnel, your people are there after the tunnel…",
          "",
          "The tunnel was nowhere in sight, behind every bend in the road there was just another bend, under the road a river, above the road a gorge, and through it a bit of sky.",
          "",
          "The first dark started to fall, and we finally saw the tunnel. Going through the tunnel, further along we found my aunt Safeta, Niska and Niho. When she saw me Niska screamed. From her voice the gorge broke, and I froze. I don’t remember anyone hugging me and shaking as firmly as Niska did then. Through the tears she kept on repeating ‘dad is gone, my dad is gone’... I was thinking, so uncle Muhamed is also in another truck… and then I started crying. Without a sound, the tears just kept streaming..",
          "",
          "From there on we walked only because we had to. I wasn’t present in myself. I can only remember in flashes the frame of the window in the bus that was taking us to Dubrave, and the red sky in the frame of the window.",
          "",
          "By the time we got to Dubrava it was already dark. Mother and I were slogging through a sea of unknown silhouettes. Some were running towards cisterns, some towards the tents. Then mother saw a street light, and we sat there, on a pile of gravel. Outside the circle of light cast by the light bulb, all fell into darkness, like there wasn’t anyone left in the world, only mother and I, sitting on a gravel pile, and waiting. Mother was telling me: “They’ll see us first if we are here.” We didn’t know whether they truly would.",
          "",
          "I was already losing myself… Suddenly from somewhere someone shouted our names:",
          "",
          "“Husnija! Jasko!”",
          "",
          "We didn’t take notice, we didn’t believe.",
          "",
          "And from that darkness towards us appeared the face of my cousin Emir.",
          "",
          "“It’s me Husnija! Emir! Don’t you recognize me!”, he was saying, and crying.",
          "",
          "He gave us water to drink. And I was there for a moment, gone the next one. I remember waking up in his car, and next to him, in front of us, seeing another woman, whom I couldn’t recognize.",
          "",
          "In between falling in and out of consciousness, I noticed that they had brought us home. The woman from the front seat had taken me up some steps. When the light hit me, from the room at the top of the staircase, inside I saw two boys, and recognized that it was my cousin Kada, leading me by the hand.",
          "",
          "I do not know how, or when, but I found myself dressed in different clothes, and planted in front of a table, on which there was a baked chicken. I gasped, lost myself, I didn’t know what to do. I’m looking at Kada, and then at the chicken… Kada took a piece of chicken, put it in my hands, and said: “come on, chicken, eat!”.",
          "",
          "That baked chicken, and the piece of meat in my hands that I didn’t know what to do with, those were the last things I remember of that day. I know that I slept, where and how I fell asleep I don’t remember. I just couldn’t be present any more on this world that day.",
          "",
          "I returned to Srebrenica for the first time in ‘99. The “Children’s Embassy” had organized a visit, and I suddenly found myself in front of the municipal council, with a microphone in my hand, having to say something after an insulting heckle from a councilwoman.",
          "",
          "I told them: “Look at me! You haven’t killed all of us!”",
          "",
          "I remember Duško Tomic, who I came with, going pale, and the journalists and cameras suddenly falling upon me, so I added:",
          "",
          "“By the will of God, I have remained alive after my father, to testify about my father and what was done to us, whether you like it or not.”",
          "",
          "I do not know where I got the courage.",
          "",
          "After this they grabbed me, and we left faster than we had arrived. I did not have the time to ask to stay for a bit, to see if any of my books were left, anything of my fathers, was there anyone in our house or to check if they burnt my snow sled.",
          "",
          "Translated by:  Burza, Širbeg, Erdžan, Sanjin, Damir, Imer"
        ]
      },
      Dutch: {
        title: "Jasmin Jusuf Jusufović",
        subtitle: "De kleine prins",
        language: "Dutch",
        paragraphs: [
          "In de ochtend van 8 juli hoorde we een gedempt geluid komen van ergens in de heuvels, het was niet veel luider dan een deur die werd dichtgegooid of een steen die werd gegooid en ergens op de stoep belandde, maar het was nog steeds luid genoeg om ons wakker te maken. Ik sprong uit mijn bed en ging naar mijn schuilplaats achter de vriezer, waar de muur het dikste was. Waar ik ook de volgende paar dagen ontelbare keren terug ben gegaan voor bescherming. Dit was niet de enige plek waar ik me verstopte; ik ging namelijk zo nu en dan naar een andere plek, als de vriezer te ver zou zijn, zouden mijn familie en ik een nieuwe plek vinden, en elke keer weer keken we voor een muur die dikker was dan de andere.",
          "Vanaf 9 juli, zat ik de meeste tijd weer voor de vriezer, het was echt heel saai. Dus, Om de tijd te doden, telde ik de explosies die door de hele dag door konden worden gehoord. Soms waren het er dertig tegelijk. Mensen liepen toch over de straat. Bakker Ramo rende door de straten met gewonden in een bakwagentje. In de middag werd het rustig. Ik ging kijken wat mijn Edo en Dado aan het doen waren. Ik wist ook dat mijn vader ergens in de buurt was. Toen ik naar beneden naast Milosev liep dacht ik dat mijn vader over de rivier naar de smid was gegaan. Een granaat explodeerde in het midden van de open plek nabij Naser’s dam, maar mijn grootste zorg was dat mijn moeder moest laten weten dat alles goed met mij gaat. Ik rende en schreeuwde naar mijn tante: “ik ga naar mijn moeder”, maar dat mocht niet van haar. Ik moest in de schuilplaat onder de tafel blijven met Edin en Dado. “Je moeder weet dat je bij ons bent”, zei ze. Naast al die onrust, ging het leven toch nog door als normaal.",
          "Het was 10 juli, we waren naar de tweede verdieping gegaan om de kamer van mijn oma schoon te maken. Ik pakte een van de dekens en ging naar het balkon om het even schoon te schudden. Op dat moment viel een granaat achter het huis. De aarde viel op het dak, net als regen. Tien seconden lang heb ik aan de muur gehangen. Na die granaat zei ik geen woord meer. Mijn ouders waren bezorgt. Toen het in de middag wat rustiger werd, hadden mijn ouders besloten om mij naar mijn tante te brengen, ze wist hoe ze angst moest bedaren. Ze gaf me een pot met water en metaal, ik moest erin kijken. Toen zei ik tegen mijn tante: “ Waar zie je de leeuw?”.",
          "Tante woonde in Kazan en wij in de stad, en onder het gevaar van de granaten was het best wel ver om te lopen. Vandaag de dag heb ik nog steeds geen idee hoe ze in die tijd hebben gecommuniceerd om af te spreken, zonder een telefoon of zoiets. Na die poging om mijn angst te bedaren, was ik niks minder bang. En toen begonnen ze weer met de granaat aanvallen. Edo en Dado waren bij ons. Edo en ik gingen bij de vriezer zitten en Dado naast een andere dikkere muur. Iemand zei dat de granaten bij de oude stad zijn gevallen. Ik dacht meteen aan mijn vriend Kemo. Hij woonde daar.",
          "De ochtend van 11 Juli is meteen met beschietingen begonnen, nog dichter bij ons. Het was best wel heet die dag. Moeder en vader deden onze spullen in de tassen,  in het geval dat we weg moesten. Ze wouden niet met lege handen gaan, zoals toen we naar de Drinjac gingen. Snel pakte ik mijn boekjes “de Kleine Prins” en “1000 waarom daaroms” en ik stopte ze in mijn kleine rugzak. De beschietingen gingen de hele ochtend door. Door de straten renden meer en meer mensen. Wij stonden te wachten, tot  iemand riep: ”de Chetniks zijn bij de markt!”. Wij  pakten meteen onze rugzakken, en vader pakte mij ook net als een rugzak op. Daardoor vloog ik over de berg waar ons huis stond en kwam ik tussen de mensen die aan het vluchten waren. Achter ons konden we knallen en schoten horen. Rennend door de straat, bij het hek naast de school waar vroeger kinderen toernooitjes speelden, had ik mijn rechter mouw gescheurd. Mischien ook mijn arm, maar op dat moment voelde ik niks. Ik weet nog dat mijn moeder mij uit dat hek had getrokken en dat wij naar het huis van mevrouw Munevere bij het bus station renden. Opeens stopte we. Vader ging snel terug naar de stad en moeder heeft mijn rugzak gepakt, ze haalde de boeken eruit, keek me aan, ik schudde. Mijn Kleine Prins bleef achter. Ik stond bevroren naast mijn boeken, moeder probeerde uiteleggen dat boeken te zwaar zijn om mee te nemen... Ik was stil, ik dacht, mischien ook niet, maar ik zei niks. Dadelijk kwam vader bij Mevrouw Munevera, met de zak bloem op zijn schouders. Hij zei dat ze bij Naserov’s magazijn hadden ingebroken. Mevrouw Munevere heeft daarmee oliebollen voor ons gemaakt. Ik was vergeten dat tot dat moment ik niks had  gegeten en dat ik ook geen honger had. Ik had maar één oliebol opgegeteen. Toen gingen we uit elkaar. Vader zei dat hij wat ging brengen, maar eigelijk ging hij door het bos. Terwijl we liepen, zijn we tante Sena  tegengekomen en samen gingen we verder. Rond het ziekenhuis werd het druk. Ik zag mensen over de balkons springen binnen in nabijgelegen gebouwen. Wij zijn ook in een van die balkons gesprongen. Wij zijn niet lang in de duisters van dit gebouw gebleven, wij gingen verder, maar verder dan het ziekenhuis konden wij niet.  Mensen hebben geprobeerd het kamp van de VN in Vezionica binnen te gaan, maar iemand zei dat Zulfo Tursun de jongens terug stuurt van het ziekenhuis. Op deze manier hebben we onze tante verloren. Moeder, oma en ik zijn bij een van de gebouwen dichtbij het ziekenhuis gestopt, achter Vezionica. Ergens in de buurt hoorden we een granaat. Mijn oma sprong eerst op mij, dan moeder en boven ons kwamen de laatste overgebleven stukken glas van het gebouw. Ik weet nog dat ik onder oma en moeder bijna geen adem had. Na die granaat drongen de vrouwen bij het hek van Vezionice nog harder op. Wij gingen ook met massas mensen op straat. Tussen al dat geschreeuw hoorde ik een bekende stem: “Husnija!!!” Vader kwam terug uit het bos. “Ik ga met jullie mee, en het maakt me niet uit wat gaat gebueren”. Ergens hebben ze het hek van Vezionica kapot gemaakt. Vader heeft me op een of andere manieren binne het kamp gebracht, en we gingen naar de garage van Un’s autos en tranporters. En toen kwam er nog een granaat. De scherven hebben gaten in het plaatwerk van de garage gemaakt en daar kwam licht doorheen. Wij zijn inmiddels mijn moeder verloren. Mijn oma ook. Wij zijn ook mijn tante , Edo , Dado, Keke en oom Kike verloren….allen vader en ik zijn nog samen. Vader zag de VN terugtrekken uit Vezionica, in hun trucks, hij heeft me gepakt, rende naar een vrachtwagen, en hij heeft me in aanhanwagen van vrachtauto gegooid. Hij heeft nog meer mensen geholpen om in de aanhanwagen te klimmen. Ik werd door al die massas mensen ergens in de hoek geperst. Ik kon net mijn hoofd een beetje naar buiten draaien om bij adem te komen. Door al die drukte zijn vader en ik auto elkaar geraakt. Onze handen raakten los. Op de weg kwame nog meer mensen in de vrachwagen, ik had bijna geen adem meer. De vrachwagen kwam langzaam in de volgende basis, in de accu fabriek. Door rijen van mensen en VN kwamen vader en ik in een donkere hal. Op de weg zagen we oom Ibro, hij lag op een brancard. Er kwamen meer en meer mensen in de fabriek. Vader heeft ook een brancard voor mij geregeld. Nu kon ik zitten. Ik zat ergens naast de muur. Naast een vernielde ingang. De brancard was nat. Nat van het bloed. Terwijl we op op de brancar zatten, keek ik de hele tijd naar de deur waardoor we binnenkwamen. Misschien zou mijn moeder door die deur komen. De tijd heeft elke context verloren. Opeens kwam mijn moeder aangesneld, huilend, het leekt alsof ze ruzie met iemand had gehad. Ze zag ons meteen. Ze zei dat ze van de VN niet naar binnen mocht komen. Ze had ruzie met een van de VN’ers en op een of andere manier kwam ze toch naar binnen. Vader ging op zoek naar water. Hij bracht een fles die naar benzine stonk, met daarin een klein beetje water.  Hij heeft ook tante, Edo en Dado en oom Kiko gevonden, maar oma was er niet. Moeder zei dat oma  in de ander fabriek met onze zwangere tante Keka is gebleven. Ik had genoeg van al die ruis, het geschreeuw, ik kon al die bebloedde gezichten en het grijs van de fabriek niet meer aanzien. Ik heb een gat in de muur achter me gevonden. Ik deed mijn hoofd daarin. En zo ben ik in slaap gevalen. Naar een paar minuten schrok ik. “Waarom ga je niet verder slapen?”, zei moeder. Ik wilde naar tante. “Later”, zei moeder. Ik wil naar het toilet. Ze nam me naar achter de ingang, door de fabriek. Ik zag weer bloederige en gesheurde mensen, veel uitwerpsels en bloederige ingewanden. “Ik hoef niet meer naar het toilet”, zei ik, en ik ben niet meer naar het toilet gegaan. Op een bepalde moment begon mijn moeder door de tassen te kijken. Ze nam een ​​bundel van bestek en ze rende om die weg te gooien. In de fabriek vertelden de mensen dat de Chetnik, verkleed in VN uniforms, door de fabriek patrouilleren en ze redenen zoeken om iemand naar buiten te brengen, om hun dood te kunnen schieten. “Wat moet je daar me, verdorie?”, vroeg vader. “Ik had geen idee waar wij naar toe gaan, wat er gaat gebeuren. Ik dacht dat we het nodig hebben”, zei moeder. Voor al lange tijd had ze geen juwelen en die dag heeft ze ook alles van metaal weggegooid. ",
          "Ze konden niet meer weigeren. Toen mensen zij gestopt met binnen komen, gingen we tante en oom Kiko zoeken. Oom Kiko was hartstikke bleek. Hij was de persoon met de speciale behoeften , maar tot nou toe was hij nooit zo bang. Moeder vroeg tante waar haar Senaid is, mijn jongere oom. Ze zei dat hij door het bos is gegaan. Ik kwam dichter bij Edo en Dado. Ik ging naast hun zitten op een dekentje op het beton. Het was hard en koud. Zo veel onbekende mensen zaten in de hal. Ik heb ze nooit gezien, maar het voelde alsof wij elkaar al langer kenden. Door de hal kwamen alleen golven van paniek. Wij hoorden steeds dat ze mensen naar buiten brachten en dat mensen werden neergeschoten, verkracht en geslacht. Iedereen dacht dat alles snel over zou gaan, dat wij veilig zijn bij de VN , maar toen het donker werd realiseerde iedereen dat deze fabriek een kamp was. Op een of andere manier hebben we de nacht doorgebracht….Ik heb weer met mijn hoofd in dat gat gekropen, mijn moeder leunde tegen me aan alsof zij me vasthield, om mij een veiliger gevoel te geven. Het enige wat ik door de nacht hoorde waren zuchten, huilende kinderen en geschreeuw, ergens ver achter de fabriek. Had ik het gedroomd of was ik met mijn ziel uit de hal gevolgen, ik weet het niet, maar de hele nacht door zag ik rivier Drinjaca, warme melk van Pahljevic, frambozen van Burnica, de zonsondergang in Konjevic Polje... en al die gedachten gingen door elkaar. Terwijl de geur en de duister van het kamp op mijn geheugen stuitte. ",
          "Chaos en schreeuwen begon vroeg in de ochtend. Wij hadden geen idee water aan de hand was, maar het volk ging op een neer door de gang. Vader sprong op en ging naar dezelfde ingang waar binnen kwamen. “Ik ga kijken wat er aan de hand is”, zei hij. Later kwam vader met en metalen bord, met daarin een klein beetje soep en een klein beetje doorzichtig brood. De soep leek op het vieze water, het bord was ondiep, bestek hebben we weggegooid en de soup  konden we ook niet opdrinken. We wilden het brood in de soep dopen maar het brood verdween in de soep.  Terwijl ik dacht hoe ik die soep ging opeten, zei vader tegen moeder dat hij bij oom Ibro was en dat hij nog te zwak is om van de brancard op te staan. Hij zei dat hij er nog steeds bleek en uitgedroogd uitzag. “Niemad is er om hem een infuus te geven”, zei vader.  Ik vroeg moeder en vader of ik een rondje mocht lopen. In de verte aan de rechter kant zag ik dat er geen muur was , het licht kwam er doorheen. Toen ik bij deze opening kwam, zag ik veel volk in de weide, zo veel volk als binnen in de gang, brandend in alle kaalte in de zonnenschijn. Toen ik terug kwam kon ik niet vertellen wat ik had gezien. Mij moeder beveelde me om te gaan zitten, en toen knielde er een man voor ons.  Hij vroeg onze namen, de naam van vader en de mijne. “de VN vroeg om alle mannen namen”, zei hij. Vader zei dat ik Jasmin Jusuf Jusufovic was en hij schreef het op. De manier hoe mijn vader mijn naam uitspraak maakte mij zo trots. Ik dacht, ik ben de zoon van mijn vader, en niemand kan mij kwaad doen. Toen de teller weg was, zei een man ergens aan de linkerkand: “Niet de VN.. de Chetniks willen onze namen!”. “Vader, waarom schreef die man alleen jouw en mijn naam op?”, zei ik. “ Ik weet het niet. Misschien wilde ze weten hoeveel bevolking er is” , antwoordde hij……”Waarom vroeg hij dan niet ook om moeders naam? Schrijven ze Nico’s naam op? En Keko en oma? Wie schrijft Kiko erin?”, ging ik door met vragen. “ Ik weet het niet mijn kind, ik weet het niet.”, antwoorde vader. Moeder en vader gingen met mensnen om ons heen praten. Het nieuws kwam naar onze hoek dat ze een Chetnik tussen ons hebben gelaten en ze brengen mensen naar een witte huis naast de fabriek en daar verkrachten ze en schieten ze ons dood. Daarna mocht ik van mijn moeder niet meer naar die weide gaan om een luchtje te schreppen. “Alstublieft moeder mag ik gaan om een luchtje te schreppen”  vroeg ik. “Nee, zitten.”, antworde zij. “Alstublieft moeder, ik wil alleen de zon zien”, ging ik door. “Nee,zitten!” , anwoorde zij weer. “Maar, moeder, kunnen we dan naar Nico, daar kunnen we Edo en Dado zien, kunnen we niet samen gaan?”, ik ging door met vragen totdat zij mee ging.",
          "Toen we er kwamen…..”Moeder, mag ik gewoon een beetje daar naar die hoek, Ik blijf met Nico.  Je zult me zien”, zei ik. Elke half minuut zwaaide ik naar mijn moeder. Toen hoorde ik in de verte ergens bij het hek, gegil en geschreeuw, ik hoorde wat de vrouw aan het schreeuwen was. Ik rende heel hard naar mijn moeder. “Wat is er aan de hand? Wie is er aan het schreuwen?”, vroeg moeder. “ Ik weet het niet. Een vrouw zei: “Hij rijdt over mijn kind!””. “Wie is er over de baby gereden?” vroeg moeder. “Ik weet het niet moeder, ik hoorde alleen een vrouw zeggen : “Hij rijd over mijn kind””, antwoorde ik. Opeens werd het stil. Iedereen was stil, zelfs het fluisteren was gestopt. ",
          "Later hoorden we dat er naast het hek een vrouw was bevallen. Toen kwam er een Chetnikdie de baby toen oppakte en eroverheen reed. Ik durfde niet meer te vragen om ergens naar toe te gaan. Ik durfde niet eens te vragen om naar mijn tante te gaan….. ik was alleen op zoek naar het gat in de muur om mijn hoofd te verstoppen en om op zoekte gaan naar Drinjacu. Toen de nacht weer kwam, zei ik, tegen mezelf, “wanneer gaan we weg van hier?” Vader hoorde het, hij dacht dat ik het hem vroeg: “we gaan morgen. Ga nu maar slapen!”",
          " Op 13 Juli werd ik van de paniek in de hal wakker. De helft van het volk stond in de gang. Moeder ging onze spullen inpakken. Ze gaaf mij mijn rugzak. Erin was alleen die fles die aan benzine stonk. “Gaan we weg?” vroeg ik. Niemand hoorde me. De geluiden die ik hoorde in mijn oren, kon ik niet meer negeren, deze ochtend waren ze nog luider. De mensen kwamen steeds met nieuw nieuws, een vrouw werd geroepen door haar bewustzijn, aan de linkere kant probeerde ze een opa op zijn voeten te laten staan, een moeder probeerde haar kind still te houden. Ik zei tegen mezelf, “ik wil niet zo worden. Ik ben lief, ik ga niet huilen en schreeuwen, ik ga mijn vader en moeder niet ergeren.” Iedereen was klaar om te gaan, maar iedeeren bleef staan. Na een tijdje, in de diepde van de gang, bij dezelfde ingang waar wij binnen kwamen, gingen de deuren openen en kwamen er steeds meer mensen binnen, net een rivier.  Wij gingen niet meteen naar die deur, maar de menigte bracht ons naar het midde van de gang, waar onze tante zou zijn, maar ze was er niet. Opeens stonden we naast een groene vrachtauto. Er kwamen steeds meer en meer mensen door die deur. Ik pakte mijn vader voor zijn arm en ik probeerde te zeggen: “Vader, ik kan niet meer ademen!” hij pakte me en zet me op zijn schouders. Ik kon de vrachtauto zien. In de vrachtauto waren er ook mensen. Terwijl we liepen, zijn we buiten gekomen. Ik werd verblind door de zon en warmte. Ik dekte mijn ogen. Vader merkte dat ik hem niet meer vast hield en dus liet hij mij zakken, tussen hem en moeder. Ze hielden mijn handen vast. Om aan de zon te wennen boog ik mijn hoofd naar beneden. Wij liepen door lange rijen soldaten. Ik zag alleen de laarzen, de voeten in de defensie pakken en de lopen van de geweren. Ze duwde ons, ze schreeuwde, wij moesten rennen. Pas toen wij op de weg waren ben ik aan zon gewend geraakt. Voor ons stond een kolom van de vrachtautos, bussen en soldaten aan de linkere kant. Vader duwde moeder en mij voor zich uit. Moeder hield mij dicht tegen haarzelf, gewikkeld in haar rok. Het was moeilijk om zo te rennen. We liepen naast de eerste en de tweede bus en vrachtauto, tot iemand riep “GA NAAR BINNEN!”.  ik kwam terecht voor een aangelege houte ladder op een vrachtauto. Ik dacht, als ik er snel in kom, dan kunnen we sneller in de vrachtauto verstoppen, en dan zijn we gered. Toen ik bijna bij de top van de ladder was, hoorde ik dat er iemand achter mijn rug een bevel gaf :” Jij! kom daar af! Hier!” en ik hoorde wat rumoer. Ik klom nogeen paar treden voor ik me omdraaiden. Moeder stond voor de trap, met gekruisde armen. en langs de weg duwde een soldaat mijn vader weg met een geweer. Ik zag niks meer, behalve ons drieen en de soldaat. Ik verstijfde. De soldaat bracht vader langs de weg naar de greppel. Een van de andere soldaten schreeuwde tegen mijn moeder om te klimmen. Ik bevroor en moeder pakte me en bracht me dieper in de vrachtauto. Ik keek mijn vader nog steeds aan. Hij stopte op de greppel. Hij draaide zich nog een laatste keer om. om dag te zeggen. Toen hij zag dat ik keek, hij hield zijn vinger voor zijn mond zoals hij vroeger deed als ik stil moest zijn “ssssttttt”, en hij zwaaide met zijn hand zoals vroeger , toen hij “ga” wou zeggen . Ik hoorde niks, misschien waren wij ook aan het schreeuwen, maar ik hoorde het niet .Ik weet alleen dat ik niet kon bewegen. Mijn ogen zijn bij vader gebleven. Ik weet nog dat ik mijn tanden har od elkaar klemden en van binnen wou breken. Ik kreeg tranen in mijn ogen, het zicht op mijn vader werd waziger. Ik slaagde om mijn hand op te trekken, in mijn hoofd dacht ik nog dat ik naar hem zwaaide. Hij deed dezelfde beweging: “sssstttt! Ga”. Hij had zijn gebruikelijke baret niet. Ik dacht dat hijheet in de zon zal hebben. Hij had een T-shirt met korte mouwen aan, crème kleur, met azuurblauwe brede strepen. Een versleten leder vest. Een zwarte broek en versleten schoenen, de enige die hij in Srebrenica had. Onder zijn armen had hij mijn rode jas met kap. En zo bleef hij, kijkens naar ons, “sssstttt! Ga”. Het voelde als of iemand mij oppakte en uit de vrachtauto naar de hemel bracht. Ik zag dat er naast de kanaal nog meer mensen naast mijn vader stonden. Achter hun zag ik rijpe tarwe die op de zon en de wind deinsde. Uit de gang kwamen er nog steeds mensen. Sommige gingen in de bussen. Ik zag mijn moeder in de vrachtauto. Ze drukte me hard tegen haar aan en ze wikkelde me weer in, nu was ze aan het huilen. Ergens vandaan, sprong een jongen onder de vrachtauto. Hij verstopte zich achter de tonnen die er stonden. De vrouwen gooide de tassen die ze hadden over hem, om hem beter te verstoppen. Toen de vrachtauto helemaal vol zat, deden ze het met een zeildoek dicht, en weer werd het donker, ik werd weer blind. Toen de vrachtauto bewoog, zei ik tegen mezelf “vader komt met een andere vrachtauto….”. De vrachtauto reed langzaam en lang. Onder de  huif was er steeds minder en minder lucht. Opeens stopte we. Idereen werd stil. Ik hoorde de deur van de vrachtauto opengaan, en zag een lange lat die een deel van het zeildoek opende en schreeuwde: “Waaroom hebben jullie hem dicht gemaakt? Jullie zullen erin stikken in Godsnaam!”. Door een kleine gat zagen we dat er vanaf de weg waar wij waren ze op een bos hebben geschoten. Ik dacht meteen aan mijn jonge oom die door de bos heen is gegaan, en verdween. De lat begon weer te praten: “Kijke maar eventjes, dan is het niet saai voor jullie”. Van dat kleine licht, zag ik een oude oma, haar hoofd was gewikkeld in bloedderig verband, en ik zag dat haar ogenin hetzelfde verband waren gewikkeld. Ik merkkte ook dat er veel vrouwen in de vrachtauto zaten. Ik kan me niet meer herinneren of ik kinderen zag.. de lat liet de zeildoek weer zakken,en we waren weer begonnen met reiden. Na een tijde was de vrachtauto weer gestopt, mijn moederzette me op de grond, en bedekkte me met kleding. Een paar andere vrouwe gooiden hun rugzakken over me heen en toen voeldde het heel zwaar. Ik hoorde een stem. Zijn er nog mannen hier? De Vrouwen zeiden van niet, en toen begon het gescreeuw: “Niemand is er, hij was de enige! Niemand is er meer!” Na een tijdje begon de vrachtauto weer te rijden. Toen wij weer gestopt waren, hebben ze ons van de vrachtwagen gelost, onder de weg waar ze er ons uit lieten, was een weide en een rivier. en boven was een nog een andere weide , huizen en soldaten. Ze wezen op de weg en zeiden, “ga naar boven, de jouwe zijn daarboven….” Moeder en ik liepen op de straat….wij waren de enige. Van het volk hoorden we dat wij in Tišča waren. De weg leidde naar Kladanj. Wij liepen uren en uren, soms stoppte we om tot adem te komen. Op de weg waren sommige bomen verpletterd. Wij moesten door sommige bomen klimmen en bij sommige moesten we kruipen. Aan de rand van de weg zeiden ze, ga maar recht door de tunnel, de jouwe zijn achter de tunnel…..er was geen tunnel om te zien….achter elke bocht kwam nog een ander bocht. Onder de weg was de rivier, erboven waren kliffen, en tussen de kliffen was een klein beetje hemel.de eerste schemering begon. Toen zagen we de tunnel. Voorbijgande de tunnel, een klein beetje verder op, kwamen we tante Safeta, mijn Niska en Niho tegemoet. Toen Niska mij zag, begon ze van blijdschap te gillen. Haar gil bevroor mij. Ik kan me niet herineren dat er ooit iemand mij zo stevig heeft omhelst en zo naast mij stond te trillen, als Niska. Ze huilde… en herhaalde:” Mijn vader is er niet, mijn vader is er niet”….Ik dacht, oom Muhamed is ook in een de andere vrachtauto…..en toen begon ik ook te huilen. Zonder geluid. Alleen tranen. Vanaf dat moment gingen we normaal verder, alleen omdat het moest. Ik was niet echt aanwezig, tenminste niet als mijzelf. Alleen via uitbarsting herinner ik me de kozijnen van de bus die ons naar Dubrava heeft gebracht. In het kozijn van het raam was de rode hemel. We kwamen pas aan in Dubrava toen het al donnker was. Moeder en ik staarde naar de onbekende silhouetten van de zee. Ze botsten met het volk. Sommige rende naar de water en sommige naar de tenten. Toen zag moeder een lantaarn. Daar zaten wij, op de hoop grint. Buiten de cirkel van de lamp zonk plotseling iedereen in de duisternis. En het leek alsof er niemand meer was. Alleen moeder en ik. Wij zaten op de hoop grint, wachtend. Moeder zei: “Hier zullen ze ons sneller vinden.” Wij wisten niet eens of iemand  ons zou zoeken. Ik begon mezelf te verliezen….op eens hoorden we van ergens onze namen: Husnija! Jasko! Wij lette niet op, wij geloofde het niet.  Uit het duisternis kwam opeens het gezicht van mijn oom Emir. “ Ik ben Husnija! Emir! herken je me niet?!” huilde zei hij het. Hij gaf ons water te drinken. En ik, soms was ik er, en soms niet. Ik herinner me dat ik soms wakker werd in zijn auto, naast hem.  voor ons zag ik een vrouw die ik niet herkende. Half bij bewustzijn, merkte ik dat ze ons naar een huis hadden gebracht. De vrouw in de voorstoel bracht mij via een trap. Toen het licht van de kamer boven op de trap scheen, zag ik twee jongens aan de top staan. Toen  herkende ik de vrouw die mijn hand vastvield, het was mijn tante Kada. Ik weet niet hoe en wanneer, maar opeens vond ik mezelf in andere kleding aan tafel, met gebakken kip voor mij. Ik stond daar met open mond, ik verloor mezelf, ik wist niet wat ik moest doen. Ik keek naar tante Kada, en toen naar de kip…. Kada pakte een stuk van de kip, pakte mijn hand, en legde het stukje kip erin. Ze zei,” ga eten lieverd.” Het laatse dat ik me herinner van die dag is de gebakken kip en stuk vlees in mijn hand waar ik niet van wist wat ik ermee moest doen. Ik weet nog wel dat ik daarna ging slapen. Ik weet niet meer hoe, waar en waanner ik in slaap ben gevallen. Ik was kapot. Die dag kon ik gewoon niet meer in onze wereld aanwezig zijn. ",
          "In 1999 kwam ik voor het eerst weer terug naar Srebrenica. Het was door de Kinder Ambassade georganiseerd. Opeens stond ik voor de raadslid van het gemeentehuis, achter de microfoon met het gevoel dat ik iets moest zeggen, na dat ik komentaar van een raadslid had gehoord. Ik zei: “ Kijk mij! Jullie hebben niet iedereen gedood!” Ik herinner me nog dat Dusko Tomic, de man met wie ik kwam, versteef. De journalisten en de kameras waren opeens op mij gericht. Ik zei nog: “-Dankzij de God, ik bleef na het dood van mijn vader leven om getuigen te zijn van mijn vader, en om jullie te vertellen wat jullie met ons hebben gedaan, of je het leuk vindt of niet”. Ik weet niet waar ik die kracht van kreeg. Iedereen was op mij gericht en toen rennde we sneller weg dan dat we kwamen. Ik had geen tijd om te vragen of ik langer mocht blijven….om te zien of een van mijn boeken nog ergens lag…of er nog iets van mijn vader was overgebleven…. Of er iemand in onze huis woonde….of ze mijn slee hebben verbrand…",
          "Translated by: Aida & Alaya Lima"
        ]
      },
      French: {
        title: "Jasmin Jusuf Jusufović",
        subtitle: "Le Petit Prince",
        language: "French",
        paragraphs: [
          "À l'aube du 8 juillet, on entendait tonner quelque part au loin, dans les collines, mais suffisamment fort pour nous réveiller. C'est à ce moment que je me suis rendu dans mon abri à côté du congélateur, où le mur de la maison était le plus épais. Pendant quelques jours, nous avons passé d'innombrables fois à courir jusqu'au mur le plus épais de la maison, selon nos estimations faites en courant. Pour faire passer le temps dans mon nid devant le congélateur, à partir de ce 9 juillet, j'ai commencé à compter les obus entendus tout au long de la journée. Il y en avait jusqu'à une trentaine à la fois. Les gens sortaient dans la rue, mais en appliquant la règle du \"c'est la première qui tue\". Ramo le boulanger roulait à vive allure avec les blessés en arrière de son lorry.",
          "Puis, le silence fut vers midi. Je suis allé voir ce qu’Edo et Dado faisaient. Et je savais que papa était quelque part dans le quartier. Quand je suis descendu sur  Miloševa, je pensais que papa aurait pu aller chez le forgeron de l'autre côté de la rivière. L'explosion d'un obus m'a trouvée au milieu de la clairière près du barrage de Naser, mais ma plus grande préoccupation était de retrouver ma mère pour qu'elle puisse au moins voir que je suis OK. ",
          "En passant, j'ai arrêté chez ma tante et je lui ai lancé: «Je retourne chez ma mère pour ne pas l'inquiéter», et elle m'a à peine retenu dans l'abri avec Edo et Dado sous la table, disant que maman savait que j'étais avec eux.",
          "Et malgré tout, nous étions entêtés d'avoir une vie normale. Le 10 au matin, nous sommes montés à l'étage pour nous occuper de la chambre de mamie. J'ai pris une carpette et je suis sorti sur la terrasse pour l'aérer. L'obus est tombé juste au-dessus de la maison. La terre tombait sur le toit comme de la pluie et moi, je suis resté suspendu au mur une dizaine de secondes. Après cette frénésie sous les bombardements, lorsque ça s'est calmé vers midi, papa et maman ont décidé de m'amener chez une tante pour qu'elle me \"fasse fondre la terreur\"1 puisque je n'avais pas dit un mot depuis l'obus du matin. Ils pensaient que j’avais eu quelque chose. Chez la tante, je scrutais le bassin rempli avec de l'eau et du plomb et je disais à ma tante \"Où est-ce que tu vois le lion?\".",
          "La tante habitait à Kazani et nous dans la ville, qui était loin pour marcher sous les obus. Même aujourd'hui, je ne comprends pas comment ils en sont venus à communiquer qu'ils viendront nous rejoindre chez ma tante à la gare d'autobus, à mi-chemin, sans téléphone et sans aucun autre moyen de communiquer. Quand ils avaient terminé le rituel pour ma terreur, je ne me suis pas senti moins effrayé du tout. Puis les obus ont recommencé à tomber. Edo et Dado étaient chez nous, Edo avec moi près du congélateur et Dado près de l'autre mur épais. Quelqu'un a crié que ça tombait sur la vieille ville. Je commence alors à m'inquiéter pour mon ami Kemo.",
          'Au matin du 11 juillet, les tirs ont commencé très tôt, plus proche que jamais durant ces jours-là. Il faisait chaud. Maman et papa ramassaient des choses et les mettaient dans les sacs, probablement, si nous allions sortir d\'ici, au moins, on ne sortirait pas comme de la Drinjača les mains vides. J\'ai sauté et mis "Le Petit Prince" et "1000 pourquoi / 1000 parce que"2 dans mon petit sac à dos.',
          "Les bombardements ne se sont pas arrêtés toute l'aube et le matin. Le long de la rue, de plus en plus de têtes ont commencé à sortir de la ville en courant. Nous attendions quelque chose en vain, puis quelqu'un de cette foule a crié: « Les Chetniks3 sont descendus sur le marché! » Nous avons saisi nos sacs à dos, le père m'a saisi comme un sac à dos, alors j'ai volé par dessus la colline où notre maison était dans cette foule de personnes en fuite. Derrière nous, on entendait des rafales et des coups de feu.",
          "En courant dans la rue, sur la clôture détruite par l'obus qui a tué les enfants qui jouaient au tournoi de football devant notre école bien avant, j’ai déchiré mon manche droit. J'ai peut-être éraflé la main aussi, mais je ne l'ai pas ressentie à ce moment. J'ai seulement vu que ma mère m'a tiré de la clôture puis nous nous sommes précipités chez la tante Munevera à la gare d'autobus. Puis, nous nous sommes arrêtés à cause de quelque chose. Papa est parti en courant vers la ville et ma mère m'a enlevé mon sac à dos, en a sorti les livres, m'a regardée, j'ai haussé les épaules. C'est comme ça que mon \"Le Petit Prince\" est resté derrière. Quand elle m'a vu figer sur les livres, ma mère m'a réconforté en disant que les livres seraient trop lourds sur mon dos pendant que nous nous échappions et courions… Je me taisais, je pensais, peut-être ils ne le seraient pas, mais je voyais que plus rien n’avait du sens ni  en valait la peine.",
          "Plus tard, papa est entré dans la maison avec un sac de farine blanche sur le dos. Il a dit que les gens avaient dévalisé l'entrepôt de Naser. De ce sac, la tante Munevera nous a fait des beignets à tout le monde. J'avais oublié qu'avant ces beignets, je n'avais rien mangé depuis des jours et j'avais oublié que j'avais faim. J'en ai à peine mangé un. Peu de temps après, nous nous sommes séparés. Papa me faisait croire qu'il allait chercher quelque chose, mais il est réellement allé dans les bois. Nous nous sommes retrouvés avec la tante Sena et nous sommes partis plus loin. Autour de l'hôpital, il y avait une foule. J'ai vu des gens sauter par-dessus les balcons des bâtiments environnants. Nous sommes rentrés dans l'un de ces immeubles.",
          "On n'est pas restés dans l’obscurité de ce bâtiment trop longtemps, et sommes allés plus loin, mais nous ne pouvions pas aller au-delà de l’hôpital. Les gens essayaient d'entrer dans le camp des Nations Unies à Vezionica et quelqu'un a dit que Zulfo Tursun renvoyait les garçons à partir de l'hôpital. C'est là qu'on s'est perdu de vue avec la tante. Maman, mamie et moi nous sommes arrêtés sous l'un des bâtiments de l'hôpital et au-dessus de Vezionica. Tout près, un obus est tombé. Mamie s'est jetée sur moi, puis ma mère ensuite, alors que les vitres qui avait survécu sur le bâtiment nous tombaient dessus. Je me souviens que je respirais à peine sous mamie et ma mère.",
          'Après les obus, les femmes étaient encore plus insistantes à la porte et à la clôture de Vezionica. Et nous nous sommes retrouvés tous les trois dans la foule sur la route. À travers le bruit, j\'ai reconnu la voix: "Husnija !!!" Papa est revenu du bois. "Je serai avec vous et il arrivera ce qu\'il arrivera".',
          "",
          "Une partie de la clôture de Vezionica a été abattue. Papa a peiné pour me faire entrer dans le camp et nous sommes allés dans le garage des véhicules et des transporteurs de la FORPRONU. Puis un autre obus est tombé. Les morceaux d'obus ont pénétré la tôle du garage et la lumière a commencé à saillir à travers les petits trous. Nous avons perdu de vue maman. Nous avons aussi perdu mamie. La tante n'y était pas non plus, ni Edo, ni Dado, ni Keka, ni oncle Kiko ... nous n'étions que nous deux. Papa a vu les troupes de l'ONU sortir de la Vezionica dans les camions, il m'a attrapé et a couru jusqu'à un camion, m'a laissé tomber sur la carrosserie.",
          "",
          "Il a aidé beaucoup d'autres personnes à monter dans le camion, et toute cette foule m'a pressée sur le côté du camion. J'ai seulement réussi à tourner la tête vers l'extérieur pour respirer. À travers la foule, j’ai senti que papa et moi, dans toute la confusion autour du camion, on ne se tenait plus par les mains. ",
          "Le long du chemin les gens s'accrochaient au camion et je perdais de l’air chaque fois que j'en avais un peu. Le camion est lentement arrivé à une autre base, dans l'usine « Akumulatorka ». À travers le cordon de soldats de la FORPRONU et de gens, papa et moi-même avons terminé notre parcours dans l'obscurité de la salle. En passant, nous avons vu oncle Ibro, sur la civière.",
          "",
          "Il y avait de plus en plus de gens dans l'usine. Papa s'est chamaillé avec quelqu'un pour nous trouver une civière pour m'asseoir sur elle. Je me suis assis accoté sur un mur. À côté d'une porte d'entrée détruite. La civière était humide. De sang. On était assis sur la civière et j'ai pointé mes yeux vers cette porte par laquelle nous sommes arrivés pour voir si ma mère n'apparaîtrait pas de quelque part. Le temps a perdu tout contexte. Soudainement, ma mère était à la porte, rouge et en pleurs comme si elle venait de se battre avec quelqu'un. Elle nous a vus tout de suite. Elle a dit que la FORPRONU ne lui avait pas permis d'entrer dans l'usine, alors elle a eu maille à partir et s'est battue avec un soldat, et a finalement réussi à entrer. Papa est allé nous trouver de l'eau. Il apporta une bouteille qui sentait le gaz et avait un peu d'eau dedans. Il a dit qu'il avait trouvé le la tante, Edo et Dado, et oncle Kiko, mais n'a pas trouvé mamie. La mère a ajouté que mamie était restée dans une autre usine avec notre Keka enceinte.",
          "",
          "Je n'en pouvais plus d'écouter le bruit, les cris, de regarder les visages ensanglantés et la grisaille des halls d'usine. J'ai trouvé un trou dans le mur derrière mon dos et j'y ai mis ma tête et, d'une manière ou d'une autre, dans tout cela, comme si je m'étais endormi. Je suis sorti de ma torpeur une minute après, ma mère me demande pourquoi je ne dors pas, je veux aller chez ma tante, nous irons plus tard. Ensuite, je veux aller aux toilettes. Elle m'a emmenée à travers l'entrée, derrière l'usine, alors j'ai revu des personnes ensanglantées et déchirées, beaucoup d'excréments et des bandages et des guenilles en sang. J'ai dit que je ne pouvais plus aller aux toilettes et que je n'allais même pas plus loin.",
          "À un moment donné, la mère a commencé à fouiller nos sacs en panique. Elle prit un paquet de couverts argentés et courut le jeter. À l'usine, les gens racontaient que les Chetniks patrouillaient parmi nous vêtus des uniformes de l'ONU et ne cherchaient que des raisons pour faire sortir et exécuter des gens. \"Nom de dieu, femme, qu'est-ce que tu fais avec ça?\" lui a demandé papa.",
          "« Comment pouvais-je savoir où allons-nous après ça et comment finira-t-on? Je pensais qu'on pouvait en avoir besoin. »",
          "Elle n'avait pas de bijoux depuis longtemps, et alors elle a pris et jeté tout ce que nous avions en métal.",
          "",
          "Elle a vu qu'elle ne pouvait plus me refuser, alors quand les gens ont cessé d'entrer, nous sommes allés chercher la tante et oncle Kiko. Oncle Kiko était terriblement pâle. C'était une personne avec des besoins spéciaux, mais il ne m'avait jamais paru aussi effrayé. La mère a demandé à la tante où était Senaid, mon jeune Daidza. Elle avait dit qu'il était allé dans les bois. Je me suis approché d'Edo et Dado, assis avec eux sur une couverture sur le béton. C'était dur et froid.",
          "",
          "Tant de visages que nous n'avions jamais vus auparavant étaient maintenant autour de nous dans le hall. Nous ne les avons jamais vus, mais c'était comme si nous les avions toujours connus. Le hall ne faisait que subir des vagues de panique. La nouvelle parvint aux oreilles du hall qu'ils sortent les gens dehors, qu'ils les égorgent, violent et fusillent. Maman et papa parlaient avec les gens qui nous entouraient, que tout finirait vite, qu'avec la FORPRONU tout près devrait être OK, mais avec la lumière du jour qui disparaissait, il nous est apparu évident que nous étions dans ces usines en fait dans un camp de concentration.",
          "",
          "Nous avons réussi à passer à travers la nuit... J'ai de nouveau rentré ma tête dans ce trou dans mon mur, ma mère a blotti son corps contre le mien comme pour me serrer, me rassurer. Tout ce que j'ai entendu toute cette nuit c'était des soupirs, les pleurs de l'enfant et quelque part au loin derrière l'usine à travers cette entrée, des hurlements. Ai-je rêvé ou bien, mon esprit vient de sortir de cette salle, je ne sais pas, mais toute la nuit j'avais devant les yeux Drinjača, lait chaud de Pahljević, framboises de Burnica, coucher de soleil à Konjević Polje ... tout se mélangeait. Alors que le camp burinait son odeur et sa noirceur dans ma mémoire.",
          "",
          "Le vacarme a commencé très tôt le matin. Aucune information ne nous est parvenue sur ce qui se passait, mais les gens se promenaient ci et là dans le hall. Papa s'est levé et a franchi la porte où nous sommes entrés, je suis allé voir ce qui se passait. Plus tard, il est venu avec une assiette en fer blanc, il a apporté des soupes et une tranche de pain transparent. La soupe ressemblait à de l'eau sale et l'assiette était peu profonde. Nous avions laissé tomber les couverts et nous ne pouvions même pas siroter la soupe. Nous voulions ramasser la soupe avec du pain, mais il fondait dedans.",
          "Pendant que je réfléchissais à comment manger ce peu de soupe, papa disait à maman qu'il était chez oncle Ibro et qu'il ne pouvait toujours pas se lever de la civière où nous l'avions vu hier. Il a dit qu'il était très pâle et \"sec\". \"Personne ne peut lui faire une infusion.\" J'ai demandé à maman et papa de me laisser partir marcher. Sur le côté droit au loin, j'ai vu qu'il n'y avait pas de mur et que la lumière venait par là. Quand je suis arrivé à ce trou, j'ai vu un champ plein de monde, identique à celui qui se trouvait dans le hall, qui se faisait griller au soleil sur la plaine nue.",
          "",
          "Quand je suis revenu, je n'ai pas réussi à dire ce que j'ai vu. Maman m'a ordonné de m'asseoir, puis un homme s'est accroupi devant nous. Il a demandé les noms, celui de mon père et le mien. \"La FORPRONU nous a demandé de n'inscrire que des hommes\", a-t-il dit. Papa a dit que mon nom est Jasmin Jusufovic fils de Jusuf et l'homme l'a inscrit. Ma fierté quand mon père a prononcé mon nom! J'ai pensé: oui, je suis le fils de mon père et personne ne peut rien me faire. Quand le recenseur est parti, quelque part sur la gauche, un homme a crié: \"Quelle FORPRONU ... chetniks!\"",
          "Papa, pourquoi l'homme a inscrit seulement nos deux noms?",
          "Je ne sais pas. Peut-être pour savoir combien de personnes il y a ici ...",
          "Et ensuite pourquoi il n'a pas inscrit maman? Va-t-il inscrire la tante? Est-ce qu'il inscrira Keka et mamie? Qui va inscrire Kika?",
          "Je ne sais pas mon cher enfant, je ne sais pas...",
          "Maman et papa ont parlé aux gens autour de nous. Nous avons appris dans notre coin qu’ils avaient lâché les Chetniks parmi nous et qu’ils avaient emmené les gens dans une maison blanche près de l’usine pour les violer et les tuer. Pour cela, ma mère ne me laissa plus aller seul sur la plaine pour prendre de l'air. ",
          "Allez, maman, allons-y prendre de l'air.",
          "Non. Assieds-toi.",
          "S'il te plaît, maman, juste pour voir le soleil un peu.",
          "Non. Assieds-toi!",
          "Mais, maman, allons voir tatie puis voir Edo et Dado, allons ensemble.",
          "",
          "Quand nous sommes arrivés ...",
          "Allez, maman, juste un peu plus loin au coin, alors tu me verras à partir de chez la tante.",
          "Toutes les 30 secondes, je me retournais vers maman pour la saluer et confirmer que nous regardions. Puis j'ai entendu un cri depuis le bout de la clôture et j'ai reconnu la voix d'une femme. J'ai couru chez maman à la vitesse à laquelle je pensais retourner dans le temps.",
          "Quel est le problème, c'est quoi ces cris?",
          'Je ne sais pas une femme crie: "Son enfant s\'est fait piétiner"',
          "Qui a piétiné l'enfant de qui?",
          "Je ne sais pas maman j'ai juste entendu une femme crier \"Son enfant s'est fait piétiner\".",
          "Soudain, il y eut un grave silence dans toute la foule. Tout le monde était silencieux et il n'y avait même pas de murmure pour un moment.",
          "Plus tard, la nouvelle a commencé à circuler qu'un Chetnik a piétiné un bébé qui venait de naître.",
          "Je n'ai pas osé mentionner que je voulais aller quelque part. Je ne pouvais plus demander que nous allions chez la tante... Je cherchais seulement mon trou dans le mur pour cacher ma tête et me rappeler Drinjača.",
          "Quand la nuit était tombée, j'ai dit sous mon souffle, pensant que j'étais seul « Quand partirons-nous d'ici? » Papa m'entendit et pensant que je le lui demandais dixit:",
          "« On y va demain. Couche-toi maintenant! »",
          "",
          "Le 13 juillet, la panique dans le hall m'a réveillé. La moitié de la salle s'était levée, ma mère ramassait des petites choses qui m'entouraient et a mis mon sac à dos sur mes genoux, dans lequel il ne restait que cette bouteille qui sentait le gaz. « Est-ce ce qu'on s'en va? » ai-je demandé. Personne ne m'a entendu.",
          "",
          "Le chaos que je pouvais ignorer comme un bruit de fond les jours précédents était plus fort ce matin-là. Les gens ont apporté de nouvelles, on tentait de ramener une femme à la conscience un peu plus à gauche, on remettait un grand-père  sur ses pieds, une mère essayait de faire taire un enfant qui hurlait.",
          "",
          "Je me suis dit, je ne serai pas comme ça. Je suis gentil, je ne vais pas pleurer et crier et inquiéter maman et papa. Tout le monde était prêt à aller quelque part, mais nous n'avons pas bougé. Les gens, ceux qui s'étaient levés, se balançaient d'un pied à l'autre et se retournaient sur place. Peu de temps après, au fond de la salle, du même côté où nous sommes entrés, une grande porte s'est ouverte et les gens, comme une rivière, l'ont prise d'assaut. Nous ne sommes pas allés directement à la porte; la foule nous a jetés au milieu du couloir, à l'endroit où ma tante était assise, mais elles n'étaient pas là.",
          "Soudainement, nous nous sommes arrêtés devant un camion vert, mais les gens ont continué à faire pression sur la porte. J'ai tiré mon père par le bras et j'ai essayé de lui crier:",
          "\"Papa, je ne peux pas respirer!\" Il m'a pris et m'a mis sur ses épaules. J'étais au niveau du camion. Il y avait des gens sur le camion. Quand nous avons finalement commencé à bouger et sommes sortis dehors, le soleil et la chaleur m'ont aveuglé. Je me suis pris les yeux. Mon père a remarqué que je ne le tenais plus par sa ceinture, alors il m'a pris et m'a descendu entre lui et ma mère et m'a pris par les mains. J'ai baissé la tête pour habituer mes yeux à la lumière du jour.",
          "",
          "Nous avons marché à travers un long cordon de soldats. Je n'ai vu que de vraies bottes et jambes en tenue militaire et les canons des fusils. Ils nous ont poussés, ont crié, nous avons dû commencer à courir. Je ne m'étais pas encore habitué au soleil avant de sortir sur la route. Devant nous s'étendait une colonne de camions et de bus et des soldats à la gauche. Mon père nous poussait, ma mère et moi, ma mère m'a serré près d'elle, m'enveloppant dans ses jupes. Il était difficile de courir comme ça. Nous sommes passés à côté de l'un, puis de l'autre bus et camion, jusqu'à ce que quelqu'un crie \"ENTRE!\" et je me suis retrouvé devant des échelles en bois appuyées contre le camion.",
          "",
          "Je me suis dit, le plus tôt que je monte l’échelle, plus tôt tous les trois nous serons dans le camion et tout sera fini. Rendu à la moitié de l'échelle, j'entendis l'ordre derrière moi: \"Toi, descends! Viens ici!\" et j'ai entendu une commotion. J'ai réussi à monter quelques marches qu'il me restait et je me suis retourné.",
          "",
          "La mère se tenait devant l’échelle, se tordant les bras, tandis que le soldat poussait mon père avec un fusil plus loin sur la route. Je ne voyais plus rien, sauf nous trois et ce soldat. J'ai figé. Ce soldat poussait mon père vers le fossé à côté de la route, puis un autre a crié à ma mère de monter dans le camion. Ma mère m'a attrapé, figé comme j'étais, et m'a emporté plus loin dans le camion. Je regardais toujours mon père. Il s'arrêta devant le fossé. Il se tourna vers nous pour ses adieux. Quand il a vu que je le surveillais, il a mis son doigt sur sa bouche comme quand il me disait «chut» et il m'a motionné avec sa main comme quand il me disait «va-t’en».",
          "Je n'ai pas entendu parler, peut-être avons-nous crié, mais je ne l'ai pas entendu. Je me souviens seulement que je ne pouvais rien bouger. Mes yeux étaient restés fixés sur mon père. Je sais que j'ai serré très fort mes dents et que je voulais exploser de l'intérieur. Des larmes me vinrent aux yeux, embrumant mon regard vers papa. J'ai seulement réussi à lever une main un peu, mais, dans ma tête, je le saluais. Il a seulement répété son mouvement \"Chut! Va-t’en\". Il n'avait pas son béret habituel sur la tête, j'ai pensé qu'il aura chaud sous ce soleil.",
          'Il portait une chemise à manches courtes de couleur crème avec de larges rayures bleu azur. Veste en cuir usé. Un pantalon noir et des chaussures usées, les seuls qu\'il ait portés à Srebrenica. Sous son bras, il portait mon manteau rouge avec une cagoule. Et il est resté comme ça en nous regardant, "Chut! Va-t’en".',
          "",
          "C'est comme si quelqu'un m'a pris, m'a sorti du camion et m'a emmené au ciel. Autour de papa, j'ai vu beaucoup d'autres gens devant le canal. Derrière eux, j'ai vu le blé mûr jouer au soleil et au vent, les gens sortaient encore et toujours du hall, certains montaient dans des bus. J'ai aussi vu maman sur le camion, me pressant contre elle, m'enroulant dans ses jupes et pleurer.",
          "",
          "Sorti de nulle part, un type a sauté dans le camion. Il s'est caché derrière quelques-uns des tonneaux qui se trouvaient là, puis les femmes l'ont couvert avec des morceaux de linge qu'elles portaient. Lorsque le camion a été rempli de personnes, ils ont fermé la bâche et l'obscurité totale s'est installée, me rendant aveugle de nouveau. Quand le camion est parti, je me suis murmuré: \"papa sera dans un autre camion …\"",
          "",
          "Le camion nous conduisait longtemps et lentement. En dessous de la bâche, on manquait d'air. Puis soudainement, nous nous sommes arrêtés. Nous étions tous silencieux. J'ai entendu la porte du camion, puis j'ai vu une latte ouvrant une partie de la bâche, et la voix qui nous engueulait: \"Pourquoi vous vous êtes enfermés? Vous allez vous étouffer, nom de Dieu!\" À travers cette partie ouverte, nous avons vu comment, de la route où nous étions ou quelque part devant nous, ils bombardaient la forêt. Je me suis souvenu de mon jeune oncle qui est parti traverser la forêt. La latte a encore parlé: \"Jetez un coup d'oeil pour ne pas vous ennuyer.\"",
          "",
          "Dans le peu de lumière autour de moi, j'ai vu une vieille femme, la tête enveloppée dans un bandage ensanglanté, et j'ai vu qu'elle était enveloppée autour des yeux avec ce bandage, et j'ai vu de nombreuses femmes. Je ne me souviens pas d'avoir vu un seul enfant. Encore une fois, la latte nous a enfermés sous la bâche et ils ont continué à nous emmener quelque part. Longtemps après, lorsque le camion s'est arrêté à nouveau, maman m'a couché fermement sur le sol, m'a recouverte de choses. D'autres femmes autour de nous ont jeté des morceaux de vêtements sur moi, puis j'ai ressenti quelque chose de plus lourd par-dessus tout ça. J'ai entendu une voix:",
          '"Y a-t-il un homme ici?" Les femmes ont répondu que non, puis il y a eu des cris.',
          "\"Personne d'autre, c'était juste lui! Il n'y a plus personne!\" Le camion est réparti peu de temps après.",
          "",
          "Quand nous nous sommes encore arrêtés, ils nous ont fait sortir du camion sur une route en dessous de laquelle se trouvaient un pré et une rivière, et au-dessus des autres prés, la maison et l'armée. Ils nous ont montés sur le bord de la route et ont dit, montez maintenant, les vôtres sont là-haut ... ... ma mère et moi sommes partis sur la route ... nous étions juste elle et moi.",
          "Des gens nous ont appris que l'endroit où nous sommes venus s'appelle Tišča. La route sur laquelle ils nous ont dirigés conduisait vers Kladanj. Nous avons marché pendant des heures et des heures, nous arrêtant parfois, exténués, pour reprendre notre souffle. Sur le chemin, des arbres étaient abattus et nous avons dû grimper par-dessus certains, et ramper sous d'autres.",
          "Au passage, les gens au bord de la route nous disaient, tout droit tout droit, jusqu'au tunnel, derrière le tunnel, ce sont les vôtres ...",
          "Le tunnel n'était nulle part en vue, derrière chaque courbe se trouvait seulement une deuxième courbe, au-dessous de la route, la rivière, au-dessus de la route la gorge et dans la fente au dessus, un peu de ciel.",
          "Le premier crépuscule avait déjà commencé quand nous avons finalement vu le tunnel. Passant dans le tunnel, un peu plus loin, nous avons rencontré ma tante Safeta, mes Niska et Niha. Quand elle m'a vu, Niska a crié. De son cri, le ravin a éclaté et j'ai gelé sur place. Je ne me souviens pas que quelqu'un m'ait étreint si fort et se soit secoué aussi fort auprès de moi que Niska. Elle a juste répété à travers les larmes \"Mon papa n'est pas là, mon papa n'est pas là\" ... j'ai pensé, alors oncle Muhamed est aussi dans un autre camion ... et puis j'ai commencé à pleurer. Pas de voix. Seules les larmes coulaient.",
          "De là, nous avons marché seulement parce que nous le devions. Je n'étais plus présent. Ce n’est que par bouts que je me souviens du cadre de la fenêtre de l’autobus qui nous conduisait à Dubrave et, dans cet encadré, du ciel rouge.",
          "Arrivés à Dubrave, il faisait déjà noir. Ma mère et moi titubions dans la mer de silhouettes inconnues. On entrait en collision avec des gens. Certains couraient vers des citernes, d'autres vers des tentes. Puis ma mère a vu un poteau avec une ampoule et nous nous sommes assis là, sur un tas de gravier. En dehors du cercle éclairé par cette ampoule, nous nous sommes tous enfoncés dans l'obscurité. Et comme s'il n'y avait plus personne au monde. Seulement maman et moi. Assis sur un tas de gravier et attendant. Ma mère m'a dit: « Ici, on va nous voir en premier ». Nous ne savions même pas qui ni s'ils vont vraiment nous voir.",
          "J'ai déjà commencé à me perdre ... Tout à coup, quelqu'un a crié nos noms: « Husnija! Jasko! »",
          "Nous ne sommes pas retournés, nous ne l'avons pas cru.",
          "Et de ces ténèbres, le visage de mon cousin Emir a soudainement surgi devant nous.",
          '"C\'est moi, Husnija! Emir! Tu ne me reconnais pas!" il parlait et pleurait.',
          "",
          "Il nous a donné de l'eau à boire. Et moi, j'étais à la fois présent et absent. Je me souviens que je me réveillais dans sa voiture et que, à côté de lui, je vis devant nous une autre femme que je ne pouvais pas reconnaître.",
          "Dans mes périples à travers le semi-éveil, j'ai remarqué qu'ils nous avaient ramenés à la maison. La femme du siège avant m'a amené vers un escalier. Lorsque je me suis habitué à la lumière des pièces en haut des escaliers, j’ai vu deux garçons dedans et j’ai reconnu que c'est ma tante Kada qui me menait par la main.",
          "Je ne sais pas comment, ni quand, tout à coup je me suis retrouvé vêtu d'autres vêtements et planté devant un repas de poulet cuit. J'étais bouche-bée, perdu, je ne savais pas quoi faire. Je regarde Kada, je regarde le poulet ... et Kada a pris un morceau de poulet, l'a mis dans mes mains et m'a dit, vas-y, poulet, mange.",
          "Ce poulet cuit et ce morceau de viande dans mes mains dont je ne savais pas quoi faire sont la dernière chose dont je me souviens de ce jour-là. Je sais que je dormais, comment et où je me suis endormi, je ne me souviens plus. Je ne pouvais plus être présent dans ce monde ce jour-là.",
          "",
          "La première fois que je suis retourné à Srebrenica c'était en 1999. L'ambassade des enfants a organisé cette visite et je me suis soudainement retrouvé devant les conseillers de la ville, avec un micro, pour répondre à des propos insultants d'une conseillère.",
          "J'ai dit, « Regarde-moi! Vous ne nous avez pas tous tués! »",
          "Je me souviens de Duško Tomić, avec qui je suis venu au bureau, et des journalistes et des caméras qui m'ont soudainement entouré. J'ai ajouté:",
          "« Je suis resté en vie après mon père, par la volonté de Dieu, pour témoigner de mon père et de ce qui nous a été fait, que cela vous plaise ou non. » Je ne sais pas où j'ai trouvé mon courage.",
          "",
          "Après cela, j'ai été poussé et on est partis de là plus vite que nous sommes venus. Je n’ai pas eu le temps de leur demander de s’arrêter un peu, pour voir s’il y avait quelque part l'un de mes livres, quelque chose de mon père, s’il ya quelqu'un dans notre maison, est-ce que mes traineaux ont été brûlés?",
          "",
          "Traduit par Igor Marincic"
        ]
      },
      Czech: {
        title: "Jasmin Jusuf Jusufović",
        subtitle: "Malý princ",
        language: "Czech",
        paragraphs: [
          "Za úsvitu toho 8. července to bouchalo někde v kopcích, ale dostatečně hlasitě, aby nás to vzbudilo. Tehdy jsem se přemístil do svého úkrytu u mrazáku, kde zeď byla nejsilnější. Těch několik dnů jsme nespočetněkrát běželi k nejsilnější zdi v domě, a ??? jsme hodnotili v běhu. Abych zkrátil čas ve svém kutlochu před mrazákem, od toho devátého července jsem počítal granáty, které bylo slyšet po celý den. Občas jich bylo i třicet najednou. Lidé vycházeli na ulici, ale pouze podle pravidla „První zabíjí“. Po ulici uháněl Ramo pekař se zraněnými v náklaďáčku.",
          "",
          "Pak to kolem poledne ztichlo. Já jsem se šel podívat co dělají Edo a Dado. A věděl jsem, že táta je někde v sousedství. Když jsem sešel po Miloševé ulici, pomyslel jsem si, že táta možná šel ke kováři přes ulici. Výbuch granátu mě zachytil uprostřed mýtiny vedle Naserovy brány, ale největší starostí pro mě bylo vyběhnout zpět za matkou, aby aspoň viděla, že jsem OK. Vletěl jsem cestou tetě do domu a vykřikl „jdu za mámou, ať si nedělá starosti“, a teta mě jen tak tak zadržela v úkrytu Edina a Dady pod stolem. Řekla, že máma určitě ví, že jsem s nimi. ",
          "",
          "Navzdory tomu jsme doufali, že život jde dál normálně. Desátého ráno jsme šli na patro, abychom s babičkou uklidili její pokoj. Vzal jsem si nějaký koberec a vyšel jsem na terasu, abych ho vyklepal. Granát spadl za barák. Země padala na střechu jako déšť a já deset sekund visel na zdi. V celém tom zmatku pod granátama, když se to kolem poledne ztišilo, se táta s mámou rozhodli, že půjdeme k tetě, aby mi vyhnala strach (tradiční bosenský zvyk slévání olova do vody k vyhnání strachu), jelikož jsem od toho granátu už nepromluvil. Mysleli si, že se mi něco stalo. Tam jsem se nakláněl nad miskou s vodou a olovem a říkal tetě „kdepak vidíš lva“.         ",
          "",
          "Teta žila v Kazanech, a my ve městě, což na chůzi bylo daleko kvůli nebezpečí z granátů. Dodnes mi není jasné, jak se jim, bez telefonu a jakéhokoliv připojení, podařilo dohodnout, že se sejdeme na půl cesty v domě mé tety u autobusového nádraží. Když jsme dokončili vyhnání, já jsem necítil o nic menší strach. Pak zase začaly padat granáty. Edo a Dado byli u nás, Edo se mnou u mrazáku, a Dado u další silnější stěny. Někdo zakřičel, že to padá na Staré město. Začínám si dělat starosti o svého kamaráda Kema.     ",
          "",
          "Toho 11. července hned od rána začala střelba, blíž než kdykoliv předtím. Bylo horko. Máma a táta do tašek balili věci, asi když už půjdeme, tak ať k těm z Drinjači nejdeme s prázdnýma rukama. Taky jsem skočil a do svého baťůžku hodil Malého prince a 1000 proč/proto. Ostřelování nepřestalo celé ráno. Po ulici se čím dál víc lidí začalo pohybovat směrem z města, a běželi. My jsme na něco jako by bláznivě čekali a pak někdo z davu zakřičel: Četnici sešli na trh! Popadli jsme batohy, otec popadl i mě jako batoh, takže jsem přeletěl z kopce, na kterém byl náš dům do toho davu lidí, který utíkal. Za námi byly slyšet kulomety a střelba.      ",
          "",
          "Jak jsem běžel dolu po ulici, o plot, který už dávno zničil granát, co zabil děti hrající fotbalový turnaj před naší školou, jsem si roztrhl rukáv na pravé ruce. Možná jsem si roztrhl i ruku, ale tehdy jsem to necítil. Jen jsem viděl, jak mě matka strhla dál od plotu a pak jsme vběhli do domu tety Munevery na autobusové zastávce. Kvůli něčemu jsme se zastavili. Táta odběhl zpět do města, a mě matka sundala můj batoh, vysypala z něj knihy, podívala se na mě a já jen pokrčil rameny. Tak zůstal můj Malý princ. Když mě viděla strnulého nad knížkami, matka mě utěšovala, že knížky by mi na zádech byly moc těžké, dokud bychom utíkali a běželi. Já mlčel, myslel si, že možná ani ne, ale viděl jsem, že už nic víc nemá smyls.          ",
          "",
          "Později do domu vešel i táta, s pytlem bílé mouky na zádech. Říkal, že lidi vyloupili Naserův sklad. Z toho pytle teta Munevera všem udělala ušitpky (tradiční pokrm z těsta). Já už zapomněl, že jsem do těch uštipků už několik dnů nic nejedl a zapomněl jsem i že jsem měl hlad. Snědl jsem sotva jeden. Brzy jsme se rozdělili. Táta mě oklamal, že půjde něco přinést, ale doopravdy šel přes les. My jsme se sešli s tetou Senou a šli jsme dál. Kolem nemocnice se vytvořil dav. Viděl jsem lidi, jak skáčou přes balkóny v budovách v okolí. My jsme vešli do jedné z nich.   ",
          "",
          "Nezůstali jsme dlouho ve tmě té budovy, vyrazili jsme dál, ale dál, než k nemocnici to nešlo. Lidé se pokoušeli vstoupit do kempu OSN ve Vezionici a někdo říkal, že Zulfo Tursun od nemocnice vrací kluky zpět. Tak jsme se ztratili i s tetou. Matka, babička a já jsme se zastavili u jedné z budov u nemocnice, nad Vezionici. Někde v blízkosti spadl granát. Nejdřív na mě skočila babička, pak matka, a na nás sesypalo nějaké sklo, které na této budově ještě přežilo. Vzpomínám si, že jsem pod babičkou matkou sotva dýchal.    ",
          "",
          "Po tom granátu se ženy ještě více nahrnuly na bránu a plot Vezionice. I my tři jsme sešli do davu na silnici. V hluku jsem rozpoznal hlas: Husnija!!! Táta se vrátil z lesa. „Půjdu s vámi, ať se děje cokoliv.“ Někde, v nějaké části, byla protržena brána Vezionici. Táta mě nějak dostal do okruhu kempu a odešli jsme do garáží vozidel a transportérů OSN. Pak spadl ještě jeden granát. Šrapnely do plechu garáže prorazily díry, skrz které se světlo v páscích začalo protlačovat dovnitř. Ztratili jsme matku. Ztratili jsme babičku. Nebyla tam ani teta, ani Edo, ani Dada, ani Keka, ani strejda Kiko… byly jsme jen my dva. Táta viděl, jak se OSNovci pod Vezionicí dostávají pryč v kamionech, popadl mě, doběhl k jednomu z nich a hodil mě na karoserii.         ",
          "",
          "Pomohl ještě i mnoha dalším, aby se vyšplhali na kamion, a veškerá ta masa mě přitiskla k straně kamionu. Podařilo se mi jen otočit hlavu směrem ven, abych mohl dýchat. V té hromadě lidí jsem akorát cítil, že jsem si s tátou, v tom davu a tlačenici, pustili ruce. Lidé se cestou zavěšovali za kamion a mě docházel vzduch, tam kde jsem ho měl málo. Kamion pomalu dojel na druhou základnu, do továrny Akumulatorka. Skrz dav lidí a vojáků OSN jsme s tátou skončili ve tmě haly. Cestou jsme viděli strejdu Ibra, na nosítkách. 	 ",
          "",
          "Lidí v továrně bylo čím dál víc. Táta s někým vybojoval, aby nám našel jedna nosítka, aby mě na ně posadil. Sedl jsem si vedle nějaké zdi. Vedle proraženého vchodu. Nosítka byla mokrá. Od krve. Seděli jsme na nosítkách a já jsem upřel zrak na ty dveře, kterými jsme přišli v naději, že se v nich odněkud objeví matka. Čas ztratil jakýkoliv kontext. Najednou do dveří vešla i matka, s rudými tvářemi, ubrečená, jako by s někým právě teď zápasila. Hned nás spatřila. Říkala, že ji vojáci UNPROFORu nechtěli pustit do továrny, tak se s jedním vojákem hádala a bojovala, a nakonec nějak proklouzla dovnitř. Táta později odešel sehnat nám nějakou vodu. Přinesl nějakou láhev, která smrděla po benzínu a v ní trochu vody. Řekl, že našel tetu, Eda a Dadu a strejdu Kika, ale babička nikde nebyla. Matka dodala, že babička zůstala v jiné továrně s naší těhotnou Kekou.    ",
          "",
          "Už jsem nemohl poslouchat hluk, křiky, dívat se do zakrvácených obličejů a šedi tovární haly. Našel jsem nějakou díru za zády a tam položil hlavu a nějak, v tom všem, jako bych usnul. Probral jsem se pár minut potom, matka se ptá, proč nespím, chci k tetě, půjdeme později. Tak chci na záchod. Odvedla mě skrz vedlejší vchod za továrnu, cestou jsem zase viděl zakrvácené a strhané lidi, hodně výkalů a krvavých hadrových obvazů. Řekl jsem, že nemůžu na záchod, a už jsem ani nešel.   ",
          "",
          "V jednu chvíli matka začala panicky přehrabovat naše tašky. Vzala hrst nějakých příborů a odběhla to vyhodit. V továrně si lidé vyprávěli, že četnici hlídkují po továrně oblečení do UN uniforem a jen hledají důvod proč někoho vzít ven a zastřelit.   ",
          "",
          "K čemu to ženo, proboha, potřebuješ? ptal se jí táta. ",
          "",
          "Jak mám vědět kam půjdeme potom a jak dopadneme. Myslela jsem, že to možná budeme potřebovat.",
          "Šperky už dávno neměla a tehdy vzala a vyhodila všechno kovové, co jsme měli.",
          "",
          "Viděla, že už mě nemůže odmítnout, tak jsme, nějak až lidé přestali vcházet, odešli hledat tetu a strejdu Kika. Strejda Kiko byl strašně bledý. Byl osobou se zvláštními potřebami, ale nikdy mi nepřipadal tak vystrašeny jako tehdy. Matka se zeptala tety, kde má Senaida, mého mladého strejdu, a odpověděla, že šel přes les. Já přišel k Edovi a Dadě, posadil jsem u nich na nějakou deku rozbalenou na betonu. Bylo to tvrdé a studené.    ",
          "",
          "Tolik tváří, které jsme nikdy předtím neviděli teď bylo kolem nás v hale. Nikdy jsme je neviděli a jako bychom je znali odjakživa. Halou jen procházely vlny paniky. K uším přicházely zprávy, že vyvádějí z haly a podřezávají, a znásilňují, a střílí. Máma s tátou rozprávěli s lidmi kolem nás, že vše brzy skončí, že bychom měli být OK tady s UNPROFORem, ale nám, až se stmívalo, začalo být jasné, že jsme v těch továrnách vlastně v táboře.    ",
          "",
          "Nějak jsme to přes noc zvládli… já jsem zase hlavu dal do té své díry ve zdi, matka se tělem opřela ke mně jako by mě přitiskla, abych se cítil bezpečněji, vše co jsem slyšel celou noc byly vzdechy, dětský pláč a někde v dáli za továrnou, skrz ten vchod, výkřiky. Nevím, jestli jsem snil, nebo prostě byl nepřítomen duchem, ale celou noc jsem před očima měl Drinjači, teplé mléko z Pahljevićů, maliny z Burnice, západ slunce v Konjević Polje… vše se mi navzájem míchalo. Zatím co mi tábor ryl svoji vůni a tmu do paměti.          ",
          "",
          "Zmatek a hluk začaly velmi brzo ráno. Nedostaly se k nám žádné ohlasy, o čem je řeč, ale lidé po hale pobíhaly sem tam. Táta vyskočil a odešel skrz ty dveře kterými jsme vešli, jdu se podívat co se děje. Táta později přišel s nějakým plechovým talířem, ve kterém přinesl něco málo polévky a krajíc průhledného chleba. Polévka vypadala jako špinavá voda, a talíř byl nízký, příbory jsme vyhodili, a nemohli jsme ji ani vypít. Chtěli jsme polévku nabrat chlebem, ale v ní se pouze rozpadl. ",
          "",
          "Zatím co jsem přemýšlel, jak sním to málo polévky, táta mámě vyprávěl, jak byl u strejdy Ibra a jak on ještě nemůže vstát z těch nosítek, na kterých jsme ho včera viděli. Říkal, že je hodně bledý a že je „vyschlý“. „Nenajde se ani nikdo, aby mu dal infuzi.“ Poprosil jsem mámu a tátu, ať mě nechají se trochu projít. Z pravé strany jsem v dáli viděl, že chyběla zeď a přicházelo tudy světlo. Když jsem přišel k tomu otvoru, viděl jsem pole plné lidí, podobných těm, co byli uvnitř v hale, jak se smaží na mýtině a slunci.   ",
          "",
          "Když jsem se vrátil, nepodařilo se mi vyprávět co jsem viděl. Matka mi přikázala, abych se posadil a pak si vedle mě dřepnul nějaký člověk. Ptal se na jména, tátovo a moje. „Z UNPROFORu nám říkali, ať zapisujeme jen muže“ říkal. Táta říkal, že já jsem Jasmin Jusufův Jusufović a on to zapsal. Jak jen jsem byl pyšný, když táta takto vyslovil moje jméno. Myslel jsem si, ano, já jsem syn svého otce, a nikdo mi nic nemůže. Když zapisovač odešel, odněkud zleva nějaký člověk zakřičel: „Jaký UNPROFOR… četnici!“  ",
          "A proč, táto, ten pán zapsal jen moje a tvoje jméno? ",
          "",
          "Nevím. Možná aby věděl, kolik lidi tady je…",
          "",
          "A proč pak nezapsal mámino? Zapíše i Nicu? Zapsal Keku a babičku? Kdo zapíše Kiku?",
          "",
          "Nevím, dítě drahý, nevím.",
          "",
          "Máma a táta se bavili s lidmi okolo nás. K nám, do našeho kouta, se dostala i zpráva, že mezi nás pustili četniky, a že oni vyvádí lidi do nějakého bílého domu vedle továrny, aby znásilňovali a stříleli. Proto mi matka už nedovolila, abych šel sám tam k té louce a na vzduch.     ",
          "",
          "Mami, pojď prosím tě, půjdeme tam na vzduch",
          "",
          "Nechci, posaď se.",
          "",
          "Prosím tě, mami, jen abych na chvíli viděl slunce.",
          "",
          "Nechci, posaď se!",
          "",
          "Ale, mami, pojďme tedy tam za Nicou, ať vidíme Eda a Dadu, pojďme spolu.  ",
          "",
          "Když jsme přišli…",
          "",
          "Tak pojď mami, půjdu jen na chvíli tam do koutu, uvidíš mě od Nici. ",
          "",
          "Každých půl minuty jsem se otáčel za matkou, abych jí mávnul a potvrdil, že se vidíme. Pak jsem, z konce plotu, slyšel hluk a výkřiky a rozpoznal jsem co křičel jeden ženský hlas. Běžel jsem za matkou rychlostí, kterou jsem si myslel, že přetočím čas.   ",
          "",
          "Co se stalo, jaké jsou to výkřiky?",
          "",
          "Nevím, křičí nějaká žena „Zašlápl jí dítě.“",
          "",
          "Kdo komu zašlápl dítě?",
          "",
          "Nevím mami, jen jsem slyšel ženu jak křičí „Zašlápl jí dítě.“",
          "",
          "Najednou mezi všemi těmi lidmi zavládlo hrobové ticho. Všichni zmlkli, na chvíli nebylo slyšet ani šeptání. ",
          "Pak se od ucha k uchu rozšířila zpráva, že četnik ženě, která vedle plotu teprve porodila, vzal dítě a zašlápl ho. ",
          "",
          "Už jsem se neodhodlal ani zmínit, že někam chci. Už jsem se neodhodlal ani požádat, ať jdeme za tetou… jen jsem hledal tu svoji díru ve zdi, abych schoval hlavu a přizval Drinjači. 	",
          "",
          "Když spadla noc, vyslovil jsem, víc jen tak, jelikož jsem myslel, že jsem sám a sobě do brady, kdy už odsud odejdeme? ",
          "",
          "Táta mě slyšel, myslel si, že jsem se ptal jeho a řekl:",
          "",
          "- Zítra odejdeme. Teď si pojď lehnout! ",
          "",
          "Panika v hale 13. července mě vzbudila. Půlka haly vstala, matka kolem mě zase shromažďovala to málo našich věcí a mě do klína vložila můj baťůžek, ve kterém zůstala jen ta láhev, co smrděla po benzínu. ",
          "",
          "Jdeme pryč? zeptal jsem se. Nikdo mě neslyšel. ",
          "",
          "Mumlání, které jsem předchozí dny mohl ignorovat jako neustálý šum, to ráno bylo hlasitější. Lidé přicházeli s novými zprávami, nějakou ženu se kousek vlevo snažili probrat, jednoho dědečka se snažili postavit na nohy, jedna matka se snažila ztišit dítě, které křičelo.  ",
          "",
          "Říkal jsem si: Já takový nebudu. Já jsem hodný, já nebudu brečet a křičet, aby si máma s tátou nedělali starosti. Všichni byli připravení, že někam půjdeme, ale nikam jsme se nehýbali. Jen lidé, kteří vstali, se v místě kymáceli a otáčeli. Po chvilce se, v hloubi haly, na stejné straně, na které jsme vešli, otevřely velké dveře, a lidé, jako, řeka začali chodit všichni na ty jedny dveře. My jsme nešli rovnou k dveřím, ale dav nás pronesl po středu haly, k místu kde seděla teta, ale oni tam nebyli.   ",
          "",
          "Najednou jsme se zastavili vedle nějakého zeleného kamionu, ale lidé se stále tiskli přede dveřmi. Zatáhl jsem otce za ruku a pokusil se mu říci:",
          "",
          "„Tati, nemůžu dýchat!“ on mě vzal a dal na ramena. Byl jsem v rovině s kamionem. I na kamionu byli lidé. Když jsme konečně vyjeli, až jsme se dostali ven, mě slunce a teplo oslepily. Chytil jsem se za oči. Otec si všimnul, že už se ho na ramenou nedržím, tam mě sundal mezi sebe a matku a chytili mě za ruce. Ohnul jsem hlavu k nohám, aby se mi oči zvykli na světlo.     ",
          "",
          "Šli jsem skrz dlouhý kordonem vojáků. Viděl jsem pouze opravdové boty a nohy ve vojenském oblečení a vršky pušek. Hnali nás, křičeli, museli jsme začít běhat. Nezvykl jsem si na slunce až do doby, co jsme vyjeli na cestu. Před námi se zleva roztáhla kolona kamionů, autobusů a vojáků. Otec mě a matku strkal před sebe, matka mě držela pevně u sebe a motala mě do svých kalhot. Bylo složité takto běhat. Míjeli jsme jeden, pak druhý autobus a kamion, dokud někdo nezakřičel DOVNITŘ! a já se nacházel před dřevěným žebříkem opřeným o kamion.  ",
          "",
          "Myslel jsem si, že čím dřív vyběhnu po žebříku, tím dřív se my tři schováme na kamionu a tohle všechno skončí. V půlce žebříku jsem za sebou slyšel rozkaz: „Ty, dolu! Pojď sem!“ i slyšel jsem nějaký rozruch. Vyšplhal jsem ještě těch pár schodů, co mi zbývalo a otočil jsem se.   ",
          "",
          "Matka stála před žebříkem, se zkříženýma rukama, a po cestě voják puškou odstrkoval mého tátu. Neviděl jsem nic jiného, než nás tři a toho vojáka. Ztuhl jsem. Tátu ten voják hnal k příkopu u cesty, a druhý pak na matku křičel ať šplhá nahoru. Matka mě ztuhlého popadla a přenesla hlouběji do kamionu. Já se stále díval na tátu. Stoupnul si do příkopu. Otočil se k nám, aby nás vyprovodil. Když si všimnul, že se na něj dívám, dal si prst přes na ústa, jako by říkal „pšššt“ a mávl rukou, jako když říkal „jdi.“   ",
          "",
          "Neslyšel jsem, možná jsme i křičeli, ale já to neslyšel. Vzpomínám si pouze, že jsem se nijak nemohl hnout. Oči jsem nechal na otci. Vím, že jsem silně zatnul zuby, a vevnitř jsem chtěl prasknout. Z toho mi začaly téct slzy, které mi zamlžovaly pohled na tátu. Podařilo se mi jen trochu zvednout jednu ruku, v hlavě jsem si představoval, že mu mávám. On jen zopakoval ten svůj pohyb „pšššt! Jdi.“ Neměl na hlavě svůj typický baret, tak jsem pomyslel, že mu na tomhle slunci bude vedro.    ",
          "",
          "Měl na sobě tričko s krátkým rukávem, krémové barvy, s širokými azurovými pruhy. Opotřebenou koženou vestu. Černé kalhoty a opotřebené boty, jediné, které měl v Srebrenici. Pod paží nesl mojí červenou bundu s kapucí. A tak zůstal, díval se na nás, „pšššt! jdi.“",
          "",
          "Mě pak jako by někdo vzal, vynesl mě z kamionu a odnesl do nebes. Viděl jsem, že v příkopu, kolem mého otce, stojí ještě hodně lidí. Za nimi jsem viděl zralou pšenici, jak poletuje na slunci a větru, z haly lidé stále ještě vycházeli, někteří lezli do autobusů. Viděl jsem i mámu na kamionu, jak mě k sobě tiskne, zamotává mě do kalhot a brečí.   ",
          "",
          "Odněkud nějaký kluk skočil do kamionu. Schoval se za nějaké barely, které tam byly, a pak na něj ženy naházely věci, které sebou měly. Když se kamion naplnil lidmi, zavřeli nás plachtou a nastala úplná tma, ze které jsem znovu oslepl. Až se kamion rozjel, zašeptal jsem sám sobě „táta pojede v jiném kamionu…“  ",
          "",
          "Kamion nás dlouho a pomalu vezl někam. Pod plachtou nám docházel vzduch. Najednou jsme se zastavili. Všichni jsme zmlkli. Slyšel jsem dveře kamionu, a pak jsem viděl dlouhého maníka, jak otevírá jednu část plachty a vykřikuje: Proč jste se zavřeli, udusíte se, sakra. Skrz tu otevřenou část jsme viděli jak z cesty, na které jsme, někde před námi, ostřelují les. Vzpomněl jsem si na svého mladého strejdu, který šel skrz les. Ten maník zase promluvil: Dívejte se trochu, ať se nenudíte. ",
          "",
          "V tom kousku světla kolem sebe jsem viděl babičku, s hlavou zabalenou do nějakého krvavého obvazu, a viděl jsem, že tím obvazem má zabalené i oči, a hodně dalších žen. Nevzpomínám si, že bych viděl nějaké dítě. Ten maník nás zase zavřel pod plachtu a pokračovali nás vézt dál. Dlouho poté se kamion zase zastavil, a mě matka položila na podlahu a pokryla mě věcmi. Některé další ženy kolem nás na mě hodily další věci a pak jsem nad to vše cítil i něco těžkého. Později jsem zaslechl hlas.      ",
          "",
          "Jsou zde nějací muži? Ženy odpovídaly, že nejsou, a pak začal výkřiky.",
          "",
          "„Není už nikdo, byl jenom on! Není už nikdo!“",
          "",
          "Kamion se po chvilce zase rozjel. ",
          "",
          "Když jsme znovu zastavili, vyložili nás z kamionu na nějaké cestě, pod kterou byla louka a řeka, a nad ní další louka a dům, a vojáci. Ukazovali směrem po cestě a říkali, teď jděte nahoru, nahoře jsou vaši… matka a já jsme se vydai cestou… byli jsme sami dva.      ",
          "",
          "Od lidí jsme zaslechli, že se místo, kam jsme přijeli jmenuje Tišča. Cesta, kterou nás poslali vedla směrem na Kladanj. Chodili jsme hodiny a hodiny a občas se zastavovali, abychom, vyčerpaní, trochu popadli dech. Po cestě byly popadané stromy, a tak jsme přes ně museli šplhat, nebo pod některými lézt.     ",
          "",
          "Cestou nám z krajnic silnice hovořili, že jen rovně, rovně k tunelu, za tunelem jsou vaši…",
          "",
          "Tunel nebyl nikde na obzoru, za každou zatáčkou přicházela jen další zatáčka, pod cestou řeka, nad cestou rokle, a v mezeře nahoře kousek nebe. ",
          "",
          "Už se začalo stmívat a konečně jsme spatřili tunel. Když jsme prošli tunelem, kousek opodál jsme narazili na mají tetu Safetu, mojí Nisku a Niha. Když mě spatřila, Niska vykřikla. Z jejího křiku se rokle zatřásla a já ztuhnul. Nevzpomínám si, že by mě někdo někdy tak silně objal a že se vedle mě tak třásl jako Niska. Jen, skrz pláč, opakovala „nikde není táta, nikde není můj táta…“ Pomyslel jsem si, že strejda Muhamed tedy asi taky bude v jiném kamionu… a pak jsem propukl v pláč. Bez zvuku. Jen tekly slzy.  ",
          "",
          "Odtud jsme šli dál jen protože jsme museli. Nebyl jsem při smyslech. Jen mlhavě si vybavuji rám okna na autobuse, který nás vezl směrem na Dubravy, a červené oblohy v rámu toho okna. ",
          "",
          "Když jsme dorazili do Dubrav už byla tma. Máma a já jsme se potulovali v moři neznámých siluet. Naráželi jsme do lidí. Někteří běhaly směrem k cisternám, někteří k stanům. Pak matka zahlédla nějaký sloup osvětlení a tam jsme se posadili, na hromadu štěrku. Mimo kruh, který osvětlovala ta žárovka, najednou se všichni ponořili do tmy. I jako by už na světě neexistoval nikdo další. Jen matka a já. Sedíme na hromadě štěrku a čekáme. Matka mi říkala: Tady nás uvidí nejdřív. Nevěděli jsme kdo, ani zda někdo vůbec.      ",
          "",
          "Já už jsem se začal ztrácet… Najednou někdo odněkud vykřikl naše jména: Husnija! Jasko! ",
          "",
          "Nevnímali jsme to, nevěřili jsme.",
          "",
          "A z té tmy se před námi najednou vynořila tvář mého bratrance Emira. ",
          "",
          "„Jsem to já, Husnija! Emir! Copak mě nepoznáváte!“ říkal a brečel.",
          "",
          "Dal nám vodu, ať se napijeme. A já chvíli byl přítomen, chvíli ne. Vzpomínám si, že jsem se budil v jeho autě, a že jsem vedle něj, před námi, viděl ještě jednu ženu, kterou jsem nepoznával. ",
          "",
          "Během toho mého blouzení mezi snem a bdělosti, jsem si všimnul i že nás přivezli před dům. Žena ze sedadla spolujezdce mě vzala nahoru po nějakých schodech. Když mě probralo světlo z místnosti na konci schodů, viděl jsem vevnitř dva kluky a poznal, že mě za ruku vede moje sestřenice Kada.       ",
          "",
          "Nevím jak, ani kdy, ale najednou jsem byl převlečen do jiného oblečení a seděl před stolem, na kterém bylo pečené kuře. Otevřel jsem pusu, ztrácel se, nevěděl jsem, co mám dělat. Dívám se na Kadu, pak na to kuře… a Kada to kuře vzala, ulomila z něj kousek, dala mi ho do ruky a povídá – ber, kuřátko moje, a jez.  ",
          "To pečené kuře, a ten kus masa v mých rukou, s kterým jsem nevěděl, co mám dělat, jsou moje poslední vzpomínky z toho dne. Vím, že jsem spal, ale jak, kde a kdy jsem usnul si už nepamatuji. Už jsem ten den nemohl být přítomen na tomto světe.  ",
          "",
          "Poprvé jsem se vrátil do Srebrenici v roce 99. Dětská Ambasáda zorganizovala tu návštěvu a já se najednou ocitl před radními na radnici, u mikrofonu, že mám jako něco říci po urážlivém selhání jedné z radních.  ",
          "",
          "Řekl jsem: podívejte se na mě! Všechny jste nás nezabili!",
          "",
          "Vzpomínám si, že se Duško Tomić, s kterým jsem přišel, zarazil, a novináři a kamery se najednou všechny soustředili na mě. Dodal jsem:",
          "",
          "- Zůstal jsem naživu po svém otci, z boží vůle, abych podal svědectví o svém otci a co nám bylo učiněno, ať se vám to líbí nebo ne. ",
          "",
          "Nevím, kde jsem vzal odvahu. ",
          "",
          "Potom mě popadli a odešli jsme odtud rychleji, než jsme přišli. Neměl jsem čas je poprosit, abychom se na chvíli zastavili, abych se podíval jestli tam někde je nějaká moje knížka, jestli je tam něco mého táty, jestli je někdo v tom našem domě, jestli mi spálili saně.  ",
          "",
          "Translated by: Amila Hrustić-Batovanja & Mladen Grebo"
        ]
      },
      Turkish: {
        title: "Jasmin Jusuf Jusufović",
        subtitle: "Küçük Prens",
        language: "Turkish",
        paragraphs: [
          "8 Temmuz, şafak vaktinde, ormanda bizi uyandıracak kadar gürleme vardı. O, duvarın en kalın olduğu yerde, buzluğun yanındaki sığınağıma geçtiğim zamandı. O birkaç gün içinde evin en kalın duvarına çok kez koştuk, duvarın kalınlığını da koşarak değerlendiriyorduk. 9 Temmuzdan itibaren buzluğun yanındaki yuvamda vakti kısaltmak için tüm gün art arda patlayan bombaları saydım. Bazen aynı anda otuzar tane patlardı. İnsanlar sadece “Birincisi öldürür” kuralı ile sokaklara çıkıyordu. Ramo Hljebara (Ekmekçi Ramo) kamyoneti ile yaralıları sokaklarda alel acele taşıyordu.",
          "Mermiler durdu ve hepsi öğle saatlerinde sessizleşti. Ben Edo ve Dado ne yapıyor diye bakmaya gittim. Babamın mahallede bir yerde olduğu biliyordum. Miloševa (Miloşeva) caddesinden indiğimde babamın nehrin öbür tarafında bulunan demircinin yanına gitmiş olabileceğini düşündüm. Bomba tam vadinin ortasında, Naser’in barajı yanında olduğumda patladı, ama en büyük endişem tekrar annem iyi olduğumu görmesi için koşa koşa evime dönmek idi. Eve doğru giderken yengemin evine uğradım ve “annemin yanına gidiyorum, merak etmesin”. Yengem zorlukla beni Edin ve Dadin'le masanın altındaki barınakta tuttu, annemin kesinlikle onların yerinde olduğumu bildiğini temin ederek. ",
          "Her şeyin yanı sıra, hayatımızı normal bir şekilde sürdürecek kadar inatçıydık. Sabah ayın onunda büyükannemin odasını temizlemek için üst kata çıktık. Bir kilim aldım ve balkondan kilimi sallamaya çıktım. Bomba tam evin üzerinde düşüp patladı. Toprak evin çatısına yağmur gibi yağdı, ben de on saniye falan duvardan asılıyordum. Bu karmaşada, bombalama öğlen sakinleştiğinde babam ve annem yengeme, “kurşun dökmeye” (Boşnakların bir ritüeli, kurşun ısıtıp eritilir ve suya dökülür, bu da korkuları giderir) gitmeye karar verdiler, çünkü o bombadan sonra bir kelime konuşmadım. Bana bir şey olduğu düşündüler. Orada kurşunlu su ile dolu kaseye bakıp yengeme “aslanı nerede görüyorsun be?” söylüyordum. ",
          "Yengem Kazani’de, biz ise çarşıda oturuyorduk, bombalama tehlikesi altında uzak bir yürüme mesafesi idi. Telefon veya başka bir iletişim aracı olmadan orta yolda, otogarın yanında bulunan yengemin evinde buluşmamızı nasıl ayarladılar hala anlamış değilim. Kurşun dökmeyi töreni bittiğinde ben hiç daha az korkmuş hissetmedim. Bombalama tekrar başladı. Edo ve Dado bizde idi, Edo benimle buzdolabın yanında, Dado diğer kalın duvarın yanında. Biri bombaların Eski şehrin her tarafını vurduğunu söyledi. Ben arkadaşım, Kemo için endişe etmeye başladım.",
          "O 11 Temmuz, atışlar sabah erken başladı, hem de önceye göre çok daha yakın. Hava sıcaktı. Annem ve babam eşyaları kumaş torbalara paketliyordu, muhtemelen, gideceksek Drinjača’dan ellerimiz boş gittiğimiz gibi gitmeyelim diye. Ben zıpladım ve küçük sırt çantama “Küçük Prens” ve “Bin niçin, bin çünkü” ansiklopedisini koydum. Bombalama tüm sabah durmadı. Gittikçe daha fazla insan şehirden çıkmaya başladı, koşarak. Biz deli gibi bir şey bekliyorduk ve sonuçta o insan yığınından biri bağırdı: “Çetnikler pazara indi!” O sırt çantalarını kaptık, babam beni de bir çanta gibi kaptı, o şekilde evimizin bulunduğu tepeden o kaçmaya çalışan millet yığınına indik. Arkamızda atış ve patlamaları duyuyorduk.",
          "Sokakta koşarken, futbol turnuvası oynayan çocukları öldüren bomba tarafından mahvedilen korkulukta tişörtümün sağ kolunu yırttım. Belki de kolumu da yırttım, ama o anda bunu hissetmedim. Gördüğüm tek şey annemin o korkuluktan beni ayırdığı ve ondan sonra otogarın yanında bulunan Munevera yengemin evine koşarak gitmemizdi. Bir şeyden dolayı durduk. Babam tekrar çarşıya doğru koştu, annem de sırtımdaki çantamı aldı, içindeki kitapları çıkarttı, bana baktı, ben de omuz silktim. Küçük Prensim bu şekilde geride kaldı. Beni kitaplardan dolayı üzgün görünce annem kaçacağımız ve koşacağımız için o kitapların sırtımda ağır olacağı anlatmaya çalıştı… Ben sustum, belki de ağır olmazdı diye düşündüm, ama her şeyin artık anlamsız ve faydasız olduğu anladım.",
          "Sonra babam eve girdi, sırtında beyaz un torbası vardı. Millet Naser’in ambarını soydu dedi. Munevera yengem hepimize çörek yaptı. O çöreklere kadar günlerce hiçbir şey yemediğimi hatta açlığı da unuttum. Zar zor bir tane yedim. Kısa süre sonra ayrıldık. Babam bir şey getirecek diye bizi kandırdı ve aslında ormanın içinden Tuzla’ya gitti. Biz dayımın hanımı Sena ile buluştuk ve devam ettik. Hastanenin etrafında kalabalık oldu. Etrafındaki binalarda balkondan balkona atlayan insanları gördüm. Biz de o binalardan birine atladık.",
          "O binanın karanlığında çok uzun süre kalmadık, devam ettik, ama hastaneden daha fazla ilerleyemedik. Millet Vezionica’daki BM (Birleşmiş Milletler) kampına girmeye çalışıyordu, biri de Zulfo Tursun’un oğlanları hastaneden geriye döndürdüğünü söyledi. Böylece dayımın hanımını kaybettik. Annem, nenem ve ben hastanenin yanındaki ve Vezionica’nın üzerindeki binaların birinin yanında durduk. Yakınlarda bir bomba patladı. Önce nenem kendini üstüme attı, sonra da annem ondan sonra da o binada hala kalan cam üzerimize düştü. Nenem ve annem altında çok zor nefes alıyordum, bunu hatırlıyorum.",
          "Bombadan sonra kadınlar Vezionica’nın kapısını ve çitini daha fazla itmeye başladılar. Üçümüz de o caddedeki yığına indik. Gürültünün içinde bir ses tanıdım: Husnija!!! Babam ormandan geri döndü. “Ne olursa olsun, ben sizinle gideceğim”. Vezionica’nın çitinin bir kısmı kırıldı. Babam beni bir şekilde kampın içine taşıdı, BM araçlarının garajına gittik. O anda bir bomba daha patladı. Bombanın şarapnelleri garajın sacında küçücük delikler açtı, bu delikler sayesinde garajın içine şerit biçiminde ışık girdi. Annemi kaybettik. Nenemi kaybettik. Dayının hanımı da yoktu, Edo da, Dado da, Keko da, Kiko da yoktu… Sadece ikimiz vardık. Babam BM askerlerinin Vezionica’dan kamyonlar içinde kaçtığını gördü, beni kaptı ve koşarak bir kamyonuna yanına gelip beni kamyonun gövdesine attı. ",
          "Birçok kişinin daha kamyona binmesine yardım etti ve bu kalabalık beni kamyonun kenar duvarlarına sıkıştırdı. Sadece nefes alabilecek kadar kafamı dışarıya doğru çevirmeyi başardım. İnsan yığını içinde, kamyondaki kalabalık ve baskıdan dolayı babam ve benim ellerimizi tutmaya devam etmediğimizi fark ettim. İnsanlar yolda kamyona takılıyordu ve daha önce az da olsa olan havayı da kaybetmeye başladım. Kamyon yavaş yavaş diğer kampa geldi, Akumulatorka fabrikasına. Babam ve ben BM asker ve insan koridorundan geçip bir fabrika binasının karanlığında bulunduk. Yolda eniştem İbro’yu gördük, sedyede yatıyordu. ",
          "İnsanlar fabrikaya dökülmeye başladı. Babo bir şekilde bir sedye bulmayı başardı, böylece bir süreliğine oturabilecektim. Bir duvarın yanında oturdum. Kırılan girişin yanında. Sedye ıslaktı. Kandan. Sedyede oturuyorduk, ben de o girdiğimiz kapıya gözlerimi diktim ki annem o kapıdan bir yerden girsin. Vakit bütün bağlamını kaybetti. Aniden o kapıdan annem girdi, ağlamaklı ve kızarmış, sanki bir an önce biriyle güreş yapmış gibi. Bizi hemen gördü. UNPROFOR askerlerinin onun fabrikanın içine girmesine izin vermediğini, bu yüzden bir askerle tartışmak ve kavga etmek zorunda kaldığını söyledi. Bir şekilde içeriye girmeye başardı. Babam sonra su bulmaya gitti. İçinde biraz su olan, petrol kokulu bir şişe getirdi. Dayının hanımını, Edo’yu, Dado’yu ve Kiko dayıyı bulduğu söyledi, ama nene yoktu. Annem nenemin hamile olan Keka ile diğer fabrikada kaldığını söyledi.",
          "Gürültüyü, çığlıkları artık dinleyemedim, kanlı yüzleri ve fabrikanın renksizliğini artık izleyemedim. Arkamdaki duvarda bir delik buldum, oraya kafamı soktum ve sanki uyudum. Birkaç dakika sonra uyandım, annem neden uyumadığımı sordu, dayımın hanımının yanına gitmek istiyorum, sonra gideriz. O zaman tuvalete gitmek istiyorum. Benimle beraber yanımızdaki fabrikanın arkasına giden çıkıştan çıktı. Orada kanlı ve parçalanmış insanlar vardı. Her yerde kanla ıslanmış birçok dışkı ve bandaj vardı.. Tuvalete gidemiyorum dedim ve artık hiç gitmedim.",
          "Aniden annem panik içinde torbalarımızda bir şey aramaya başladı. Bir çatal bıçak takımını çıkardı ve atmaya gitti. Fabrika içinde, çetniklerin fabrikayı BM askerleri gibi giyinmiş şekilde devriye gezdikleri ve insanları çıkarmak ve katletmek için bahaneler aradıkları şeklinde bir söz yayıldı.",
          "Babam ona Allah aşkına, bu gümüş eşyanın neden yanında olduğunu sordu.",
          "“Bundan sonra nereye gideceğiz ve sonucumuz ne olacak nereden bileceğim. İhtiyacımız olabilir diye düşündüm.”",
          "Takıları çoktan gitmişti, şimdi de metaldan ne varsa her şeyimizi attı.",
          "Ricalarımı artık reddedemeyeceğini anladı, bu yüzden millet girmeyi durdurduğunda Kiko dayımı ve yengemi bulmaya gittik. Kiko dayımın yüzü beyazdı. Özel ihtiyaçları olan bir kişi idi, ama onu hiç bu kadar korkmuş görmedim. Annem dayının karısına küçük dayım Senaid’in nerede olduğunu sordu, ormanın içinden gittiğini söyledi. Ben Edo’ya ve Dado’ya yaklaştım, yanlarına betondaki battaniyeye oturdum. Sert ve soğuktu.",
          "Fabrika binasında etrafımızda daha önce hiç görmediğimiz çok fazla yüz vardı. Daha önce hiç görmedik, ancak sanki hep tanıyorduk. O binanın içinde sadece panik dalgaları geçiyordu. Fabrikadan çıkarıyorlar, kesiyorlar, tecavüz ediyorlar ve idam ediyorlar diye haberler kulaklarımıza geliyordu. Annem ve babam etrafımızdaki insanlarla konuşurken, UNPROFOR burada olduğu için her şey iyi olacak diye konuşuyorlardı, ancak akşam gelirken bu fabrikalar içinde aslında bir toplama kampında bulunduğumuz tamamen belli oldu.  ",
          "Geceyi bir şekilde atlattık… Ben kafamı tekrar o deliğime soktum, annem vücuduyla vücuduma yaslandı, bir şekilde sardı, beni daha güvenli hissettirmek için, tüm gece duyduğum tek şey ah etmeleri, çocukların ağlaması, ve o girişin içinden fabrikanın arkasındaki uzak bir yerden gelen çığlıklar. Rüya mı gördüm yoksa o fabrikadan ruhumla mı taşındım, bilmiyorum, ama tüm gece gözlerimin önünde Drinjača (Drinyaça) nehri, Burnica’dan ahududu, Konjević Polje’de güneş batımı ve saire vardı. Her şey kafamda rastgele karışıyordu, çünkü kamp kokusunu ve karanlığını hatıralarıma kazıyordu ",
          "Kargaşa ve gürültü sabah erken başladı. Neler oluyor duyamadık, ama insanlar fabrika binasının içinde oraya buraya gidiyordu. Babam zıplayıp o girdiğimiz kapıdan çıktı, neler oluyor görmeye gidiyorum dedi. Babam sonra metal bir tabakla geldi, içinde biraz bir çorba ve şeffaf bir ekmek dilimi vardı. Çorba kirli suya benziyordu, tabak da alçaktı, sofra takımımızı da daha önce attık, çorbayı kaşıksız da içemedik. Çorbayı ekmekle toplamak istedik, ama ekmek onun içinde eridi.",
          "Ben o azcık olan çorbayı nasıl yiyeceğimi düşünerek, babam anneme İbro eniştemin yanına gittiğini ve eniştemin dün gördüğümüz sedyeden hala kalkamadığını anlattı. Yüzünün çok beyaz olduğu ve “kuruduğunu” söyledi. “Kimsenin ona vereceği infüzyonu yoktu”. Annem ve babamdan biraz yürüyüşe gitmek için izin istedim. Sağ tarafta uzakta duvarın olmadığını gördüm ve oradan ışık geliyordu. Bu açıklığa kadar geldiğimde insanlarla dolu bir tarla gördüm, fabrikanın içinde olan insanlarla aynı idi, ancak çorak arazide ve güneşin altında pişiyorlardı.",
          "Döndüğümde anneme gördüklerimi anlatamadım. Annem oturmamı emretti, önümüze bir adam çömeldi. İsimlerimizi sordu, babamın ve benimkini. “UNPROFORlar sadece erkekleri yazmamızı söylediler.” Babam benim Jasmin Jusufa Jusufović (Yusufun oğlu Yasmin Yusufoviç) olduğumu söyledi, bu adam da bunu yazdı. Babam ismimi öyle söylediğinde çok gururlandım. Ben babamın oğluyum ve bana kimse bir şey yapamaz, diye düşündüm. Soruşturmacı gittikten sonra, solumuzdan bir adam bağırdı: “Ne UNPROFORları… Çetnikler!” ",
          "Baba, o adam niye benim ve senin isimlerimizi yazdı?",
          "Bilmiyorum. Belki burada ne kadar insan var diye öğrenmek için.",
          "O zaman neden annemin ismini yazmadı? Nica’yı yazacak mı? Keka’yı yazacak mı? Kiko’yu kim yazacak?",
          "Bilmiyorum, canım oğlum, bilmiyorum.",
          "Annem ve babam etrafımızdaki insanlarla konuşuyordu. Aralarımıza çetnikleri bıraktılar ve onların insanları fabrikanın yanındaki beyaz ev diye bir yere tecavüz etmek ve idam etmek için çıkardıkları haberi köşemize geldi. Bundan dolayı annem tek başıma o çayıra ve dışarıya gitmeme izin vermedi. ",
          "Anne lütfen, oraya hava almaya gidelim.",
          "İstemiyorum, otur!",
          "Lütfen anne, sadece güneşi biraz göreyim.",
          "İstemiyorum, otur!",
          "Ama, anne, Nica’nın yanına gidelim, Edo’yu ve Dado’yu göreyim, hadi beraber gidelim.",
          "Geldiğimizde…",
          "Hadi anne, ben sadece o köşeye kadar gideyim, Nica’dan görürsün beni.",
          "Birbirimizi izlediğimizi tasdik etmek diye her yarım dakikada bir anneme doğru dönüp el salladım. O zaman çitin sonundan gelen bir çığlık ve bağırma duydum ve bir kadın sesinin ne bağırdığını anladım. Anneme doğru koştum, o kadar çabuk koştum ki zamana geri döneceğimi düşündüm. ",
          "Ne oldu, o çığlıklar ne?",
          "Bilmiyorum, bir kadın “Çocuğunu ezdi!” diye bağırıyordu.",
          "Kim kimin çocuğunu ezdi?",
          "Bilmiyorum anne, sadece bir kadının “Çocuğunu ezdi” diye bağırdığını duydum.",
          "Aniden tüm o milletin üzerine ölümcül bir sessizlik düştü. Herkes sustu, bir an için fısıltı bile yoktu.",
          "Sonra kulaktan kulağa haber geldi: bir çetnik, birkaç dakika önce doğan bir bebeği annesinin elinden alıp ezdi.",
          "Bir yere gitmek istediğimi söylemeye cesaret edemedim artık. Dayımın hanımının yanına gitmemizi istemeye de cesaret edemedim artık… Sadece o duvardaki deliğimi aradım, oraya kafamı sokup Drinjača’yı çağırmak istedim.",
          "Tekrar kararmaya başladığında tek başıma olduğumu zannederek, çok sessizce fısıldayarak: “Buradan ne zaman gideceğiz?” dedim.",
          "Babam duydu beni ve ona sorduğumu zannederek söyledi:",
          "- Yarın gideceğiz. Hadi yat şimdi!",
          "Temmuzun 13’ünde binadaki panik beni uyandırdı. Binanın yarısı ayağa kalktı, annem etrafımda bulunan o az kalan eşyalarımızı topluyordu ve o küçük sırt çantamı kucağıma attı. Çantamın içinde artık sadece o benzin kokan şişe vardı. ",
          "- Gidiyor muyuz? diye sordum. Kimse duymadı.",
          "Önceki günlerde önemsemeyebildiğim hırıltı o sabah daha sesliydi. İnsanlar yeni haberler getiriyordu, uzakta ve solda bir kadın bayıldı ve uyandırmaya çalışıyorlardı, bir dedeyi ayağa kaldırmaya çalışıyorlardı, bir anne bağıran çocuğunu susturmaya çalışıyordu.",
          "Ben öyle olmayacağım, diye kendime söylüyordum. Ben kibarım, ben ağlamayacağım ve bağırmayacağım, annem ve babamı endişelendirmeyeceğim. Herkes bir yere gitmeye hazırdı, ama kimse hareket etmiyordu. Sadece ayağa kalkan insanlar, öyle durdukları yerde sallanıyorlardı ve dönüyorlardı. Az sonra, binanın öte yanında, o girdiğimiz tarafında büyük bir kapı açıldı ve insanlar bir nehir gibi herkes o tek kapıdan geçmeye başladı. Biz doğru kapıya gitmedik, ancak o kalabalık bizi binanın ortasından taşıdı, dayımın hanımının daha önce oturduğu yere getirildik, onlar çoktan gitmişti. ",
          "Aniden, bir yeşil kamyonun yanında durduk, ama insanlar kapıya doğru itmeye devam etti. Babamın elimi çekip: “Baba, nefes alamıyorum!”, demeye çalıştım. O beni kaldırıp omuzlarına koydu. Kamyonla aynı seviyedeydim. Kamyonun üzerinde de insanlar vardı. Nihayet hareket etmeye başladık, dışarıya çıktığımızda güneş ve sıcaklık tarafından kör edildim. Gözlerimi avuçlarımla kapattım. Babam ellerimle omuzlarını tutmadığımı fark etti ve beni kendisinin ve annemin arasına indirdi, ikisi de ellerimi tuttular. Başımı ayaklarıma doğru indirdim, gözlerim ışığa alışsın diye.",
          "Uzun bir asker kordonundan yürüyorduk. Gördüğüm tek şey gerçek çizme, askeri üniforma içindeki bacaklar ve silah fıçıları idi. Bizi yürümeye zorluyorlardı, bağırıyorlardı, koşmak zorundaydık. Caddeye çıkana kadar güneşe alışamadım. Önümüzde sola doğru kamyon, otobüs ve asker sırası uzanıyordu. Babam annemi ve beni önünde itiyordu, annem beni eteği ile sararak sıkıca yanında tuttu. Bu şekilde koşmak zordu. Birçok kamyon ve otobüs geçtik ve biri GİR! diye bağırdı ve ben kendimi kamyona yaslanan ahşap bir merdivenin önünde buldum.  ",
          "Merdiveni ne kadar çabuk tırmanırsam, üçümüz de o kadar çabuk kamyona bineriz ve bunların hepsi biter diye düşündüm.  Merdivenin ortasında arkamda talimat duydum: “Sen, in! Buraya!” ve kargaşa duydum. O kalan birkaç basamağı çıktım ve döndüm. ",
          "Annem merdivenin önünde durdu, ellerini sallayarak, bir asker tüfek ile babamı yol boyunca itiyordu. Üçümüz ve o asker dışında hiçbir şey göremedim. Dondum. O asker babamı yol kenarındaki kanala itiyordu, ondan sonra ikinci asker anneme kamyona binsin diye bağırdı. Annem tamamen donmuş olan beni alıp kamyonun derinlerine götürdü. Ben hala babamı izliyordum. Kanalın üstüne durdu. Bizi giderken görmek için bize döndü. Onu izlediğimi gördüğünde “şışşş” dediği gibi elini dudaklarının önünde tuttu, ondan sonra da “git!” der gibi elini salladı. ",
          "Duymadım, belki de bağırıp çağırdık, ama duyamadım. Hiçbir şeyi kıpırdatamadığımı hatırlıyorum. Gözlerimi babamın üzerinde bıraktım. Sadece dişlerimi çok sık sıktığımı biliyorum, içimde de patlamak istedim. Bir elimi biraz kaldırmayı başardım, kafamda el salladığımı düşündüm. O da sadece o hareketi tekrarladı “şışşş! Git!”. Başında genel olarak taşıdığı bere şapkası yoktu, o güneşte sıcak olacak düşündüm.",
          "Üzerinde masmavi çizgili, krem renkli tişört vardı. Yıpranmış yelek. Siyah pantolon ve yıpranmış, Srebrenica’da sahip olduğu tek ayakkabı. Kolunun altında benim şapkalı kırmızı ceketimi taşıyordu. Ve öyle bizi izleyerek durdu, “Şışşş! Git!”",
          "Ondan sonra sanki beni o kamyondan biri alıp gökyüzüne götürdü. Babamın çevresindeki çukurda daha çok erkek olduğunu gördüm. Onların arkalarında güneşte ve rüzgarda oynayan olgun buğday gördüm, fabrikanın içinden insanlar çıkmaya devam ediyordu, bazıları otobüslere biniyordu. Annenin kamyonda, beni sıktığını, uzun, geniş elbisesiyle sardığını ve ağladığını gördüm. ",
          "Bir yerden erkek bir genç kamyonun dibine atlayıp girdi. Kamyonun içinde bulunan birkaç varilin arkasında saklandı, ondan sonra kadınlar taşıdıkları çantalarını, bohçalarını üzerine attılar. Kamyon insanlarla dolduğunda, bizi branda ile kapattılar ve içi kapkara oldu, tekrar kör oldum. Kamyon yola çıktığında düşük sesle kendi kendime söyledim: “babam diğer kamyonla gelecek…”",
          "Kamyon uzun ve yavaş giderek bizi bir yere götürüyordu. Brandanın altında hava tükeniyordu. Aniden durduk. Hepimiz sustuk. Kamyonun kapısını duyabiliyordum, ondan sonra brandanın bir kısmını açan uzun bir ahşap kiriş  gördüm: Niye kapattınız kendilerini, hepiniz boğulacaksınız, lanet olsun!  O açık kısmından bakarak bulunduğumuz yoldan, önümüzden bir yerden ormanı bombaladıklarını izleyebildik. O ahşap kiriş tekrar konuşmaya başladı: Sıkılmayın, biraz izleyin.",
          "Bu azcık gelen ışıktan yararlanarak etrafımda kanlı bir bezle kafası sarılmış bir nene gördüm, gözleri bile o bezle sarılıydı, ve çok kadın gördüm. Bir çocuk bile görmedim. O ahşap kiriş brandamızı tekrar kapattı ve yolumuza devam ettik. Uzun bir süre sonra kamyon tekrar durdu, annem de beni yere koydu ve eşyalarla kapattı. Birkaç kadın daha üzerime bohçalarını attılar ve ondan sonra ağır bir şey hissetim. Bir insan sesi duydum. ",
          "Burada erkek var mı? Kadınlar olmadığını söylediler, ondan sonra çığlıklar duyuldu.",
          "“Başka kimse yok, sadece o vardı! Başka kimse yok!”",
          "Kısa süre sonra kamyon tekrar yola devam etti.",
          "Durduğumuzda bizi bir yolda kamyondan indirdiler, yolun altında bir çayır ve nehir vardı ve diğer tarafta başka çayırlar, ev ve ordu. Yolun bir tarafını gösterip söylüyorlardı: “Haydi şimdi yukarıya gidin, sizinkiler yukarıda…”. Annem ve ben yolu takip etmeye başladık… Bir tek o ve ben vardık.",
          "İnsanlardan geldiğimiz yerin isminin Tišča (Tişça) olduğu duyduk. Bize gösterdikleri yol Kladanj’a (Kladan) doğru gidiyordu. Saatlerce yürüyorduk, ara sıra, yorgunluktan ve nefes almak için duruyorduk. Yol boyunca yıkılmış ağaçlar vardı ve bazen ağaçların üzerinden, bazıların da altından sürünerek gitmek zorundaydık.",
          "Yol boyunca caddenin kenarından insanlar: “Tünele kadar ileri gidin, sizinkiler tünelden sonradır…”, diyorlardı.",
          "Tünel hiç gözükmüyordu, her virajdan sonra yeni viraj vardı, yolun altında nehir, yolun üstünde bir geçit, ve geçitin boğazından gözüken biraz gökyüzü.",
          "Akşam olmaya başladı ve en sonunda tüneli gördük. Tüneli geçtikten sonra Safeta teyzemi, Niska’yı ve Niho’yu bulduk. Beni gördüğünde Niska haykırdı. Onun çığlığından geçit kırıldı, ben de dondum. Bugüne kadar kimse bana o kadar sık sarılmadı ve yanımda kimse Niska kadar beni sallamadı.  Ağlayarak tekrarlıyordu “babam yok, babam yok”... “demek ki eniştem Muhammed de diğer kamyondadır”, diye düşündüm… Ve o zaman da ağlamaya başladım. Sessizce. Gözyaşlarım akıyordu sadece.",
          "Ondan sonra sadece mecburiyetten yürümeye devam ettik. Kendimde değildim. Dubrave’ye doğru bizi taşıyan otobüsün sadece cam çerçevesini aralıklı aracılığıyla hatırlıyorum ve o pencerenin çerçevesinin içinde kırmızı gökyüzünü hatırlıyorum. ",
          "Dubrava'ya vardığımızda hava çoktan karanlıktı. Annem ve ben bilinmeyen siluetler denizinde duruyorduk. Bazıları sarnıçlara, bazıları da çadırlara doğru koşuyordu. O zaman annem ışığı yanan bir sokak lambası gördü, ve oraya, bir çakıl yığını üzerine oturduk. Ampulün açtığı ışık çemberinin dışında, hepsi karanlıkta düştü, dünyada hiç kimse kalmamış gibi, sadece annem ve ben, bir çakıl yığını üzerinde oturmuş ve bekliyorduk. Annem bana: “Biz buradaysak ilk önce bizi görecekler” diyordu. Gerçekten görecekler mi bilmiyorduk.",
          "Kendimi kaybediyordum… Aniden bir yerden biri isimlerimizi bağırdı: Husnija (Husniya)! Jasko (Yasko)!",
          "Dikkat etmedik, inanmadık.",
          "Aniden o karanlıktan kuzenim Emir’in yüzü çıktı.",
          "“Benim Husnija! Emir! Beni tanımıyor musunuz!”, diyordu ve ağlıyordu. ",
          "Bize su verdi. Ben ise, bir an vardım, bir an yoktum.",
          "Onun arabasında birkaç kez uyandığımı hatırlıyorum, onun yanında, yani önümüzde tanımadığım bir kadın daha gördüğümü hatırlıyorum.",
          "Bilinç durumundan çıkan bu gezinmeler sayesinde, bizi eve getirdiklerini fark ettim. Ön koltuktaki kadın beni bir merdivenden çıkarttı. Işık bana çarptığında, merdivenin tepesindeki odadan içeride iki oğlan gördüm ve kuzenim Kada olduğunu fark ettim, beni elle yönlendiriyordu.",
          "Nasıl ve ne zaman bilmiyorum ama aniden kendimi farklı giysilerin içinde buldum ve üzerinde fırında pişmiş bir piliç bulanan bir sofranın önündeydim. Ağzımı açtım, kendimi kaybettim, ne yapacağım bilmiyordum. Kada’ya baktım, sonra o pilice baktım… Kada o piliçten bir parça kopardı, ellerimi aldı, içine o parçayı koydu ve “hadi ye, piliç.”",
          "O fırında pişmiş piliç ve ellerimde onunla ne yapacağım bilmediğim et parçası o gün son hatırlayabildiğim şeylerdi. Uyuduğumu biliyorum, nasıl, nerede ve ne zaman uyuduğumu hatırlayamıyorum. O gün bu dünyada daha fazla var olamadım.",
          "Srebrenica’ya (Srebrenitsa) ilk kez 1999 yılında döndüm. “Çocuk Elçiliği” bir ziyaret düzenledi ve kendimi aniden belediye meclisinin önünde, elimde bir mikrofonla buldum, bir meclis üyesinden gelen hakaretten sonra bir şey söylemek zorundaydım.",
          "“Bana bak! Hepimizi öldürmediniz!”, dedim.",
          "Benimle beraber gelen Duško Tomić’in (Duşko Tomiç) soluklaştığını ve gazeteciler ve kameraların bir anda hepsinin bana doğru geldiğini hatırlıyorum. Devam ettim:",
          "“Tanrının izniyle, beğenseniz de beğenmeseniz de, babam ile ilgili ve bize yapılanla ilgili şahitlik etmek için babamın ardında hayatta kaldım.”",
          "Cesaretimin nereden geldiğini bilmiyorum.",
          "Bundan sonra beni yakaladılar ve geldiğimizden daha hızlı ayrıldık. Kitaplarımın, babamın bir şeyinin kalıp kalmadığını, evimizde biri olup olmadığını, kar kızaklarımı yakıp yakmadıklarını görmek için biraz durmamızı rica etmeye zamanım yoktu.",
          "Translated by: Ado Sirbegovic, Özgür Karadeniz"
        ]
      },
      Danish: {
        title: "Jasmin Jusuf Jusufovic",
        subtitle: "Den lille prins",
        language: "Danish",
        paragraphs: [
          "Ved daggry d. 8 juli, buldrede det i et sted i bjergene, lige akkurat højt nok til at vække os. Da rykkede jeg til mit ly ved fryseren, der hvor væggen var tykkest. De dage rykkede vi et utal gange hen til den tykkeste væg i huset. For at slå tiden ihjel i mit ly ved fryseren, begyndte jeg fra d. 9 juli at tælle antallet af granater der faldt, som kunne høres hele dagen lang. Til tider var der 30 ad gangen. Folk gik ud på gaden, men med reglen om at det kun var den første der dræbte. Ned ad gaden gik også Ramo, brødbageren med de sårede i en vogn. ",
          "",
          "Omkring middagstid blev det stille igen. Jeg gik hen for at se hvad Edo og Dado lavede. Jeg vidste at far også var et sted i nabolaget. Da jeg gik ned ad Milosheva, tænkte jeg at far måske var gået hen til smeden på den anden side af floden. Granaten eksploderede imens jeg stod i det åbne ved siden af Nasers dæmning, min største bekymring var at løbe op af bakken igen, for at se om mor i det mindste var okay. På min vej løb jeg hurtigt ind til min tantes hus og råbte at jeg ville op til mor så hun ikke bekymrede sig, men min tante ville ikke lade mig løbe ud igen, hun holdte mig i ly under bordet sammen med Edo og Dado, imens hun sagde at mor nok vidste at jeg var sammen med dem. ",
          "",
          "På trods alt, gik livet videre normalt. D. 10 juli tidligt om morgenen gik vi ovenpå for at gøre rent i bedstemors værelse. Jeg tog et lille gulvtæppe og gik ud på altanen for at ryste det. Granaten faldt bag huset. Jord faldt på taget som regn, mens jeg i ti sekunders tid hang langs væggen. I al den tumult, da det blev stille igen ved middagstid, besluttede mor og far at vi skulle mødes med faster for at saliti stravu, for efter den første granat sagde jeg ikke et ord mere. De troede at der var sket mig noget. Hos hende kiggede jeg ned i terrinen med vand og bly og blev ved med at sige til min faster “altså hvor ser du den løve?”. ",
          "",
          "Faster boede i Kazanima, vi boede i midtbyen, hvilket var en lang gåtur under truslen af granater. Den dag i dag forstår jeg ikke hvordan de kommunikerede til hinanden at vi skulle mødes halvvejs ved min tantes hus tæt på bus stationen, uden fungerende telefoner eller noget.  Da vi var færdige med strava, følte jeg mig på ingen måde mindre bange. Granaterne at falde igen. Sammen med os var Edo og Dado, Edo sammen med mig ved fryseren, Dado ved den anden tykke væg. Nogen råbte at det faldt over den gamle by. Jeg begyndte at bekymre mig for min ven Kemo. ",
          "",
          "Om morgenen d. 11 juli, begynte skyderierene tidligt, tættere på end nogensinde før. Det var varmt. Mor og far var begyndte at pakke ting i tasker, sandsynligt fordi at hvis vi skulle flygte, så skulle det ikke være tomhændet, som den gang fra Drinjaca. Jeg hoppede op og pakkede i min lille rygsæk Den Lille Prins og 1000 Hvorfor/Fordi.  Granaterne stoppede ikke på noget tidspunkt den morgen. Ned ad gaden, gennem byen begyndte flere og flere at flygte fra midtbyen, de skulle væk. Jeg ved ikke hvorfor men vi ventede længe på noget, indtil nogen fra menneskemængden råbte “Chetnici er kommet ned på markedet!”  Vi tog fat i de klargjorte rygsække, far tog fat i mig som en rygsæk også, og hurtigt kom vi ned fra højen vores hus lå på og blev en del af den enorme folkemængde som også flygtede. Bag os kunne man høre skud. ",
          "",
          "Mens vi løb ned af gaden, rev jeg mit ærme i stykke på det hegn som var blevet ødelagt af en granat, som slog børnene ihjel der spillede en fodbold turnering foran skolen. Måske rev jeg også min arm, men i det øjeblik mærkede jeg intet. Jeg nåede kun at se min mor rive mig væk fra hegnet, og vi løb ind i tante Muneveras hus ved bus stationen. Vi stoppede af en eller anden grund. Far løb tilbage mod midtbyen, imens mor tog min rygsæk af, hældte bøgerne ud af den. Hun kiggede på mig, og mine skuldre faldt til jorden. Det var sådan jeg at mistede min Lille Prins. Da hun så mig stående over bøgerne, trist, prøvede hun at trøste mig, sagde at bøgerne var for tunge at løbe med når vi skulle flygte… Jeg sagde ikke noget, jeg stod blot og tænkte at måske ville de ikke være for tunge, men jeg vidste at der ikke var nogen pointe i at skændes med hende om det. Slaget var tabt. ",
          "",
          "Senere kom far tilbage til huset, med en sæk mel på ryggen. Han sagde at folk havde brudt ind og røvede Nasers lager. Med mel fra sækken lavede tante Munevera boller til alle. Jeg havde glemt at indtil de boller havde jeg ikke spist i flere dage og jeg havde glemt hvor sulten jeg var. Jeg spiste med besvær to. Vi blev ikke længe, kort efter skiltes vi. Far sagde at han skulle hente noget, men det var ikke sandt, sandheden var at han krydsede skoven. Vi mødtes med tante Sena og tog videre. Omkring hospitalet havde der samlet sig en folkemængde. Jeg så folk hoppe fra altanerne fra de omkring liggende bygninger. Vi gik ind i en af dem. ",
          "",
          "Vi blev ikke længere i mørket af bygningen, vi gik videre, men vi kunne ikke kommer videre end til hospitalet. Folk prøvede at komme ind i FNs lejr ved Vezionica, og nogen sagde at Zulfo Tursun sendt drenge tilbage fra hospitalet. Der blev vi også væk fra min tante. Bedstemor, mor og jeg stoppede ved en af bygningerne ved hospitalet. Et sted tæt på faldt der en granat. Først kastede bedstemor sig over mig, dernæst mor, og over os faldt det sidste glas der havde overlevet på den bygning. Jeg husker at jeg dårligt kunne trække vejret under bedstemor og mor. ",
          "",
          "Efter granaten faldt, blev kvinderne endnu mere ivrige efter at komme ind i lejren, og gennem hegnet af Vezionica. Vi tre gjorde det samme. Gennem larmen genkendte jeg en stemme “Husnija!!” Far var kommet tilbage fra skoven. “Jeg går med jer, uanset hvad der kommer næst” Et styke af hegnet var blevet brudt igennem, og far formåede at bære mig ind i lejren og ind i FNs garage. Da faldt endnu en granat. Granatplinterne begyndte at komme igennem garagens metalplade tag, hvilket lod lys komme ind.  Vi havde mistet mor. Vi havde mistet bedstemor. Vi kunne heller ikke se min tante, Edo eller Dado, Keke og hellere ikke onkel Kiki. Der var kun os to. Far havde lagt mærke til hvordan FNs soldater prøvede at flygte fra Vezionica i deres lastbiler, han tog fat i mig og mens han løb mod en af lastbilerne kastede han op på karrosseriet. Han hjalp mange andre med at komme op på lastbilen, og mængden gjorde at jeg blev presset mod siden af lastbilen. Jeg nåede at vende hovedet mod åbning, så jeg kunne trække vejret. Gennem mængden mærkede jeg at far og jeg ikke længere holdt fast i hinanden. Folk hoppede på lastbilen langs vejen, og jeg mistede det lidt luft jeg i øjeblikke ville have. Lastbilen kom langsom til en anden base, an akkumulator fabrik. I mængden af mennesker og FN soldater endte far og jeg i mørket af hallen. Undervejs nåede vi at se onkel Ibro på en båre. ",
          "",
          "Der kom flere og flere til fabrikken. Far kom op at tottes med nogen omkring en båre som jeg kunne sidde på. Jeg satte mig op af en væg, ved siden af en ødelagt indgang. Båren var våd, gennemblødt i blod. Vi sad på båren men mine øjne var fast fokuseret på den dør vi var kommet ind af, i håbet om at mor ville komme gående ind af den. Jeg havde mistet tidsfornemmelsen. Pludseligt dukkede mor op ud af ingenting, hun så ud som om hun havde grædt, og som om hun havde kæmpet med nogen. Hun sagde at FN soldaterne ikke ville lade hende komme ind i fabrikken, hvorefter hun havde skændtes med en af dem og formåede a snige sig ind endeligt. Far gik senere hen for at finde noget vand. Han kom tilbage med en falske der lugtede af benzin, og med meget lidt vand i. Han sagde at han havde fundet tante, Edo og Dado og også onkel Kiko. Men ikke bedstemor. Mor tilføjede at bedstemor var forblevet i en anden fabrik med den da gravide Keka. ",
          "",
          "Jeg kunne ikke længere lytte til larmen, skrigende, kigge på de blodige ansigter og de grå toner af fabrikshallen. Jeg fandt et hul i væggen bag mig, satte hovedet på det og havde følelsen af at jeg faldt i søvn. Kort efter var jeg klar igen og mor spurgte hvorfor jeg ikke sov. Sagde at jeg ville hen til tanten, hendes svar var kort -senere. Dernæst ville jeg på toilet. Hun tog mig med ud bag fabrikshallen, gennem den ødelagte indgang hvor vi sad. Bag fabrikken så jeg blodige mennesker med blodige forbininger. Sagde at jeg alligvel ikke skulle på toilet, og jeg gik ikke ud igen. ",
          "",
          "På et tidspunkt begyndte mor at panikke. Hun gik igennem vores tasker. Hun tog noget bestik op af tasken og løb for at smide det ud. I fabrikshallen havde folk begyndte at snakke om hvordan Chetnici, forklædt som FN soldater gik rundt i fabrikken og ledte efter en grund for at tage nogen med ud og henrette dem. ",
          "",
          "Hvad skal du dog med alt det, havd far spurgte hende. ",
          "",
          "Hvor skulle jeg vide fra hvor vi skulle hen og hvordan vi ville ende. Jeg tænkte at vi måske ville få brug for det. ",
          "",
          "Hun havde ikke længe haft nogen smykker, og på det tidspunkt tog hun alt hvad vi havde af metal og smed det ud. ",
          "",
          "Hun indså at hun ikke længere kunne holde mig hen, så da folk endeligt stoppede med at komme ind i hallen, bedrog vi os ud for at finde tante og onkel Kiko. Onkel Kiko var frygtlig bleg. Han havde særlige behov, men indtil det tidpunkt havde jeg aldrig set ham være så bange. Mor spurgte min tante hvor Senaid  var, min yngre onkel. Hun sagde at han havde krydset skoven. Jeg gik hen til Edo og Dado, satte mig ved dem på et lille tæppe der var lagt ud på beton gulvet. Det var hårdt og koldt. ",
          "",
          "Omkring os var alle disse ansigter vi aldrig før havde set. Aldrig havde set, men nu føltes som om vi altid havde kendt dem. Der gik bølger af panik gennem hallen. Der gik rygter om at de tog folk ud af hallen, skar halsen over på dem, voldtog og henrettede. Mor og far snakkede med folk omkring os om hvordan det snart ville være ovre, og at vi nok skulle klare den, for vi sad trods alt med FN soldater. Mens mørket faldt på, gik det mere og mere op for folk at vi i de fabrikker faktisk sad i fangelejre. ",
          "",
          "",
          "På den ene eller anden måde klarede vi det gennem natten. Jeg havde igen sat mit hovede i det hul i væggen jeg havde fundet, mor havde trykket sin krop mod min, for at jeg skulle føle mig mere sikker tror jeg. Det eneste jeg hørte hele natten var udåndinger, børnegråd  og et sted langt væk, gennem åbningen til hallen, skrig. Jeg ved ikke om det var en drøm eller om jeg virkeligt forlod hallen i ånden, men hele natten kunne jeg se Drinjaca, varmt mælk fra Pahljevica, hindbær fra Burnica, solnedgang i Konjevic Polje… alt virkede forvirrende og var blevet blandet. Alt imens fangelejren lagrede sin lugt og mærke i mit sind. ",
          "",
          "Tumulten og larmen startede meget tidligt om morgenen. Ingen genfortælling af hvad der foregik nåede os, men folk havde hurtigt sat sig omkring i hallen. Far stod op og gik ud gennem den dør vi var kommet ind af, for at se hvad der foregik. Senere kom han tilbage med en metal tallerken, i den var der lidt suppe og en skive gennemsigtigt brød. Suppen så ud som beskidt vand, tallerknen var ikke særlig dyb, vi havde smid vores bestik ud, så vi ville dyppe brødet i den, men brødet faldt fra hinanden så snart det kom i. ",
          "",
          "Imens jeg prøvede at lure hvordan jeg skulle spise suppen, fortalte far til mor hvordan han havde været forbi onkel Ibro, og hvordan han stadig ikke kunne rejse sig fra den båre vi havde set ham på dagen før. Han fortalte mor hvordan han så bleg ud og hvordan der ikke var nogen til at give ham infusion. Jeg spurgte mor og far om jeg måtte gå en tur. Til højre kunne jeg se en åbning, der var ingen mur og lys kom ind. Jeg gik derhen og gennem åbningen kunne jeg se folk på marken, de samme som sad indenfor i hallen. Disse mennesker sad dog ude i solen og bagte. ",
          "",
          "Da jeg kom til bage fortalte genfortalte jeg hvad jeg havde st og mor beordrede mig at jeg skulle sætte mig. Foran mig satte en mand sig på hug. Han spurgte til navne, mit og fars. “FN soldaterne har bedt os om at skrive ned alle mandlige navne” sagde han. Far sagde at jeg var Jasmin, søn af Jusuf Jusufovic og han skrev det ned. Jeg følte mig så stolt da far sagde mit navn på den måde, jeg tænkte ja, jeg er min fars søn og der er ingen der kan gøre mig noget. Et sted fra den venstre side råbte en mand “ FN soldater.. som om! Det er Chetnici der vil have det!”. ",
          "",
          "Far, hvorfor skrev den mand vores navne ned?",
          "",
          "Jeg ved det ikke, måske skal de vide hvor mange mennesker der er. ",
          "",
          "Men hvorfor skrev han så ikke også mors navn ned? Skriver han også Nicos navn ned, Har han skrevet Kekas navn og bedstemors? Måske Kiko?",
          "",
          "Kære barn, jeg ved det ikke… ",
          "",
          "Mor og far snakkede med folk omkring os. Snakken nåede om hvordan de havde ladt Chetnici ind iblandt os, og Chetnici tog folk hen til et hvidt hus ved fabrikken hvor de voldtog og henrettede folk fra hallen. Derfor måtte jeg ikke længere gå hen til åbningen. ",
          "",
          "Mor kan vi ikke godt gå ud lidt og få frisk luft? ",
          "",
          "Nej, sæt dig ned. ",
          "",
          "Mor kan vi ikke godt, bare så jeg kan se solen lidt?",
          "",
          "Nej, sæt dig ned. ",
          "",
          "Men mor, lad os gå hen til Nico, så kan jeg se Edo og Dado, vi kan tage derhen sammen. ",
          "",
          "Da vi kom derhen, sagde jeg til hende at jeg gik hen til hjørnet med en åbning, hvorfra hun kunne se mig. Jeg vendte mig om hvert halve minut og vinkede til hende. Da hørte jeg et skrig fra enden af hegnet, det var en kvindelig stemme. Jeg løb tilbage til mor med sådan en fart at jeg troede tiden ville gå baglæns. ",
          "",
          "Hvad er der sket, hva’ er det for nogle skrig?",
          "",
          "Jeg ved det ikke, en kvinde sagde at de havde kørt hendes barn ned! ",
          "",
          "Hvem har kørt hvis barn ned?",
          "",
          "‘Jeg ved det ikke mor, men jeg hørte en kvinde råbe “ de kørte hendes barn ned”. ",
          "",
          "Pludseligt blev det dødstille i hallen. Alle tiede stille, ikke engang en hvisken. ",
          "",
          "Senere nåede det frem, at en Chetnik havde taget en kvindes barn, som hun lige havde født og kørt det ned. ",
          "",
          "Jeg turde ikke længere spørge om jeg måtte gå nogen steder. Jeg turde hellere ikke længere spørge om vi skulle hen til min tante. Jeg ledte kun efter mit hul i væggen, hvor jeg kunne lægge mit hovede og drømme om Drinjaca. ",
          "",
          "Da det var blevet mørkt igen, spurgte jeg mere mig selv end nogen anden “hvornår skal vi væk herfra?” ",
          "",
          "Far overhørte mig, og i den tro at jeg havde spurgt ham sagde han “I morgen skal vi videre, læg til at sove nu”. ",
          "",
          "Panikken i hallen d. 13 juli vækkede mig. Halvdelen af hallen stod oppe, mor var i færd med at pakke vores ting sammen omkring mig, og lagde min lille rygsæk på skødet af mig, der nu kun indholdt den flaske der lugtede af benzin. ",
          "",
          "Skal vi afsted nu, spurgte jeg. Men ingen hørte mig. ",
          "",
          "Larmen og tumutlen som jeg havde været i stand til lukke ude de foregående dage, var meget høj den morgen. Folk kom med nyheder, folk prøvede at råbe en kvinde tilbage til bevidstheden  til venstre, andetsteds prøvede de at få en gammel mand op at stå, en kvinde prøvede at trøste sit grædende barn. ",
          "",
          "Jeg blev ved med at sige til mig selv at sådan ville jeg ikke være. Jeg var en god dreng, jeg ville hverken græde eller skrige så mor og far blev bekymret. Alle stod klar til at gå, men vi rykkede os kke. Sådan stod vi og de mennesker der havde rejst sig, og rykkede lidt på stedet. Ikke længe efter, i den anden ende af hallen, hvor vi var kommet ind, åbnede store døre og folk begyndte at bevæge sig som en flodbølge mod den ene dør. Vi rykkede os ikke mod døren i starten. Mængden bar os først mod midten af hallen, dernæst mod det sted hvor tante havde siddet, men de var der ikke længere. ",
          "",
          "Pludseligt stod vi ved en grøn lastbil, men folk blev ved med at rykke ud af døren. Jeg rykkede far i hånden og prøvede at fortælle ham at jeg ikke kunne trække vejret. Han tog mig op og satte mig på sine skuldre. Jeg var i højde med lastbilen. Der var allerede mennesker på lastbilen. Da vi endeligt rykkede os igen og kom udenfor blændede solen og varmen mig. Jeg holdte mig for øjnene. Far mærkede at jeg  ikke længere holdt fast i ham, så han satte mig ned mellem mor og ham, de holdte mig begge i hænderne. Jeg holdte hovedet nede og kiggede i jorden for at vænne mig til lyset igen. ",
          "",
          "Vi gik gennem en lang gang dannet af soldater. Jeg så støvler, uniformer og håndtag på gevær. De skyndte på os og råbte. Vi begyndte at løbe. Jeg havde ikke vænnet mig til lyset før vi nåede vejen. Foran os var er en kø af lastbiler og busser og soldater. Far skubbede mor og jeg foran sig, mor holdte godt fast mig, jeg blev viklet ind i hendes dimije, hvilket gjorde det svært for mig at løbe. Vi gik forbi den ene efter den anden lastbil og bus, indtil nogen råbte GÅ IND og jeg befandt mig foran en træ stige, lænet op af en lastbil. ",
          "",
          "Jeg tænkte at jo hurtigere jeg kom op af stigen, jo hurtigere ville vi tre være ombord lastbilen og jo hurtigere ville alt dette ende. Halvvejs op af stigen hørte jeg nogen råbe, DIG, KOM NED IGEN! Jeg hørte larm. Jeg gik op as de sidste trin der var tilbage og vendte mig om. Mor stod med krydsede arme foran stigen, mens en soldat med et gevær skubbede far væk. Jeg så intet andet end os tre og den soldat. Jeg frys. Soldaten skubbede far i grøften ved vejen, mens en anden råbte til mor at hun skulle op af stigen. Mor tog fat i mig, hel stiv, og bar mig længere ind i lastbilen. Jeg kiggede stadig på far. Han stod i grøften. Han vendte sig mod os, og jeg kan stadig se det for mig, han satte sin finger over sine læber og sagde “sssh” og viftede med armen, som om han sagde “Gå”.",
          "",
          "Jeg hørte intet, måske råbte vi, men jeg hørte intet. Jeg husker kun at jeg ikke kunne bevæge mig. Mit blik havde jeg efterladt på min far. Jeg husker at have presset mine tænder sammen så meget som jeg kunne, imens jeg var ved at eksplodere indefra. Fra det punkt havde jeg tåre i øjnene, tåre som gjorde mit blik på far slørret. Jeg formåede at løfte min ene arm lidt, i hovede vinkede jeg febrilsk til ham. Han gentog blot sit “sssh” GÅ!. Han havde ikke sin sædvanlige baret på hovedet. Jeg tænkte at han nok havde det for varmt til at have den på. ",
          "",
          "Han havde en kortærmet t-shirt på, cream farvet, med azurfarvet brede striber. En slidt læder vest. Sorte busker og slidte sko, de eneste han havde i Srebrenica. Under armen havde han min røde hættetrøje. Og der forblev han, men blikket mod os,  “sssh. Gå!",
          "",
          "Jeg havde det som om nogen havde løftet mig, taget mig ud af lastbilen og taget mig med op i skyerne. Jeg så hvordan der omkring min far stod andre mennesker. Bag dem så jeg høsten klar, mens den dansede i solen og vinden, flere folk der kom ud af hallen, nogen  der gik ombord på busserne. Jeg så min mor i lastbilen, hvordan hun holdt mig tæt til sig, viklede mig ind i sine dimije imens hun græd. ",
          "",
          "Et eller andet steds fra, hoppede en ung mand ombord på lastbilen og gemte sig bag nogle tønder som var der. Kvinderne kastede hvad de havde i armen over ham. Da lastbilen var fyldt, lukkede de os inde under presenningen, og der dannede sig fuldkommen mørke, som gjorde mig blind igen. Da lastbilen langsomt kørte afsted, hviskede jeg til mig selv “far kommer med på en anden lastbil…”",
          "",
          "Lastbilen kørte langt og langsomt. Under presenningen kunne man ikke få luft. Pludseligt stoppede lastbilen. Alle blev stille. Jeg hørte døren til lastbilen, og så dernæst en lang planke åbne presenningen, mens en mand råbte “Hvorfor fanden har i lukket jer selv inde under pressening, i bliver kvælt, fuck jer”. Gennem den lille åbning kunne man fra vejen hvor vi var, se hvordan granater fladt over skoven. Jeg kom i tanke om min yngre onkel som ville krydse skoven. Igen snakkede manden med planken, “se lidt med, så i ikke keder jer”.",
          "",
          "I det lidt lys omkring mig så jeg en gamle kvinde, hvis hovede var viklet ind i blodige forbininger, også hende øjne. Jeg så mange kvinder. Jeg husker ikke at have set et eneste barn. Igen blev planket hevet væk, og vi var lukket inde under presenningen og lastbilen fortsatte sin tur. Længe efter det stoppede lastbilen igen. Mor satte mig på gulvet og dækkede mig med ting. Nogle andre kvinder kastede også ting over mig og så mærkede jeg noget tungt. Efter det hørte jeg en stemme. ",
          "",
          "Er der mænd ombord? Kvinderne svarede at der ikke var og da opstod råb. ",
          "",
          "“Der er ikke andre, han var den eneste, der er ikke andre!”",
          "",
          "Lastbilen kørte igen ikke længe efter. ",
          "",
          "Da vi igen stoppede, læssede de os af på en vej, på den ene side var der en dal og en flod. På den anden side et hus og en hær. De pegede ned af vejen og sagde, gå den vej, jeres folk er den vej. Mor og jeg begyndte at gå af vejen… vi var alene. ",
          "",
          "Vi hørte fra folk at stedet hvor vi var hed Tishca. Vejen som vi gik førte til Kladanj. Vi gik i timevis, vi stoppede kun for at få vejret nogle få gange. Træer lå væltede langs vejen, vi måtte kravle under nogle, og over andre. Undervejs sagde de til os, bare gå lige ud til tunnelen, efter tunnelen er jeres folk. Tunnelen var ikke til at se. Efter hvert sving kom endnu et. På den ene side en flod, på den anden bjergside, og i toppen kunne man se lidt himmel. ",
          "",
          "Mørket var begyndt at falde, da vi endeligt så en tunnel. Da vi kom igennem tunnelen og lidt længere fremme så vi min tante Safeta, min Niska og Niho. Da Niska så mig skreg hun, jeg frøs igen. Jeg husker ikke at nogen nogensinde havde holdt mig så tæt som hun gjorde, eller rystede så meget som hun gjorde. Hun gentog kun grådkvalt “min far er her ikke, min far er her heller ikke…”  Jeg tænkte at onkel Muhamed også var i en anden lastbil. Da begyndte jeg at græde, men uden lyd. Tårnene løb bare. ",
          "",
          "Derfra gik vi kun så længe vi skulle. Jeg var ikke mig selv. Jeg husker lidt. Billeder af vinduesrammen af bussen som kørte os til Dubrave,  og gennem vinduesrammen den røde himmel. ",
          "",
          "Da vi nåede Dubrave var det allerede blevet mørkt. Mor og jeg vandrede rundt i et hav af ukendte ansigter, ramte ind i hinanden. Nogen løb mod cisternerne, andre mod teltene. Da så mor en enkel pæl med et lys i toppen, og der satte vi os, på en bunke grus. Hvor lyset ikke skinnede fra den ene lygte, eksisterede ikke længere. Det var som om der ikke var andre i verden, der var kun mor og jeg, på en bunke grus, ventende. Mor blev ved med at sige, her kan de se os bedste. Vi vidste hverken hvem hvis nogen overhovedet. ",
          "",
          "Jeg var allerede ved at falde i søvn da jeg hørte nogen råbe vores navne: Husnija! Jasko! ",
          "",
          "Vi svarede ikke, vi troede ikke at det skete. ",
          "",
          "Pludseligt fra mørkt kom min fætter Emir. ",
          "",
          "“Husnija, det er mig! Emir! Kan i ikke kende mig?” sagde han og græd. ",
          "",
          "Han gav os vand at drikke. Men jeg var der kun halvt. Jeg faldt ind og ud af bevidstheden. Jeg husker at jeg vågnede i hans bil ved siden af ham, en kvinde foran os som jeg ikke kunne genkende. ",
          "",
          "Da vi ankom lagde jeg mærke til at det var et hus. Kvinden som havde siddet foran mig, tog mig i hånden og gik med mig op af nogle trapper. Da vi nåede toppen kunne jeg se  2 drenge sidde, og jeg genkendte kvinden som holdte mig i hånden, det var min kusine Kada. ",
          "",
          "Jeg husker ikke hvordan eller hvornår, men pludseligt befandt jeg mig i andet tøj, foran et bord, på hvilken der var en stegt kylling. Jeg var helt væk, med munden vidt åben. Jeg vidste ikke hvad jeg skulle gøre af mig selv. Kiggede på Kada, kiggede på kyllingen. Kada tog et stykke af kyllingen, tog mine hænder og gav mig det mens hun sagde, her min egen, spis. ",
          "",
          "Den stegte kylling og det stykke kød i mine hænder jeg ikke vidste hvad jeg skulle gøre af, er det sidste jeg husker fra den dag. Jeg ved at jeg sov, men hvor, hvornår og hvordan jeg faldt i søvn,  det husker jeg ikke. Jeg kunne ikke længere være til i verden den dag. ",
          "",
          "Første gang jeg kom tilbage til Srebrenica var i 1990. Børne Ambassaden havde arrangeret det besøg, og før jeg vidste det, stod jeg foran mine jævnaldrene på rådhuset, med en mikrofon i hånden, som om jeg burde sige noget efter en af byrådsmedlemmerne havde kommet med sårende bemærkninger. ",
          "",
          "Jeg sagde: Kig på mig, i slog os ikke allesammen ihjel!” ",
          "",
          "Jeg husker at Dusko Tomic, som jeg var kommet med kom løbende, bag ham journalister og kameraer var alle omkring mig. ",
          "",
          "Jeg tilføjede: Jeg er hvad der er tilbage efter min far, med guds vilje, for at vidne om min far og hvad der er blevet gjort mod os, om du kan lide det eller ej. ",
          "",
          "Jeg ved ikke hvor jeg fandt modet. ",
          "",
          "Efter dette, kom vi hurtigt væk derfra, hurtigere end vi var kommet. Jeg nåede ikke at spørge dem om vi kunne stoppe et øjeblik, så jeg kunne se om mine bøger stadig var der, var det noget der var min fars, er der nogen i vores hus, for at se om de havde brændt min kane.  ",
          "",
          "",
          "",
          "Prevoditeljica: Velida Cajdin",
          "Lektorica: Jannie Larsen"
        ]
      },
      Swedish: {
        title: "Jasmin Jusuf Jusufović",
        subtitle: "Den lilla prinsen",
        language: "Swedish",
        paragraphs: [
          "Vid gryningen den 8 juli hördes det ett krossande någonstans i bergen, tillräckligt högt för att väcka oss. Jag flyttade till mitt skydd vid frysboxen, där väggen var som tjockast och skyddade bäst. Under dessa dagar har vi otaliga gånger sprungit till de tjockare väggarna i huset, som vi bedömde i all hast medan vi flydde undan de fallande granaterna. För att förkorta tiden i mitt skydd vid frysboxen, efter den 9 juli började jag räkna granater som kunde höras hela dagen. Ibland föll det upp till trettio granater åt gången. Folk brukade efteråt gå ner på gatan, och alla tycktes följa den oskrivna regeln att det är den första granaten som dödar. Jag kunde också höra den lilla brödbilen, som användes nu för att transportera de sårade. ",
          "Sedan tystades allt runt 12-tiden. Jag gick för att se vad Edo och Dado gjorde. Min pappa var någonstans i grannskapet. När jag gick ner längs Miloseva gatan, det slog mig att pappa kanske hade gått till smeden på andra sidan ån. När jag nådde den öppna delen vid Nasers damm briserade en explosion. Trots faran så var min största oro hur jag ska meddela mamma att jag är OK. Jag sprang till min morbrors hus och skrek att jag måste springa hem för att säga till mamma så att hon inte oroar sig. Min moster lyckades dock hålla mig kvar tillsammans med Edo och Dado i det provisoriska skyddet under bordet. Hon lugnade mig och sa att mamma säkerligen vet att jag är med Edo och Dado. ",
          "Vi försökte trotsa allt och leva ett normalt liv. Tidigt den 10 juli 1995 gick vi upp till andra våningen där farmor bodde, för att städa och göra rent. Jag tog en matta och gick ut på balkongen för att rista den. En ny granat föll ovan vårt hus. Jord och stenar haglade på husets tak, och jag hängde från balkongen i ca 10 sekunder och var skräckslagen och därefter tappade jag förmåga att tala. När det lugnade sig igen, vid lunchtid, hade mamma och pappa bestämt att vi ska till min faster, som visste hur man ”salijeva strava”, det vill säga ”häller ut rädslan”. (Ant. Översätt: Denna sed är väldigt gammal på Balkan och består i att värma upp bly till flytande och sedan kasta den i kallt vatten upprepade gånger. Därmed tros man bli av med trauma och rädsla). Väl där hos min faster kommer jag ihåg hur jag tittade in i kärlet med vattnet och blyet och skeptiskt svarade till min faster: ”var någonstans ser du lejonet”? ",
          "Min faster bodde i Kazani, och vi bodde nere i staden, och avståndet var lång för att gå med tanke på faran med granater. Än idag kan jag inte klura ut hur de utan telefon eller andra kommunikationsmedel lyckades komma överens om att träffas halvvägs, vid busstationen, hos min andra faster. Efter ”salijevanje strave” så var jag dock inte ett dugg mindre rädd. Beskjutningen och granaterna började igen. Edo och Dado, mina kusiner, var hos oss. Edo och jag gömdes i mitt skydd vid frysboxen, och Dado vid den andra tjocka väggen. Någon sa att de beskjuter den Gamla staden. Jag började oroa mig för min kompis Kemo som bodde där. ",
          "På morgonen den 11 juli började beskjutningen tidigt, mycket närmare staden än de tidigare dagarna. Det var varmt. Mamma och pappa packade vissa saker i påsar ifall att vi måste fly igen, så det inte blir tomhänt som den gången vi flydde från Drinjaca. Jag hoppade och packade i min ryggsäck mina böcker, ”Den lilla prinsen” och ”1000 varför/1000 därför”. Beskjutningen fortsatte hela morgonen. Allt fler människor började röra sig springandes från huvudgatan. Vi väntade, visste inte vad vi ska göra, och någon skrek att ”četnici (ant. översätt.: serbiska soldater) har kommit ner till marknadsplatsen”. Vi tog vår packning, pappan tog mig som en ryggsäck och vi nästan flög från det lilla kullen där vårt hus låg ner till gatan och de andra flyende människorna. Vi hörde skottlossningen bakom oss. Springandes nedför gatan, rev jag sönder min högra ärm på det förstörda staketet vid skolan. Staketet förstördes tidigare av en granat som dödade barnen som spelade fotboll. Jag kanske rev mig i armen med men det var inget jag kände av just då, jag bara kände hur mamma tog tag i mig från staketet och sedan sprang vi in till faster Muneveras hus vid busstationen. Där fastnade vi av någon anledning. Pappa sprang tillbaka mot staden, och mamma tog min ryggsäck och hällde ut dess innehåll med mina böcker. Hon tittade på mig, och jag bara skakade med axlarna. Det var då jag blev av med ”Den lilla prinsen”. När jag stod där frusen med blicken på böckerna tröstade mamma mig och förklarade att böckerna skulle vara för tunga om vi tvingas springa för våra liv... jag förblev tyst, och tänkte att det kanske inte hade varit för tungt, men då insåg jag att ingenting hade någon mening längre. ",
          "Senare kom pappa tillbaka, med en säck mjöl på ryggen. Han förklarade att man brutits in i Nasers förråd. Min faster Munevera lagade friterat bröd av mjölet från säcken, och då insåg jag att jag hade glömt att jag inte har ätit något på flera dagar. Jag hade till och med glömt att jag varit hungrig, trots det så kunde jag bara tvinga i mig en enda brödbit. Vi skildes strax därefter. Min pappa lurade och sa att han bara skulle hämta någonting, men sanningen var att han skulle försöka fly genom skogen. Vi träffade min moster Sena och gick vidare med henne. Vid sjukhuset var det en folksamling. Jag såg hur människor hoppade över balkongerna in i höghusen omkring. Vi kom in i ett av dessa höghus. ",
          "Vi stannade inte längre i mörkret i detta hus utan gick vidare men kunde inte komma längre än till sjukhuset. Folk försökte ta sig in i FN:s läger i Vezionica (ant. översätt: Srebrenica var ett av så kallade FN:s skyddade zoner, och FN:s soldater fanns i flera läger i staden), och någon sa att Zulfo Tursun skickar tillbaka pojkarna från sjukhuset då de inte tillåts in i lägret. Där tappade vi kontakt med vår moster Sena. Mamma, farmor och jag stannade framför ett av husen runt sjukhuset, ovanför Vezionica. En granat exploderade i närheten. Farmor kastades över mig först och därefter mamma, för att skydda mig då det haglade glassplittret över oss. Jag minns att jag knappt kunde andas under farmor och mamma. ",
          "Efter granaten så ökade kvinnornas ansträngningar att forcera grinden och staketet runt Vezionica. Vi tre gick också ner till folksamlingen på gatan. Genom oväsen kände jag igen rösten som ropade ”Husnija”. Det var pappa som kommit tillbaka från skogen: ”Jag ska med er, oavsett vad!”. Till slut lyckades man bryta sig igenom staketet. Pappa bar mig in i FN:s läger, och vi gick till garaget avsedd för FN:s fordon. Ytterligare en granat föll ner. Granatsplitter borrade hål i takplåten ovanför garaget och ljuset började sippra in i garaget. Vi tappade både mamma och farmor. Vi hittade inte heller mostern, Edo, Dado, Keka eller morbror Kiko… det var bara oss två. Pappa såg hur FN:s soldater flydde med lastbilar, tog tag i mig och springandes kastade mig på ett av lastbilsflaken.",
          "Han hjälpte också många andra att klättra upp på lastbilens flak, och jag trängdes in i lastbilens kant vid ena sidan. Jag lyckades vända huvudet mot utsida för att kunna andas. I all trängsel kände jag hur pappas och min hand, som fram till dess varit sammanflätade, skildes åt. Människor klamrade sig fast på lastbilen längs vägen, och jag tappade andan. Lastbilen körde långsamt tills den kom till den andra FN bas, den i Batterifabriken. Genom folkmängden trängdes vi till slut in i den mörka fabrikshallen. På vägen dit såg vi farbror Ibro på bår, sårad. ",
          "Allt mer folk trängdes inne i fabriken. Pappa lyckades snappa åt sig en bår så jag kunde sitta på den. Jag satt lutad mot en vägg bredvid den förstörda entrén. Båren var blöt. Av blod. Vi satt på båren, och jag stirrade mot entrédörren i hopp att se mamma. Tiden tappade allt betydelse. Plötsligt, mamma dök upp vid dörren, skrämd och gråtandes, som om hon hade brottats med någon. Hon såg oss direkt, berättade om att FN soldaterna inte lät henne komma in, att hon fick bråka och kämpa med en soldat och till slut lyckades hon få komma in. Pappa gick för att hitta vatten till oss. Han kom med en flaska som stank bensin med lite vatten i. Han sa att han hittat moster, Edo, Dado och farbror Kiko men inte farmor. Mamma sa att farmor var i den andra fabriken, med vår gravida Keka.",
          "Jag kunde inte längre stå ut med allt oväsen, skrik, stod inte ut längre att titta på alla dessa blodiga ansikten och fabrikshallens gråa tillvaro. Jag hittade ett hål i väggen bakom min rygg, stoppade huvudet i, och tror att jag somnade för en kort stund. Jag vaknade några minuter senare. Mamma frågade varför jag inte sover, och jag sa att jag vill gå till moster. Hon svarade att vi ska, men lite senare. Då ville jag gå på toaletten. Hon tog mig genom entrén bakom fabriken där jag såg sårade, blodiga människor, avföring och blodiga trasor och förband. Jag sa att jag inte kan gå på toa och sedan nämnde jag inte det mer.",
          "Lite senare började mamma att söka igenom våra väskor helt panikslagen. Hon tog bestick och sprang för att kasta den. Folk pratade i fabriken att četnici patrullerar i fabriken iklädda FN:s uniformer och bara letar efter den minsta anledningen för att dra ut någon för att skjuta.",
          "”För Guds skull kvinna, vad ska du med det till?” frågade pappa. ”Hur vet jag vart ska vi hamna, och hur det hela ska sluta? Tänkte att vi kanske kan behöva det” svarade hon. Hon hade inga smycken kvar, och då tog hon allt som vi hade av metall och kastade. Till slut gav hon efter för mitt bönande och vi gick för att hitta moster och morbror Kiko. Morbror Kiko var fruktansvärt blek. Han var en person med särskilda behov, men aldrig tidigare har jag sett honom så skrämd. Mamma frågade moster var Senaid är, min unge morbror och hon svarade att han flydde genom skogen. Jag gick till Edo och Dado och satte mig vid dem på en filt på betongen. Det var hårt och kallt. Det var så många ansikten i hallen som vi aldrig tidigare sett, men ändå kändes det som om vi kände dem. Vågor av panik gick genom hallen. Det viskades i hallen att četnici drar ut folk för att döda, våldta och slakta. Mamma och pappa pratade med de omkring oss, sa att allt ska vara bra, att vi borde vara trygga med FN soldater här, men så som natten närmade sig, blev det klart för oss att vi var i dessa fabriker egentligen som i koncentrationsläger. ",
          "Vi tog oss på något sätt igenom natten. Jag stoppade huvudet i det där hålet i väggen igen, mamma lutade sig mot mig för att jag ska känna mig tryggare, och jag lyssnade hela natten på djupa suck, barn som gråter och längre bort från entréns håll hördes det skrik. Jag vet inte om jag kanske drömde, kanske min själ vandrade från fabriken men hela natten såg jag framför mina ögon min hemby Drinjača, varm mjölk från Pahljevici, hallon från Burnica och solnedgång i Konjevic Polje... allt blandades medan lägret borrade in sin stank och sitt mörker i mitt minne.",
          "Oväsen och oro började tidigt på morgonen. Det kom inga besked vad det handlade om, men människor vankade av och an i fabrikshallen. Pappa gick ut för att se vad är det som händer och kom tillbaka lite senare med en tallrik med lite soppa och en genomskinlig brödskiva. Soppan såg ut som grumligt vatten, och vi hade inget bestick heller då vi hade gjort oss av med den. Vi försökte doppa brödet i soppan men det smulades sönder. ",
          "Medan jag funderade hur jag ska äta soppan så berättade pappa för mamma att han varit hos farbror Ibro och att han inte kan komma upp från båren. Han var blek och mager, behövde näringsdropp men fanns inga. Jag frågade om jag kunde gå runt lite. Längre bort såg jag att det inte fanns längre någon vägg och ljuset sipprade in. När jag kom dit, såg jag utanför fabriken en äng full med folk som stektes i den varma julisolen. ",
          "När jag kom tillbaka lyckades jag inte ens berätta vad jag såg. Mamma beordrade mig att sitta ner och en man hukade sig ner framför oss. Han frågade efter våra namn, pappas och mitt. ”De sa åt oss att skriva ner alla män- och pojkars namn” sa han. Pappa sa att jag heter Jasmin Jusufa Jusufović och mannen skrev det ner. Å, hur stolt var jag när pappa uttalade mitt namn. Jag tänkte “ja, jag är min pappas son och ingen kan göra mig något”. När mannen som skrev listan gick skrek en annan man från vänstra sidan att det inte var FN soldater som ville ha våra namn utan četnici. Jag frågade pappa varför mannen skrev ner bara våra namn. ”Jag vet inte, kanske för att veta hur många finns här” svarade pappa.",
          "”Men varför skrev han inte mammas namn? Har han skrivit Nicu? Och Keku och farmor? Vem ska skriva in Kiko?” frågade jag. ”Jag vet inte, mitt kära barn, jag vet inte!” svarade pappa. Mamma och pappa pratade med folk omkring oss. Det kom ett rykte till vår vrå i hallen om att četnici har blandat sig med oss och att de drar ut folk till ett vitt hus bredvid fabriken där de dödar och våldtar, därefter tillät inte mamma mig inte längre att gå därifrån, inte ens för att få lite frisk luft. Jag bönade och bad “snälla mamma, bara jag får lite friskt luft!” men mamma sa bara ”Nej, sitt här”. “Snälla mamma, bara jag ser solen.” bad jag “Nej!” svarade hon. “Men mamma, kan jag gå till Nica för att se Edo och Dado, vi två kan gå...?” fortsatte jag tills hon till slut gav efter och vi gick dit. När vi kom så gick jag lite längre till hörnet men vände mig hela tiden för att vinka till mamma och bekräfta att vi har ögonkontakt. Plötsligt hördes skrik och genom oväsen hörde jag vad en kvinnoröst bakom staketet hörde. Förskräckt sprang jag tillbaka till mamma så fort att jag trodde att jag skulle spola tillbaka tiden.",
          "”Vad är det för skrik?” frågade mamma. ”Jag vet inte men jag hörde hur en kvinna skrek att han trampade ner barnet” svarade en kvinna. ”Vem trampade på barnet, vems barn?” frågade mamma ”Jag vet inte mamma, jag bara hörde hur en kvinna skrek ”Han trampade på barnet” sa jag. Plötsligt blev det helt tyst, till och med viskandet upphörde. Senare spreds det att en četnik tog ett nyligen fött barn från en ammande kvinna och trampade ner det. Jag vågade inte längre ens nämna att gå någonstans, inte ens till mostern utan jag bara stoppade huvudet i mitt hål i väggen och försökte igen framkalla idyllen av Drinjaca. När natten kom så ställde jag frågan, mest till mig själv ”när ska vi gå härifrån?”. Och pappa som trodde att frågan var ställd till honom svarade “Imorgon ska vi gå, lägg dig nu och försök sova”. Den 13 juli väcktes jag av paniken i hallen. Halva folksamlingen i hallen stod upp medan mamma samlade våra de få saker som vi hade och gav till mig min ryggsäck med flaskan som stank bensin ”Ska vi gå nu?” frågade jag men ingen hörde mig. ",
          "Det sus som var ständigt närvarande men jag lyckats ignorera tidigare blev högre den morgonen. Folk kom med nya nyheter som att en kvinna hade svimmat lite längre bort, andra försökte lyfta upp en gammal gubbe, en mamma försökte lugna ner sitt gråtande barn. Jag sa till mig själv att jag inte ska gråta. Jag är en duktig pojke, jag ska inte skrika och oroa mina föräldrar. Alla var redo för att gå någonstans men vi rörde oss inte. Människorna bara svängde och svajade på plats. Kort därefter inne i hallens djup öppnades den stora dörren och folk rann ut som en flod mot den dörren. Vi tog oss inte ut genom dörren utan först tog folkmassans ström oss till mitten av hallen, där moster var förut men de var inte där längre. Plötsligt stannade vi vid en grön lastbil men folketströmmen fortsatte trängas mot dörren. Jag ryckte i pappas hand och sa att jag inte kan andas. Pappa lyfte mig upp på sina axlar, och jag kom upp i lastbilens höjd. Där fanns det folk också. När vi till slut kom ut i solen och värme så bländades jag av ljuset. Jag tog med mina händer för ögonen, och min pappa kände att jag inte längre höll fast i hans axlar, och tog mig ner mellan honom och mamma. Jag sänkte ner min blick mot marken för att vänja mig vid ljuset. ",
          "Vi gick genom en lång väg med soldater var på bägge sidor. Jag såg enbart prydliga kängor och gevärspipor på rad. De puttade och skrek på oss, tvingade oss att springa. Jag lyckades inte vänja mig vid solsken tills vi nått gatan. Framför oss såg vi en lång rad med lastbilar och bussar och soldater på vänstra sidan. Pappa knuffade mamma och mig framför sig, mamma höll fast om mig och försökte gömma mig i sin kjol. Det var svårt att springa så. Vi passerade några lastbilar och bussar fram tills någon skrek “IN MED ER” och framför mig såg jag en trästege lutad mot en lastbil.",
          "Jag tänkte att ju snabbare jag klättrar upp och vi alla är på lastbilens flak desto snabbare kommer allt detta att få ett slut. Ungefär halvvägs på stegen hörde jag ordern: ”Du, klättra ner, Hit!” och hörde rörelse nerifrån. Jag klättrar upp de några få steg som var kvar och vänder mig om. Jag ser mamma stå framför stegen nervöst med skakande med händer och en soldat som knuffar min pappa med geväret nerför gatan. Såg inte längre något annat än oss tre och den soldaten. Jag stelnade helt, soldaten tvingade pappa ner i diket bredvid vägen. En annan soldat skrek på mamma att hon ska klättra upp. Mamma tog mig som var helt förstenad och bar djupare in i lastbilens flak. Jag tittade fortfarande på min pappa. Han stannade vid diket vände sig mot oss som om han ville ta farväl. När han insåg att jag tittar på honom, han satte sitt finger mot munnen, som om han ville säga till mig ”psssst”, och sedan vinkade med handen, ”gå, åk din väg”. ",
          "Jag hörde ingenting, vi kanske skrek men jag hörde ingenting. Jag minns bara att jag inte kunde röra mig. Mina ögon fastnade på min pappa. Jag bet ihop tänderna och kände att jag var på väg att explodera inuti. Tårarna kom och grumlade till min blick mot min pappa. Jag lyckades lyfta upp en arm lite och i mitt huvud såg jag mig vinka till min pappa. Han upprepade bara sin rörelse, ”pssstt, gå, åk...”. Han hade inte sin vanliga basker på huvudet, och jag tänkte att han kommer bli varm utan den i stekande sol. ",
          "Pappa hade på sig en kortärmad beigefärgad tröja med breda azurblå sträck. En sliten läderväst. Svarta byxor och trasiga skor, de enda som han hade i Srebrenica. Under armen höll han fortfarande min röda jacka med huva. Så blev han kvar tittande på oss, ”Psssstt, gå, åk...”. Jag kände som om någon lyfte mig från lastbilen upp bland molnen. Vid diket vid min pappa såg jag många andra. Jag såg det mogna vetet svajande i sol och vind och jag såg ännu fler människor komma ut från fabriken. Några klättrande på andra bussar och lastbilar, jag såg min mamma på lastbilen. Såg henne trycka mig mot sig själv och hur hon gråtandes försökte svepa in mig i sin kjol. ",
          "En yngling hoppade plötsligt in på lastbilen. Han smög sig in bland faten som fanns på lastbilen och kvinnorna omkring kastade på honom vad de hade för att gömma honom. När lastbilen var full, täckte de oss med presenning och total mörker infann sig. När lastbilen började rulla, viskade jag till mig själv ”pappa kommer på en annan lastbil”... ",
          "Lastbilen körde länge och långsamt, luften höll på att ta slut under presenningen. Plötsligt stannade vi. Alla tystnade. Jag hörde hur lastbilens dörr öppnades, och såg en lång träbit öppna en del av presenningen, skrikandes, ”varför har ni stängt presenningen, ni kommer att kvävas.”. Genom den öppna delen såg vi hur serbiska soldater från vägen beskjuter skogen. Jag tänkte på min unga morbror som flydde genom skogen. Samma röst som öppnade presenningen sa ”Titta ut, så ni inte har tråkigt”. Ljuset som sipprade in på lastbilens flak gjorde att jag kunde se en gammal gumma med blodigt förband runt huvudet och jag såg många, många andra kvinnor. Jag kommer inte ihåg om jag såg ett enda barn. Träbiten kom igen och stängde presenningen och lastbilen började köra igen. Långt därefter, lastbilen stannade igen och mamma tvingade mig ner på golvet och täckte över mig tillsammans med andra kvinnor. Jag hörde en röst: ”Finns det några män/pojkar här?” Kvinnorna svarade nej, men då uppstod kalabalik och hördes ”Inga fler finns det var bara han! Det finns inga fler.”. Lastbilen fortsatte åka igen och nästa gång lastbilen stannade fick vi gå av på en väg där på nedre sidan av vägen var en äng och en å, på den övre en annan äng, ett hus och soldater. De visade oss uppför vägen och sa “gå ditåt, där finne era egna”... mamma och jag började gå. Folk sa att denna plats heter Tišča och att vägen leder till Kladanj (ant. översätt. En stad under kontroll av bosniska styrkor). Vi gick i timmar med korta pauser för att vila och hämta andan. Över vägen låg fallna träd så vi var tvungna att klättra över eller krypa under. Från vägkanten sa de att vi ska genom en tunnel och att på andra sidan tunneln kommer vi finna våra egna.",
          "Men tunnel fanns ingenstans att se för bakom varje kurva kom en ny kurva, nedanför vägen fanns ån, ovanför vägen branta klippor och däremellan en bit av himlen. Vid solnedgången nådde vi äntligen till tunneln. Efter att vi gick igenom tunneln, lite längre bort såg vi faster Safeta, mina Niska och Nuha. När Niska såg mig skrek hon till och kramade mig hårt. Gråtandes upprepade hon att ”min pappa finns inte längre, min pappa finns inte längre”. Jag tänkte, då är farbror Muhamed också på en annan lastbil... och då började jag gråta, utan ljud, det var bara tårarna som rann. ",
          "Därifrån gick vi vidare enbart för att vi var tvungna till det. Jag var inte medveten eller närvarande. Kommer väldigt lite ihåg en buss som tog oss till Dubrave och den röda skyn som jag såg genom fönstret. Vi kom sent till Dubrave det var redan mörkt. Vi vandrade mamma och jag, i ett hav av okända silhuetter och krockade med folk. Vissa sprang mot vattencisterner, vissa mot tält. Mamma hittade en gatlykta och där satte vi oss på en sandhög utanför den ljusbelysta cirkeln från gatlyktan och allt sjönk in i mörkret. Som om ingen längre existerade i hela världen. Bara mamma och jag sittandes på en sandhög, väntandes. Mamma sa “här kommer de att se oss direkt”. Vi visste inte vem, och om någon verkligen kommer att se oss. Jag började förlora medvetande när plötsligt någon ropade ut våra namn: Husnija, Jasko! ",
          "Vi kunde knappt tro det. Från mörkret dök ansiktet av min kusin Emir upp. “Det är jag, Emir. Känner ni inte igen mig?“ sa han och grät. Han gav oss vatten. Jag var omtöcknad, bara halvt medveten. Jag kommer ihåg att jag vaknade i hans bil och att jag såg framför oss och bredvid honom en annan kvinna som jag inte kände igen. Vi kom till deras hus och kvinnan från framsätet hjälpte mig upp uppför trapporna. Väl inne i huset såg jag två pojkar och insåg att kvinnan som hjälpte mig var min faster Kada. ",
          "Vet inte hur eller när, men nästa minne är att jag har bytt om till nya kläder och satt vid bordet med en stekt kyckling. Jag visste inte vad jag ska göra. Tittade upp på Kada och på kycklingen. Kada tog en bit av kycklingen gav den till mig och sa “kom igen, ät!“. Den stekta kycklingen, och den bit av kött i mina händer som jag inte visste vad jag ska göra med, är det sista jag kommer ihåg från den dag. Jag vet att jag sov den dagen men inte var och hur länge. Jag var inte psykiskt närvarande den dagen. ",
          "År1999 var första gången jag kom tillbaka till Srebrenica. Barn Ambassaden (ant. översätt. En hjälporganisation aktiv i Bosnien) ordnade det besöket och jag hamnade framför kommunalrådets ledamöter, med en mikrofon i händerna som att det förväntades att jag skulle säga något efter att en av ledamöter sa något sårande och nedlåtande.",
          "Jag sa: “Se på mig. Ni har inte dödat oss alla!“ Jag kommer ihåg att Dusko Tomic (ant. översätt. Ledare av Barn Ambassaden) blev skräckslagen medan journalisterna och kamerorna vände sig mot mig. Jag lade till: “Jag har blivit kvar efter min pappa, med Guds vilja, för att vittna om min pappa och vad vi utsattes för oavsett om ni tycker om det eller inte!“. Jag vet inte varifrån fick jag mod för det. Efter detta fick vi lämna Srebrenica väldigt snabbt. Jag hade inte tid för att se om några av mina böcker fanns kvar, att kolla om det finns kvar något av pappas tillhörigheter, bor någon annan i vårt hus, har de bränt min släde... ",
          "Translated by: @Redeemer_69, @UndercoverBro12, @IlhanaD"
        ]
      },
      Italian: {
        title: "Jasmin Jusuf Jusufović",
        subtitle: "Il piccolo principe",
        language: "Italian",
        paragraphs: [
          "All’alba di quell’8 luglio, da qualche parte nei boschi, stava tuonando abbastanza forte da svegliarci. Mi trasferii quindi nel mio rifugio, accanto al congelatore, dove il muro era più spesso. In quegli ultimi giorni correvamo spesso verso il muro più spesso della casa e, correndo, contavamo le granate. Da quel 9 luglio, per ammazzare il tempo nel mio nido accanto al congelatore, passavo il tempo contando le granate che scoppiavano durante il giorno. Ogni tanto ne scoppiavano trenta alla volta. La gente usciva per strada, ma solo con la regola “Il primo uccide”. Ramo il panettiere guidava per le strade il suo camion con all’interno i feriti.",
          "Gli scoppi cessarono e a mezzogiorno calò il silenzio. Uscii a vedere cosa stessero facendo Edo e Dado. Sapevo che mio papà era da qualche parte nel quartiere. Mentre stavo percorrendo la via Miloševa, pensai che papà fosse andato dal fabbro al di là del fiume. Ci fu uno scoppio di granata mentre mi trovavo al centro di una radura, vicino alla diga di Naser. La mia preoccupazione più grande, in quel momento, fu correre da mia madre, così che vedesse che stavo bene. Correndo verso casa mi fermai a casa di mia zia e urlai: “Vado da mia madre, così non si preoccupa!”. Mia zia riuscì a malapena a tenermi in casa sotto il tavolo, con Edin e Dadin, assicurandomi che mia madre sicuramente sapeva che fossi a casa loro. ",
          "Nonostante tutto, eravamo abbastanza testardi da continuare a vivere le nostre vite normalmente. La mattina del 10 luglio andammo di sopra ad aiutare la nonna a rassettare la camera. Presi una coperta e andai in terrazza per scuoterla dalla polvere. Una granata scoppiò dietro la casa. La terra cadeva sul tetto come pioggia e passai circa dieci secondi attaccato al muro. Quando, intorno a mezzogiorno, cessò la confusione in mezzo ai bombardamenti, i miei genitori decisero di andare a casa di mia zia per compiere un rituale con del piombo, una vecchia tradizione che si credeva potesse aiutare a superare la paura e i traumi emotivi, visto che non avevo detto una parola dal momento dello scoppio di quella granata. Credevano che mi fosse successo qualcosa. Durante il rituale guardavo dentro la boccia con il piombo e l’acqua e continuavo a chiedere a mia zia: “Ma dove lo vedi il leone?”",
          "Mia zia viveva a Kazani, mentre noi vivevamo in paese, che era abbastanza lontano per essere raggiunto a piedi sotto le bombe. Ancora oggi non mi è chiaro come riuscivano a dirsi, senza un telefono o qualunque altro mezzo, che ci saremmo incontrati a casa della mia altra zia, vicino alla stazione dei bus, a metà strada da casa loro.",
          "Quando il rituale del piombo finì, non mi sentii meno spaventato. Poi i bombardamenti ricominciarono. Edo e Dado erano a casa nostra. Edo ed io eravamo nel mio rifugio vicino al congelatore, Dado vicino all’altro muro spesso. Qualcuno disse che le bombe stavano scoppiando per tutta la città vecchia, e iniziai a preoccuparmi per il mio amico Kemo. ",
          "La mattina di quell’11 luglio, gli spari iniziarono più presto ed erano più vicini che mai. Era un giorno caldo. Mamma e papà impacchettavano la nostra roba in borse di tela, così che, se mai fossimo dovuti fuggire, non andassimo a mani vuote come quando avevamo lasciato Drinjača (un villaggio sul fiume Drina, nella municipalità di Zvornik, a circa 40 km da Srebrenica). Mi sono alzato e ho preparato il mio zainetto con “Il piccolo principe” e l’enciclopedia “1000 domande - 1000 risposte”. I bombardamenti continuarono per tutta la mattina. Sempre più persone andavano via dalla città, correndo. Noi, follemente, stavamo aspettando qualcosa. Poi qualcuno dalla folla gridò: “I cetnici sono arrivati sulla piazza del mercato!”. Prendemmo le nostre borse, mio padre prese anche me come se fossi una borsa, e scappammo dalla collina, dov’era la nostra casa, per unirci alla folla di gente che fuggiva. Alle nostre spalle potevamo sentire gli scoppi.",
          "Mi strappai la manica su una recinzione, mentre correvo. Era lo stessa recinzione spazzata via da una granata, che aveva ucciso dei bambini che giocavano a calcio di fronte alla nostra scuola. Forse mi ferii anche un braccio, ma al momento non sentii nulla. L’unica cosa che vidi fu mia madre che mi strappò da quella recinzione e poi corremmo a casa di mia zia Munevera. Per qualche motivo ci fermammo. Papà corse indietro verso il paese, mia mamma mi tolse lo zainetto dalle spalle, scosse via i libri e mi guardò. Io alzai solamente le spalle. Fu così che persi il mio Piccolo Principe. Quando mi vide così, congelato, mi consolò dicendo che i libri sarebbero stati troppo pesanti sulla mia schiena mentre fuggivamo. Tra me e me pensai che forse non sarebbero stati proprio così pesanti, ma rimasi in silenzio, niente sembrava avere più senso. ",
          "Un po’ dopo, mio padre venne a casa portando un sacco di farina bianca sulla schiena. Ci disse che alcune persone avevano fatto irruzione nel magazzino di Naser. Zia Munevera cucinò le frittelle. Avevo completamente dimenticato che non mangiavo da giorni e che ero affamato. Ne mangiai a malapena una. Presto ci separammo. Mio padre mi ingannò, dicendo che stava andando a prendere alcune cose e che poi ci avrebbe raggiunto, ma invece andò verso i boschi, in direzione di Tuzla. Incontrammo zia Sena e proseguimmo. C’era una folla intorno all’ospedale. Vidi alcune persone saltare giù dai balconi degli edifici vicini. Entrammo in uno di essi.",
          "Non rimanemmo per molto tempo nell’oscurità di quel palazzo. Andammo avanti, ma non potevamo andare oltre l’ospedale. La gente cercava di entrare nel campo delle Nazioni Unite a Vezionica e qualcuno disse che Zulfo Tursun stava riportando indietro i ragazzi dall’ospedale. Qui perdemmo mia zia. Mia madre, mia nonna e io ci fermammo sotto uno degli edifici vicino all’ospedale, sopra Vezionica. Una granata esplose lì vicino. Prima mia nonna e poi mia madre mi coprirono, cercando di proteggermi dai vetri che cadevano dal palazzo. Ricordo che riuscivo a malapena a respirare, sotto di loro. ",
          "Quando la bomba esplose, le donne cercarono di forzare i cancelli e le recinzioni di Vezionica. Noi tre ci unimmo alla folla in strada. All’improvviso, in mezzo al rumore, sentii una voce familiare: Husnija!!!",
          "Mio padre era tornato dai boschi. “Verrò con voi, vada come deve andare”. Una parte della recinzione di Vezionica finalmente si aprì. In qualche maniera mio padre mi portò dentro il campo ed entrammo nel garage dei veicoli dell’ONU. Un’altra bomba esplose. Schegge di proiettile fecero piccoli buchi attraverso le mura sottili del garage. Il sole filtrò. Avevamo perso mia madre. Avevamo perso mia zia. Non c’era la zia, né Edo, né Dado, né Keka, né zio Kiko. Solo noi due. Papà vide i soldati dell’ONU uscire da Vezionica con i loro camion. Mi prese e mi gettò nel rimorchio di un camion.",
          "Aiutò molti altri a salire. La calca mi spinse ai lati. Riuscivo a malapena a girare la testa verso l’esterno, così da poter respirare. Realizzai che io e mio padre non ci tenevamo più per mano. La folla ci aveva separati. La gente continuava a salire sul camion e avevo sempre meno aria. Il veicolo giunse lentamente a un’altra base, la fabbrica “Akumulatorka”. Mio padre e io ci ritrovammo nell’oscurità del magazzino. Lungo la strada, vedemmo zio Ibro. Era su una barella. ",
          "La folla iniziò a riversarsi dentro la fabbrica. Papà riuscì in qualche modo a trovare una barella, così che potessi sedermi per un po’. Mi sedetti contro il muro, vicino all’entrata distrutta. La barella era bagnata. Bagnata di sangue. Mentre sedevo lì, i miei occhi erano puntati sulla porta, con la speranza di vedere entrare mia madre. Persi il senso del tempo. All’improvviso, apparve mia madre. Era tutta rossa, come se avesse appena fatto a botte. Ci individuò subito. Disse che gli uomini della UNPROFOR (la Forza di protezione delle Nazioni Unite) non volevano farla entrare, quindi aveva dovuto litigare con un soldato. ",
          "In qualche modo riuscì ad entrare. Papà andò a cercare dell’acqua. Portò una bottiglia che puzzava di benzina, con un po’ d’acqua dentro. Ci disse di aver ritrovato la zia, Edo e Dado, ma la nonna non si trovava da nessuna parte. Mamma disse che la nonna era rimasta in un’altra fabbrica con la nostra Keka, che era incinta.",
          "Non sopportavo più le urla, la visione delle facce insanguinate e il grigiore di quella fabbrica. Mi appoggiai a una rientranza nel muro e credo di essermi semi-addormentato. Mi risvegliai pochi minuti dopo. Mia madre mi chiese perché non dormivo. Voglio vedere la zia. Ci andiamo dopo. Allora voglio andare al bagno. Mi portò dietro la fabbrica. C’erano persone insanguinate e lacerate, un sacco di feci e bende inzuppate di sangue dappertutto. Dissi che non riuscivo ad andare in bagno e non ci andai più.",
          "A un certo punto, mia madre iniziò a cercare in mezzo alle nostre borse, nel panico. Prese una manciata di argenteria e corse a gettarla via. Dentro la fabbrica si sparse la voce che i cetnici stavano pattugliando la fabbrica vestiti da soldati dell’ONU e stavano cercando una scusa per portare fuori le persone e fucilarle. ",
          "Papà le chiese perché, in nome di dio, avesse quell’argenteria con sé.  ",
          "“Come posso sapere dove andremo dopo e come staremo. Ho pensato che potesse esserci utile”.",
          "Tutti i suoi gioielli erano ormai persi e ora stava gettando tutto il metallo che possedevamo.",
          "Capì che non poteva più rifiutare le mie richieste, quindi, quando la gente smise di entrare, andammo a trovare la zia e lo zio Kika. Lo zio Kika era spaventosamente pallido. Era una persona con bisogni speciali, ma non era mai stato così spaventato come allora. Mia madre chiese alla zia dove fosse il mio giovane zio Senaid. La zia rispose che era fuggito attraverso i boschi. Mi avvicinai a Edo e Dado, mi sedetti vicino a loro su una coperta che copriva il cemento, che era duro e freddo. ",
          "Così tante persone mai viste prima erano ora intorno a noi. Non le avevamo mai viste, ma ci sentimmo come se le conoscessimo da sempre. Attraverso il capannone echeggiavano ondate di panico. Alle nostre orecchie arrivò la notizia che stavano portando fuori alcune persone e le stavano massacrando, stuprando e fucilando. Mamma e papà mormoravano con la gente intorno a noi che tutto sarebbe finito presto, che saremmo stati in salvo con le truppe dell’UNPROFOR, ma - mentre faceva buio - diventò evidente che stando dentro queste fabbriche eravamo, in realtà, in un campo. ",
          "In qualche modo passammo la notte… Misi di nuovo la testa dentro quella rientranza nel muro, mia madre si appoggiò su di me, come per coprirmi, per farmi sentire al sicuro. Tutto quello che sentii durante la notte furono gemiti, i pianti dei bambini e, da qualche parte lontano, dietro la fabbrica, le urla. Non so se sognai o se lasciai quel capannone con lo spirito, ma per tutta la notte davanti ai miei occhi ci furono Drinjača, il latte caldo di Pahljevic, i lamponi di Burinca, il tramonto a Konjevic Polje… tutto si mescolava casualmente nella mia testa, mentre la fabbrica instillava il suo odore e la sua oscurità nei miei ricordi.",
          "Il trambusto e i rumori iniziarono molto presto la mattina. Nessuno ci disse cosa fosse successo, ma la gente si aggirava per il capannone, andando qua e là. Mio padre saltò su e uscì dalla porta per vedere cosa stesse succedendo. Più tardi arrivò con un piatto sottile, con una specie di zuppa e una fetta quasi trasparente di pane. La zuppa sembrava acqua sporca, il piatto era poco profondo, avevamo buttato via le nostre stoviglie e non potevamo nemmeno berla. Volevamo raccogliere la zuppa con il pane, ma si era sciolto al suo interno.",
          "Mentre mi arrovellavo su come mangiare quel po’ di zuppa, mio padre raccontò a mia madre di come fosse stato con lo zio Ibro e di come lui non potesse ancora alzarsi dalla barella sulla quale lo avevamo visto il giorno prima. ",
          "Disse che era molto pallido e si era “prosciugato”. “Nessuno poteva fargli un’infusione”. ",
          "Chiesi a mamma e papà di lasciarmi andare a fare una passeggiata. Sul lato destro, da lontano, vidi che non c’era il muro e che entrava la luce. Quando arrivai a quel punto vidi un campo pieno di gente, proprio come la gente dentro la fabbrica, che cuoceva al sole nel campo aperto. ",
          "Quando tornai, non riuscii a raccontargli cosa avevo visto. Mia madre mi ordinò di sedermi e un uomo si accovacciò di fronte a noi. Ci chiese i nostri nomi, il mio e quello di mio padre. “L’UNPROFOR ci ha chiesto di segnare solamente gli uomini” ci disse. Papà disse che io ero Jasmin Jusufa Jusufović e lui scrisse. Oh, come fui orgoglioso quando mio padre pronunciò il mio nome in quel modo. Pensai, sono il figlio di mio padre, e nessuno potrà farmi nulla. Quando l’ispettore andò via, un uomo esclamò “Ma quali UNPROFOR… Cetnici!”",
          "Papà, perché quell’uomo ha scritto solo i nostri due nomi?",
          "Non lo so, forse per sapere quante persone ci sono qui…",
          "Ma perché non ha scritto anche il nome della mamma? Scriverà il nome di Nica? Avrà scritto Keka e nonna? Chi scriverà il nome di Kika?",
          "Non lo so, figliolo mio.",
          "Mamma e papà stavano parlando con le persone intorno a noi. A noi nell’angolo arrivò la voce che avessero mandato dei cetnici in mezzo a noi, che stessero portando le persone in una casa bianca vicino alla fabbrica, per stuprarle e fucilarle. Ecco perché mia madre non mi aveva fatto andare da solo in quel campo, fuori all’aria aperta.",
          "Per favore mamma, usciamo, prendiamo un po’ d’aria.",
          "Non voglio, siediti.",
          "Per favore mamma, solo per vedere il sole per un attimo.",
          "Non voglio, siediti!",
          "Ma mamma, andiamo da Nica, a vedere Edo e Dado, andiamo insieme.",
          "Quando siamo arrivati…",
          "Per favore mamma, voglio andare solo per poco in quell’angolo laggiù, mi vedrai dall’angolo di Nica. ",
          "Ogni mezzo minuto mi voltavo verso mia madre per farle un cenno ed essere sicuro che ci vedessimo ancora. Poi dal fondo della recinzione sentii grida, urla, e riuscii a capire che cosa stesse urlando quella voce femminile. Corsi da mia madre, così in fretta che pensai di poter tornare indietro nel tempo. ",
          "“Cos’è successo, cos’è stato quell’urlo?” “Non lo so, una donna stava urlando: ‘Sta calpestando suo figlio’”",
          "“Chi ha calpestato il figlio di chi?” “Non lo so mamma, ho solo sentito una donna che urla ‘Sta calpestando suo figlio’”",
          "Improvvisamente un silenzio da funerale cadde sulla gente. Tutti si ammutolirono, per un po’ non si sentì nemmeno un sussurro.",
          "Più tardi, per passaparola, giunse la notizia che un cetnico aveva preso un neonato dalla donna che l’aveva appena dato alla luce vicino alla recinzione e l’aveva calpestato.",
          "Non osai più dire che avrei voluto andare via. Non osai più chiedere neanche di andare dalla zia. Cercai solo quel buco nel muro per rifugiarvi la mia testa e immaginare Drinjača.",
          "Quando si fece di nuovo notte, dissi, pensando di essere da solo: quando ce ne andremo da qui? Papà mi sentì, e pensando che avessi chiesto a lui rispose: “Domani ce ne andremo. Adesso dormi!”. Il 13 luglio, mi svegliò il panico nel capannone. Metà della gente nel capannone era in piedi, mamma stava di nuovo raccogliendo le poche cose che avevamo, e mi mise vicino il mio zainetto, in cui era rimasta solamente quella bottiglia che puzzava di benzina. ",
          "“Stiamo andando?”, chiesi. Nessuno mi sentì.",
          "Il mormorio che gli altri giorni avevo potuto ignorare, come se fosse un rumore costante, quel mattino si era fatto più forte. La gente portava nuove notizie, sulla nostra sinistra una donna aveva ripreso conoscenza, un anziano era stato rimesso in piedi, una mamma cercava di far fare silenzio al figlio piccolo che urlava.",
          "Dicevo a me stesso che non mi sarei comportato così. Che ero bravo, che non avrei urlato e pianto, che non avrei dato fastidio a mamma e papà. Tutti erano pronti per andare da qualche parte, ma non ci muovevamo. Quelli che si erano alzati in piedi si giravano e tornavano nello stesso posto. ",
          "Poco dopo, al fondo del capannone, dalla stessa parte da cui eravamo entrati, il portone si aprì, e la gente, come un fiume, iniziò a muoversi verso quella unica uscita. Noi non andammo verso quella porta, ma fu la folla a spingersi verso il centro del capannone, nel posto dove prima era seduta la zia, ma loro non c’erano più.",
          "Improvvisamente ci fermammo vicino a un camion verde, ma la gente continuava a spingere verso la porta. Tirai la mano di mio padre e cercai di gridargli: “Papà, non riesco a respirare!”. Lui mi sollevò e mi mise sulle spalle. Ero alla stessa altezza del camion. E sul camion c’era gente. Quando ci muovemmo, e uscimmo fuori, il sole e il calore mi accecarono. Strinsi gli occhi. ",
          "Papà si accorse che non mi stavo più tenendo a lui, così mi mise tra lui e mamma, e mi tenevano per mano. Abbassai lo sguardo verso le gambe per fare abituare gli occhi alla luce.",
          "Camminammo lungo una lunga fila di soldati. Vidi solo gli stivali, le gambe delle uniformi militari, i calci dei fucili. Ci ordinarono, gridando, che dovevamo cominciare a correre. Non mi abituai alla luce del sole finché non arrivammo sulla strada. Davanti a noi si presentava una colonna di camion e autobus, con i soldati sulla sinistra. Papà spinse me e la mamma davanti a lui, mamma mi teneva forte, avvolgendomi coi suoi vestiti. Era difficile correre così. Passammo uno, poi un altro autobus e un camion, finché qualcuno urlò “ENTRA!”, e mi trovai davanti a una scala di legno appoggiata al camion.",
          "Pensai che, quanto più veloce avessi salito quella scala, quanto prima tutti e tre saremmo saliti sul camion, tanto prima tutto sarebbe finito. A metà della scala, dietro di me sentii un ordine: “Tu, scendi! Qui!” e sentii un trambusto. Riuscii a salire gli ultimi pioli e mi girai.",
          "Mamma era seduta davanti alla scala, agitando le braccia, mentre giù per la strada un soldato spingeva mio papà con il fucile. Non vedevo più niente al di fuori di noi tre e di quel soldato. Mi sentii paralizzato. Papà fu spinto da quel soldato in un fosso a lato della strada, mentre un altro soldato urlava a mamma di salire. Mamma mi afferrò mentre ero impietrito e mi spinse dentro il camion. Continuai a guardare papà. Era in piedi nel fosso. Si girò verso di noi, come per accompagnarci con lo sguardo. Quando vide che lo guardavo, mise il dito davanti alla bocca come per dirmi “ssssh”, e agitò la mano come per dire “vai”. ",
          "Non sentivo, forse stavamo urlando, ma non sentivo. Mi ricordo solo che non riuscivo a muovermi per niente. Gli occhi rimanevano su mio padre. So che strinsi i denti forte, e che dentro di me volevo esplodere. Da lì mi vennero le lacrime agli occhi che annebbiarono la visione di mio padre. Riuscii solo ad alzare un po’ una mano, e nella mia testa pensai che lo stessi salutando. Lui ripeté solamente quel gesto, “Sssh! Vai”. Non aveva il suo solito berretto sulla testa. Pensai che avrebbe avuto caldo con quel sole.",
          "Indossava una maglia a maniche corte, color crema, con larghe strisce azzurre. Un giubbotto di pelle consumato. Pantaloni neri e scarpe consumate, le uniche che aveva a Srebrenica. Sotto il braccio teneva la mia giacca rossa col cappuccio. E così rimase, guardandoci, “Sssh, vai”.",
          "In quel momento fu come se qualcuno mi avesse preso dal camion e portato in cielo. Vidi che nel canale, attorno a mio padre c’erano molti più uomini. Dietro di loro vidi il grano maturo cullato dal sole e dal vento, mentre dal capannone usciva ancora gente, alcuni salivano sull’autobus. Vidi mamma sul camion come mi teneva, mi avvolgeva nel suo vestito e piangeva.",
          "Da qualche parte un ragazzo saltò sul camion. Strisciò dietro alcuni barili che erano lì, e le donne gli lanciarono le fasce che avevano con sé. Quando il camion si riempì di gente, ci chiusero con un telone, e cadde totale oscurità, per cui di nuovo non vedevo nulla. Quando il camion partì, mormorai tra me e me “Papà sarà in un altro camion…”.",
          "Il camion ci guidò a lungo e lentamente da qualche parte. Sotto il telone ci mancava l’aria. Improvvisamente ci fermammo. Tutti tacemmo. Sentii le porte del camion, e poi vidi un lungo asse di legno che apriva una parte del telone e gridava: Perché vi siete chiusi da soli, vi soffocherete, che dio vi maledica. Da quella parte aperta vedemmo che dalla strada su cui eravamo, da qualche parte di fronte a noi, bombardavano verso il bosco. Mi ricordai del mio zio più giovane, che era scappato per i boschi. Quell’asse di legno parlò di nuovo: Ehi, guardate un attimo, così non vi annoiate. In quello squarcio di luce attorno a me vidi un’anziana con la testa avvolta in una specie di benda insanguinata, e vidi che anche i suoi occhi erano coperti dalla benda, e poi tante donne. Non ricordo di avere visto un solo bambino.",
          "L’asse di legno chiuse di nuovo il telone e il camion riprese la marcia. Molto dopo, il camion si fermò di nuovo, e mamma mi mise per terra, coprendomi con alcuni oggetti. Altre donne posarono altre cose su di me e poi avvertii qualcosa di molto pesante sopra tutte queste cose. Poi ascoltai una voce.",
          "“Ci sono maschi qui?” Le donne risposero che non ce n’erano, ma poi ci fu un urlo. “Non c’è più nessuno, c’era solo lui! Non c’è più nessuno!”. Poco dopo, il camion ripartì. Quando ci fermammo di nuovo, ci scaricarono dal camion su una strada, sotto c’era un prato e un fiume, e sopra un altro prato, una casa e i soldati.",
          "Ci mostrarono il cammino lungo la strada e ci dissero, ora andate su, su ci sono i vostri. Mamma e io siamo andati per la strada, eravamo solo lei e io. Dalla gente sentimmo che il posto in cui eravamo si chiamava Tišča. La strada su cui ci avevano diretti andava verso Kladanj. Camminammo per ore e ore, fermandoci ogni tanto per riprendere fiato. Lungo la strada c’erano alberi abbattuti, e dovemmo scavalcarne alcuni e passare sotto ad altri. Dai lati della strada ci dicevano: tutto dritto fino al tunnel, dopo il tunnel ci sono i vostri.",
          "Il tunnel non si vedeva da nessuna parte, dopo ogni curva c’era un’altra curva, sotto la strada il fiume, sopra la strada la gola, e nella fessura della gola un po’ di cielo. Già iniziava a calare l’oscurità, e finalmente scorgemmo il tunnel. Attraversando il tunnel e, poco oltre, incontrammo mia zia Safeta, Niska e Niho. Quando mi vide, Niska si mise a urlare. La gola attorno a noi fu scossa dal suo urlo, e io rimasi paralizzato. Non ricordo che nessuno mi abbia mai abbracciato così forte, scuotendomi così tanto, come Niska in quel momento. Ripeté tra le lacrime: “Non c’è papà, non c’è mio papà”… Pensai che anche zio Muhamed fosse su un altro camion… E poi cominciai a piangere. Senza voce. Scendevano solo lacrime.",
          "Da lì camminammo solo perché dovevamo farlo. Non ero in me. Ricordo solo in alcuni frammenti il finestrino dell’autobus che ci portava a Dubrave, e il cielo rosso nell’angolo di quel finestrino. ",
          "A Dubrave arrivammo che era già notte. Mamma e io ci aggiravamo a fatica in un mare di sagome sconosciute. Ci scontravamo con le persone. Alcuni correvano verso le cisterne, altri verso le tende. Poi mamma vide un lampione e ci sedemmo lì, su un mucchio di ghiaia. Fuori dal cerchio di luce del lampione, improvvisamente tutti caddero nell’oscurità, come se non ci fosse nessun altro nel mondo. Solo mamma e io, seduti su un mucchio di ghiaia, aspettando. Mamma mi disse: “Qui ci vedranno per primi”. Non sapevamo né chi ci avrebbe visti, né se ci avrebbe davvero visti. Stavo iniziando a perdere i sensi… quando improvvisamente qualcuno urlò i nostri nomi: Husnija! Jasko! ",
          "Non ce ne accorgemmo, non ci credemmo. Eppure, da quell’oscurità davanti a noi apparve la faccia di mio cugino Emir. “Husnija, sono io! Emir! Non mi riconosci!” diceva, e piangeva. Ci diede dell’acqua da bere. Ma io in certi momenti c’ero, in altri non c’ero. Mi ricordo di essermi svegliato nella sua macchina, e io ero al suo fianco, davanti a noi un’altra donna che non riuscivo a riconoscere. Vagando tra stato di coscienza e incoscienza, mi accorsi che ci avevano portato a casa. La donna del posto davanti mi fece salire dei gradini. Quando mi colpì la luce nella stanza al fondo della scala, vidi che dentro c’erano due ragazzi, e riconobbi che mi stava tenendo la mano mia cugina Kada.",
          "Non so come, né quando, ma improvvisamente mi sono trovato vestito con abiti diversi, piazzato davanti a una tavola su cui c’era un pollo arrosto. Rantolai, persi il controllo, non sapevo cosa fare. Guardai Kada, poi quel pollo… Kada prese un pezzo di pollo, mi prese la mano, ci mise dentro quel pezzo e disse, - Dai, è pollo, mangia -. Quel pollo arrosto, e quel pezzo di carne nelle mie mani con cui non sapevo cosa fare, è l’ultimo ricordo di quella giornata. So che dormii, ma non mi ricordo come, dove e quando mi addormentai. Non riuscii più a essere presente in questo mondo quel giorno.",
          "Tornai a Srebrenica per la prima volta nel 1999. L’ “Ambasciata dei Bambini” organizzò quella visita e improvvisamente mi trovai davanti i consiglieri comunali, con un microfono in mano, per dire qualcosa dopo l’intervento offensivo di una consigliera.",
          "Dissi: “Guarda! Non ci avete ucciso tutti!” Mi ricordo che Duško Tomić, con cui ero venuto, impallidì, mentre giornalisti e camere si concentravano tutti improvvisamente su di me. Aggiunsi: “Sono sopravvissuto a mio padre, per volontà di dio, così che potrò testimoniare su mio padre e su ciò che è stato fatto a lui, che vi piaccia o no”. Non so da dove presi il coraggio. Dopo questo mi afferrarono, e ce ne andammo molto più in fretta di come eravamo arrivati. Non ebbi tempo di chiedere loro di restare ancora un po’, di guardare se da qualche parte c’erano ancora i miei libri, se c’era ancora qualcosa di papà, se c’era qualcuno a casa nostra, se avevano bruciato la mia slitta da neve.",
          "",
          "Traduzione a cura di Alfredo Sasso e Maria Baldovin"
        ]
      },
      German: {
        title: "Jasmin Jusuf Jusufović",
        subtitle: "Der kleine Prinz",
        language: "German",
        paragraphs: [
          'In der Morgendämmerung, am 8. Juli, donnerte es irgendwo im Wald, laut genug, um uns aufzuwecken. Dann zog ich in mein Obdach neben dem Gefrierschrank, wo sich die dickste Wand befand. Während dieser paar Tage rannten wir so oft zur dicksten Wand im Haus  und während des Laufens maßen wir die Granaten. Um die Zeit in meinem Obdach neben der Gefriertruhe zu verkürzen, habe ich ab dem 9. Juli einige Zeit damit verbracht, die Granaten zu zählen, die den ganzen Tag explodierten. Manchmal waren es dreißig. Die Leute gingen auf die Straße, aber nur mit der Regel "Die erste tötet". Ramo, der Brotverkäufer, donnerte mit den Verwundeten in seinem TAM-Truck durch die Straßen.',
          "",
          "Die Granaten hörten auf und gegen Mittag verstummte alles. Ich ging raus, um zu sehen, was Edo und Dado taten. Ich wusste, dass mein Babo (Babo = Papa) irgendwo in der Nachbarschaft war. Als ich die Milos-Straße runterkam, dachte ich, Babo wäre vielleicht zum Schneider über den Fluss gegangen. Eine Granatendetonation ging los, als ich in der Mitte des Abstandes neben Nasers Damm war. Die größte Sorgen in diesem Moment  war, zu meiner Mutter zurückzulaufen, damit sie zumindest sehen konnte, dass es mir gut ging. Als ich nach Hause rannte, rannte ich in das Haus meiner Tante und schrie: „Ich gehe zu meiner Mutter, damit sie sich keine Sorgen macht!“ Meine Tante schaffte es gerade, mich mit Edin und Dadin unter dem Tisch im Schutz zu halten und sagte dass meine Mutter wusste, dass ich mit ihr war.",
          "",
          'Trotz allem waren wir hartnäckig genug, um ein normales Leben zu führen. Am Morgen den 10. Juli gingen wir nach oben, um Nana (Nana = Großmutter) zu helfen, ihr Zimmer sauber zu machen. Ich streckte mich und ging auf die Terrasse, um es abzuwischen. Eine Granate detonierte hinter dem Haus. Die Erde fiel wie Regen auf das Dach, und ich hing ungefähr zehn Sekunden lang an der Wand. Als sich die Verwirrung zwischen dem Beschuss gegen Mittag beruhigte, beschlossen meine Eltern, zur meiner Tante zu gehen, um ein Ritual des "Bleigießens" durchzuführen, das nach alter balkanischer Tradition bei Angst und emotionalem Trauma helfen soll, da ich kein Wort gesprochen hatte, seit diese Granate gefahllen hatte. Sie dachten, mir ist etwas passiert. Während des Rituals schaute ich Blei und Wasser in der Schüssel und fragte meine Tante immer wieder: "Wo auf der Welt siehst du einen Löwen?"',
          "",
          "",
          "Meine Tante lebte in Kazani, während wir in der Stadt lebten, die weit davon entfernt war, unter der Gefahr des Beschusses zu laufen. Bis heute ist mir nicht klar, wie sie ohne ein Telefon oder eine andere Verbindung miteinander kommunizieren konnten, dass wir uns in der Nähe der Bushaltestelle auf halbem Weg zu ihrem Haus treffen sollten. Als das Ritual des Bleigießens beendet war, hatte ich nicht weniger Angst. Dann fing der Beschuss wieder an. Edo und Dado waren bei uns zu Hause. Edo und ich waren in meinem Schutz vor der Gefriertruhe und Dado an der anderen dicken Mauer. Jemand sagte, die Granaten würden in der Altstadt explodieren, und ich begann mir Sorgen um meinen Freund Kemo zu machen.",
          "",
          'Am Morgen des 11. Juli begannen die Dreharbeiten so früh wie nie zuvor. Es war ein heißer Tag draußen. Mama und Babo packten unsere Sachen in Stoff-Einkaufstüten, und wenn wir gingen, gingen wir nicht mit leeren Händen wie zu dem Zeitpunkt, als wir Drinjača verließen. Ich sprang und packte meine Schultasche mit dem Kleinen Prinzen und der Enzyklopädie "1000 zašto – 1000 zato" (1000 warum - 1000 darum). Der Beschuss dauerte den ganzen Morgen. Immer mehr Menschen verließen die Stadt und rannten davon. Wir haben dummerweise auf etwas gewartet. Dann schrie jemand aus der Menge: "Die Tschetnik sind auf den Marktplatz gekommen!" Wir packten unsere Taschen, mein Vater packte mich und wir flohen von dem Hügel, auf dem sich unser Haus befand, schließten wir uns der Menge der Menschen an, die fliehen. Hinter uns konnten wir Schüsse hören. Während ich die Straße entlang rennte, reiße ich meinen rechten Ärmel an einem Zaun. Derselbe Zaun, der von einer Granate gesprengt wurde, bei der auch Kinder getötet wurden, die ein Fußballturnier vor unserer Schule spielten. Ich habe mir vielleicht auch den Arm geschnitten, aber ich habe es damals nicht gespürt. Das einzige, was ich sah, war, wie meine Mutter mich aus dem Zaun ',
          "genommen hat und wir in das Haus meiner Tante Munever an der Bushaltestelle gerannt sind. Wir haben aus irgendeinem Grund angehalten. Babo rannte zurück in die Stadt und Mutter nahm meinen Rucksack, schüttelte meine Bücher aus und sah mich an. Ich zuckte nur die Achseln. So wurde mein Kleiner Prinz zurückgelassen. Als sie sah, dass ich über meinen Büchern gefroren war, tröstete sie mich damit, dass sie auf meiner Flucht zu schwer in meinem Rücken gewesen wären. Ich dachte an mich selbst, vielleicht würden sie nicht zu schwer sein, aber ich schwieg, nichts schien mehr Sinn zu ergeben.",
          "",
          "",
          "Wenig später kam mein Vater mit einer Tüte Weißmehl auf dem Rücken nach Hause. Er erzählte uns, dass Leute in Nasers Lager eingebrochen sind. Tante Munevera hat Krapfen für uns gemacht. Ich vergaß völlig, dass ich seit Tagen nichts gegessen hatte und dass ich hungrig war. Und wenigstens aßen ich eins. Bald trennten wir uns. Vater hat mich irregeführt, als er sagte, er würde gehen, um ein paar Sachen zu holen, und dann würde er sich mit uns treffen, aber stattdessen ging er durch den Wald in Richtung Tuzla. Wir haben uns mit Tante Sena getroffen und sind weitergezogen. Es war eine Menschenmenge um das Krankenhaus. Ich sah Leute von Balkonen in nahe gelegenen Gebäuden springen. ",
          "",
          "Wir blieben nicht zu lange in der Dunkelheit dieses Gebäudes. Wir gingen weiter, konnten aber nicht weiter als bis zum Krankenhaus. Die Leute versuchten, in das UN-Lager in Vezionica zu gelangen, und jemand sagte, Zulfo Tursun bracht die Jungen aus dem Krankenhaus zurück. So haben wir meine Tante verloren. Mutter, Großmutter und ich hielten unter einem der Gebäude in der Nähe des Krankenhauses oberhalb von Vezionica an. In der Nähe explodierte eine Granate. Zuerst bedeckte mich meine Großmutter und dann meine Mutter, um mich vor herunterfallenden Glasstücken zu schützen. Ich erinnere mich, dass ich kaum in der Lage war, unter ihnen zu atmen.",
          "",
          "Nachdem die Granate explodiert war, versuchten Frauen, sich durch die Tore und Zäune der Vezionica zu drängen. Wir drei verschmolzen mit der Menge auf der Straße. Plötzlich hörte ich durch das Geräusch eine bekannte Stimme: Husnija !!! Vater kam aus dem Wald zurück. „Ich werde mit dir gehen, was auch immer passiert, passiert.“ Endlich wurde ein Teil des Vezionica-Zauns eingerissen. Vater trug mich irgendwie ins Lager und wir gingen zur Garage der UN-Fahrzeuge und Transporter. Eine weitere Granate explodierte. Granatsplitter rissen kleine Löcher durch die Blechwände der Garage. Sonnenlicht drang herein. Wir haben Mutter verloren. Wir haben Tante verloren. Es gab keine Tante, weder Edo, noch Dado, noch Keka, noch Onkel Kiko. Nur wir zwei. Vater sah die UN-Soldaten in ihren Lastwagen aus Vezionica aussteigen. Er packte mich und warf mich in den Anhänger des Lastwagens.",
          "",
          'Er half vielen anderen, auf den Lastwagen zu klettern. Der Mob drückte mich gegen die Seiten. Ich schaffte es kaum, meinen Kopf nach außen zu drehen, damit ich tauchen konnte. Mir wurde klar, dass Babo und ich keine Hände mehr hielten. Die Menge hatte uns getrennt. Die Leute stiegen ständig auf den Truck und ich hatte immer weniger Luft. Der Lastwagen erreichte langsam eine andere Basis, die "Batterie-Fabrik“. Babo und ich landeten in der Dunkelheit des Lagers. Auf dem Weg sahen wir den Onkel Ibro. Er war auf Bahren.',
          "",
          "Die Leute strömten in die Fabrik. Babo hat es irgendwie geschafft, eine Bahre zu finden, damit ich eine Weile sitzen konnte. Ich setzte mich an die Wand neben dem zertrümmerten Eingang. Die Bahre war nass. Mit Blut benetzt. Als ich dort saß, waren meine Augen auf die Tür gerichtet, in der Hoffnung, meine Mutter durchgehen zu sehen. Ich habe jegliches Zeitgefühl verloren. Plötzlich erschien Mutter. Sie war gerötet, als hätte sie gerade mit jemandem gerungen. Sie hat uns sofort entdeckt. Sie erzählte uns, dass UNPROFOR sie nicht hereinlassen wollte, also musste sie mit einem Soldaten streiten und kämpfen. Irgendwie gelang es ihr einzusteigen. Babo suchte uns etwas Wasser. Er brachte eine Flasche mit, die nach Benzin stank und ein wenig Wasser enthielt. Er erzählte uns, dass er Tante, Edo und Dado gefunden habe, aber Nana war nirgends zu finden. Mutter sagte, Nana sei mit unserer schwangeren Keka in einer anderen Fabrik geblieben.",
          "",
          "Ich konnte das Schreien und Schreien kaum noch hören oder auf die blutigen Gesichter und die Grautöne der Fabrikhalle schauen. Ich lehnte meinen Kopf zurück in ein Loch in der Wand und dachte, ich wäre eingeschlafen. Ich bin ein paar Minuten später aufgewacht. Mutter fragte, warum ich nicht schliefe. Ich möchte meine Tante sehen. Wir werden später gehen. Ich möchte dann auf die Toilette gehen. Sie hat mich hinter die Fabrik gebracht. Überall waren blutige und zerrissene Menschen, viel blutgetränkter Kot und Verband. Ich sagte, ich kann nicht auf die Toilette gehen, und ich ging nicht mehr.",
          "",
          "Irgendwann begann Mutter panisch durch unsere Taschen zu gehen. Sie nahm ein Bündel Besteck und rannte davon, um es wegzuwerfen. Innerhalb der Fabrik verbreitete sich die Nachricht, dass die Tschetniks, die als UN-Soldaten verkleidet in der Fabrik patrouillierten, nur nach Ausreden suchten, um Menschen herauszunehmen und sie zu schlachten.",
          "",
          "Babo fragte sie, warum sie das Besteck bei sich hatte.",
          "",
          '"Wie soll ich wissen, wohin wir danach gehen und wie wir enden werden? Ich dachte, wir brauchen sie vielleicht. "',
          "",
          "Ihr ganzer Schmuck war längst verschwunden, und jetzt warf sie alles Metall weg, was wir besaßen.",
          "",
          "Sie verstand, dass sie meine Anfragen nicht mehr ablehnen konnte und als die Leute aufhörten einzutreten, gingen wir zu Tante und Onkel Kika. Onkel Kika war erschreckend blass. Er war ein Mensch mit besonderen Bedürfnissen, aber er hatte nie so viel Angst wie damals. Mutter fragte Tante, wo Senaid, mein junger Onkel, sei. Sie sagte, dass er den Wald verlassen hatte. Ich näherte mich Edo und Dada, setzte mich neben sie auf eine Decke, die den Beton bedeckte, es war hart und kalt. So viele Gesichter, die wir noch nie gesehen haben, waren jetzt in der Halle um uns herum. Wir haben sie nie gesehen, aber wir hatten das Gefühl, sie immer zu kennen. Durch die Hallen hallten die Wellen der Panik. Man hörte, dass sie Menschen herausholten und schlachteten, vergewaltigten und erschossen. Mutter und Vater wurden mit den Menschen um uns herum murmeln, dass alles bald vorbei sein wird, dass wir mit den UNPROFOR Truppen OK es sein sollen, aber, wie der Tag dunkel wurde, wurde klar, dass wir  in diesen Fabriken tatsächlich in einem Lager waren.",
          "",
          "Wir haben es irgendwie durch die Nacht geschafft ... Ich zog meinen Kopf wieder in mein Loch in der Wand, Mutter lehnte ihren Körper an mich, als wollte sie mich zudecken, mich sicherer fühlen, alles, was ich die ganze Nacht hörte, war Stöhnen, das Weinen von Kindern, und irgendwo in der Ferne hinter der Fabrik durch diesen Eingang, schreit. Ich wusste nicht, was ich tun sollte, aber ich wusste nicht, was ich tun sollte, aber die ganze Nacht vor meinen Augen war Drinjacha, warme Milch von Pahljevichi, Himbeeren von Burinca, der Sonnenuntergang in Konjevic Polje ... alles mischte sich zufällig in meinem Kopf, während das Lager seinen Geruch und seine Dunkelheit in meine Erinnerungen bohrte.",
          "",
          "Die Aufregung und der Lärm begannen sehr früh am Morgen. Es kamen keine Stimmen zu uns, um zu sagen, worum es ging, aber die Leute summten durch die Halle und gingen hier und da hin. Vater sprang auf und ging durch die Türen, durch die wir kamen, um zu sehen, was los war. Später kam der Vater mit einem Weißblech, etwas Suppe und einer Scheibe durchsichtigem Brot. Die Suppe sah aus wie schmutziges Wasser, der Teller war flach, wir hatten unser Besteck weggeworfen und konnten es nicht einmal trinken. Wir wollten die Suppe mit dem Brot aufheben, aber es war darin geschmolzen.",
          "",
          'Während ich mir überlegte, wie ich diese Suppe zu mir nehmen sollte, erzählte mein Vater meiner Mutter, wie es ihm mit Onkel Ibro erging und wie er immer noch nicht von der Bahre aufstehen konnte, auf der wir ihn gestern gesehen hatten. Er sagte, er sei sehr blass und "ausgetrocknet". "Niemand hatte eine Infusion, um ihm zu geben." Ich bat meine Mutter und meinen Vater, mich spazieren gehen zu lassen. Auf der rechten Seite sah ich in der Ferne, dass es keine Wand gab und dass das Licht durchscheinte. Als ich an dieser Lücke ankam, sah ich ein Feld voller Menschen, genau wie die Leute in der Halle, die auf dem offenen Feld in der Sonne backten.',
          "",
          'Als ich zurückkam, konnte ich ihnen nicht sagen, was ich gesehen hatte. Mutter befahl mir, mich zu setzen, und dann ging ein Mann vor uns in die Hocke. Er fragte nach den Namen meines Vaters und meines. "Die UNPROFORs haben uns gesagt, wir sollen nur die Männer aufschreiben", sagte er. Vater sagte, dass ich Jasmin Jusufa Jusufovic bin und schrieb es auf. Oh, wie stolz ich war, als mein Vater meinen Namen so aussprach. Ich dachte, ich bin der Sohn meines Vaters, und niemand kann etwas für mich tun. Als der Landvermesser ging, rief ein Mann von der Seite aus: "Nicht UNPROFORs ... chetniks!"',
          "",
          "„Vater, warum hat dieser Mann nur mein und deine Name aufgeschrieben?“",
          "",
          "„Ich weiß es nicht. Vielleicht um zu wissen, wie viele Leute hier sind.“",
          "",
          "„Aber warum schrieb er nicht auch den Namen seiner Mutter auf? Wird er Nizza aufschreiben? Hat er Keka und Nana aufgeschrieben? Wer wird Kika aufschreiben?“",
          "",
          "„Mein liebes Kind, ich weiß es nicht.“",
          "",
          "Mutter und Vater sprachen mit den Menschen um uns herum. Zu uns in der Ecke kam das Wort, dass sie Tschetniks unter uns geschickt hatten, dass sie Leute in ein weißes Haus in der Nähe der Fabrik brachten, um zu rappen und zu schießen. Deshalb ließ mich meine Mutter nicht alleine auf dieses Feld hinaus in die Luft.",
          "",
          "Bitte, lass uns raus gehen und Luft schnappen.",
          "",
          "Ich will mich nicht hinsetzen.",
          "",
          "Bitte, Mama, nur um ein bisschen die Sonne zu sehen.",
          "",
          "Ich will nicht, setz dich!",
          "",
          "Aber Mama, lass uns nach Nizza gehen und Edo und Dado sehen, lass uns zusammen gehen.",
          "",
          "Wenn wir dort ankommen ...",
          "",
          "Komm schon Mama, ich will nur ein bisschen in diese Ecke gehen, du wirst mich von Nicas Stelle sehen.",
          "",
          "Alle halbe Minute drehte ich mich zur Mutter, um zu winken und zu bestätigen, dass wir uns immer noch sehen konnten. Dann hörte ich am Ende des Zauns Schreie und Schreie und schaffte es herauszufinden, was eine weibliche Stimme schrie. Ich rannte zu meiner Mutter, so schnell, dass ich dachte, ich könnte den Lauf der Zeit zurückdrehen.",
          "",
          '"Was ist passiert, worüber hat das geschrien?"',
          "",
          '"Ich weiß nicht, und die Frau schrie:" Er stampfte auf ihr Kind! "',
          "",
          '"Wer hat auf wessen Kind getrampelt?"',
          "",
          '"Ich weiß Mama nicht, ich hörte gerade eine Frau schreien:" Er stampfte auf ihr Kind! "',
          "",
          "Auf einmal herrschte eine tödliche Stille über den Menschen. Sie alle waren stumm, für einen Moment war kein Flüstern zu hören.",
          "",
          "Später kam durch Mundpropaganda die Nachricht, dass ein Tschetnik ein neugeborenes Baby von einer Frau, die gerade eine Wand zur Welt gebracht hatte, genommen und darauf getrampelt hatte.",
          "",
          "Ich wagte nicht mehr zu erwähnen, dass ich irgendwohin gehen möchte. Ich konnte nicht mehr darum bitten, zu einer Tante zu gehen. Ich habe nur nach meinem Loch in der Wand gesucht, um meinen Kopf zu verbergen und die beruhigenden Bilder von Drinjaca in mein Gedächtnis zurückzubringen.",
          "",
          "Als wieder dunkel wurde, fragte ich mich, dachte nach und redete unter meinem Kinn. Wann werden wir von hier wegkommen?",
          "",
          "Vater hatte mich gehört und dachte, ich würde ihn fragen, antwortete:",
          "",
          '"Morgen geht es los. Jetzt geh schlafen! "',
          "",
          "Panik in der Halle am 13. Juli weckte mich auf. Die halbe Halle war auf den Beinen, die Mutter sammelte die wenigen Dinge, die wir hatten, um mich und legte meinen kleinen Rucksack in meinen Schoß, in dem nur noch die Flasche stank, die nach Benzin roch.",
          "",
          '"Gehen wir?", Fragte ich. Niemand hatte mich gehört.',
          "",
          "Das Murmeln, das ich in den vergangenen Tagen als allgegenwärtiges Summen ignorieren konnte, war an diesem Morgen lauter geworden. Die Leute brachten Neuigkeiten, eine Frau, die ohnmächtig war, wurde etwas weiter links wieder zu Bewusstsein gebracht, und der Großvater half seinen Füßen, und die Mutter versuchte, ein schreiendes Baby zum Schweigen zu bringen.",
          "",
          "Ich habe mir gesagt, ich werde nicht so sein. Ich bin nett, ich werde nicht weinen und schreien und meine Mutter und meinen Vater beunruhigen. Jeder war bereit, irgendwohin zu gehen, aber wir zogen nicht um. Die Leute, die es schafften aufzustehen, bewegten sich nur und drehten sich um. Kurz darauf wurden in den Tiefen der Halle, auf derselben Seite, die wir betreten hatten, die großen Türen geöffnet, und Menschen wie ein Fluss begannen, durch diese eine Tür zu gehen. Wir gingen nicht direkt dorthin, aber die Menge trug uns durch die Mitte der Halle zu dem Ort, an dem die Tante saß, aber sie waren bereits weg.",
          "",
          "Plötzlich hielten wir neben einem grünen Lastwagen an, aber die Leute drängten sich immer noch auf die Türen zu. Ich stupste die Hand meines Vaters an und versuchte, ihn anzurufen:",
          "",
          "„Babo, ich kann nicht atmen!“ Er nahm mich und legte mich auf seine Schultern. Ich war auf der gleichen Höhe wie der Lastwagen. Der Truck hatte Leute drauf. Als wir endlich anfingen, als wir ausstiegen, wurde ich von der Sonne und der Hitze geblendet. Ich umklammerte meine Augen. Vater bemerkte, dass ich mich nicht mehr an seinen Schultern festhielt, also entfernte er mich von sich und stellte mich zwischen ihn und seine Mutter, und beide nahmen mich bei den Händen. Ich warf meinen Blick auf meine Beine, damit sich meine Augen an das Licht gewöhnten.",
          "",
          "Wir gingen durch eine lange Reihe von Soldaten. Ich sah nur echte Stiefel und Beine in Militärkleidung und Gewehrlauf. Sie zwangen uns zu schreien, wir mussten losrennen. Ich habe mich erst an die Sonne gewöhnt, als wir auf die Straße gingen. Vor uns befand sich links eine Reihe von Lastwagen, Bussen und Soldaten. Vater drückte Mutter und mich vor mich, Mutter drückte mich fest an sich und wickelte mich in ihre Kleider. Es war schwer so zu rennen. Wir fuhren an einem, dann an einem anderen LKW und Bus vorbei, bis jemand EINSTIEG rief! Und ich befand mich vor einer Holzleiter, die sich auf den Lastwagen stützte.",
          "",
          'Ich dachte, je früher ich die Leiter hinaufstieg, desto eher würden wir alle drei auf dem Lastwagen sitzen, und all das würde ein Ende haben. Auf halber Höhe der Leiter hörte ich einen Befehl hinter mir rufen: "Du, steig runter! Hier drüben! "Und ich hörte eine Aufregung. Ich stieg die wenigen verbleibenden Stufen hinauf und drehte mich um.',
          "",
          'Mutter saß vorne auf der Leiter, winkte mit den Händen und ein Soldat stieß meinen Vater mit einem Gewehr die Straße hinunter. Ich konnte nichts anderes sehen als uns drei und diesen Soldaten. Ich wurde steif. Der Vater wurde vom Soldaten in einen Straßengraben gezwungen, während ein anderer die Mutter anschrie, sie zum Klettern zu bringen. Mutter packte mich, völlig versteinert und trug mich tiefer in den Lastwagen. Ich schaute immer noch zu meinem Vater. Er stand im Graben. Er drehte sich um, um uns zu sehen. Als er sah, dass ich ihn ansah, legte er seinen Finger über seinen Mund, um mir "shhhhhh" zu sagen, und winkte mit der Hand, als wollte er "gehen" sagen.',
          "",
          'Ich habe nicht gehört, vielleicht haben wir geschrien, aber ich habe nicht gehört. Ich erinnere mich, dass ich nichts bewegen konnte. Ich habe meine Augen auf meinen Vater gerichtet. Ich wusste nur, dass ich meine Zähne fest zusammengebissen hatte und im Inneren wollte ich explodieren. Es ließ Tränen in meine Augen kommen, die meinen Blick auf meinen Vater beschlugen. Es gelang mir nur, eine Hand ein wenig zu heben, und in meinen Gedanken dachte ich, ich winkte ihm zu. Er wiederholte nur diese Bewegung von seinem "shhhh!" Los ". Er hatte nicht seine übliche Baskenmütze auf dem Kopf, ich dachte, es könnte heiß in der Sonne sein.',
          "",
          "Er trug ein cremefarbenes T-Shirt mit breiten azurblauen Streifen. Eine abgenutzte Lederweste. Schwarze Hosen und abgenutzte Schuhe, die einzigen, die er in Srebrenica hatte. Unter seinem Arm trug er meine rote Kapuzenjacke. Und er blieb und beobachtete uns. Los.",
          "",
          "Es war, als hätte mich jemand mitgenommen, mich aus dem Truck in den Himmel getragen. Ich sah, dass auf dem Graben um meinen Vater noch viel mehr Männer waren. Hinter ihnen sah ich reifen Weizen, der in der Sonne und im Wind herumspielte, hinter der Halle gingen noch Leute aus, einige stiegen in Busse. Ich sah Mutter auf dem Lastwagen, die sich gegen mich drückte, mich in ihr langes, weites Kleid wickelte und weinte.",
          "",
          'Von irgendwoher sprang ein Teenager auf den Boden des Lastwagens. Er kroch hinter einige Fässer, die dort waren, und dann warfen die Frauen die Bündel, die sie mit sich führten, auf ihn. Als der Lastwagen mit Leuten gefüllt war, schlossen sie die Plane und wir waren von einer völligen Dunkelheit umgeben, die mich wieder blind machte. Als der Truck startete, flüsterte ich mir zu: "Vater wird in einem anderen Truck sein ..."',
          "",
          "Der Lastwagen fuhr lange Zeit langsam und brachte uns irgendwohin. Unter der Plane brauchten wir Luft. Plötzlich hörten wir auf. Wir wurden alle still. Ich hörte die Türen des Lastwagens, und dann sah ich, wie ein langer Holzbalken ein Stück Plane für uns öffnete und schrie: Warum hast du dich geschlossen, du wirst ersticken, verdammt noch mal. Durch den offenen Teil sahen wir von der Straße, wo wir waren, wie sie waren, von irgendwo vor uns, den Wald beschießend. Ich erinnerte mich an meinen jungen Onkel, der durch den Wald ging. Der Holzbalken sprach wieder: Da, pass ein bisschen auf, damit dir nicht langweilig wird.",
          "",
          "Bei ein wenig Licht um mich herum sah ich eine Großmutter, deren Kopf in eine Art blutigen Verband gewickelt war, und hier waren auch die Augen von dem Verband bedeckt, und viele Frauen. Ich kann mich nicht erinnern, ein einziges Kind gesehen zu haben. Das Brett schloss sich wieder auf der Plane, und sie fuhren weiter. Lange nachdem der Lastwagen wieder angehalten hatte und meine Mutter mich auf den Boden legte und mich mit ein paar Dingen bedeckte. Die anderen Frauen um mich herum warfen ihre Sachen auf mich und dann fühlte ich, wie etwas Schweres über alles gelegt wurde, was auf mir lag. Danach hörte ich eine Stimme.",
          "",
          '"Sind hier Männer?", Sagten die Frauen, dass es keine gäbe, und dann fingen sie an zu schreien.',
          "",
          '"Es ist niemand anderes da, es war nur er! Es gibt sonst niemanden! "',
          "",
          "Der Truck startete kurz darauf erneut.",
          "",
          "Als wir wieder anhielten, luden sie uns vom Lastwagen auf einer Straße ab, unter der sich eine Wiese und ein Fluss befanden, und darüber eine andere Wiese, ein Haus und die Armee. Sie zeigten uns, dass wir die Straße entlang gehen sollten und sagten: Geh hin, ihre Leute sind da ... Mutter und ich gingen die Straße entlang ... wir waren beide allein.",
          "",
          "Wir haben von den Leuten gehört, dass der Ort, an den wir gekommen sind, Tishes heißt. Die Straße, auf die sie uns zeigten, führte in Richtung Kladanj. Wir liefen stundenlang und hielten gelegentlich an, um zu Atem zu kommen. Auf dem Weg wurden Bäume abgeholzt, sodass wir über einige klettern und unter die anderen kriechen mussten.",
          "",
          "Unterwegs sagten uns die Leute vom Straßenrand, geh einfach weiter geradeaus zum Tunnel, ihre Leute sind da nach dem Tunnel ...",
          "",
          "Der Tunnel war nirgends zu sehen, hinter jeder Straßenbiegung befand sich ein weiteres Band, unter der Straße ein Fluss, über der Straße eine Schlucht und durch diese ein Stück Himmel.",
          "",
          'Die erste Dunkelheit begann zu fallen und wir sahen endlich den Tunnel. Als wir durch den Tunnel gingen, fanden wir meine Tanten Safeta, Niska und Niho. Als sie mich sah, schrie. Aus ihrer Stimme brach die Schlucht, und ich erstarrte. Ich erinnere mich nicht, dass mich jemand so fest umarmt und zittert wie Niska es damals tat. Durch die Tränen, die sie immer wieder wiederholte "Vater ist weg", mein Vater ist weg "... Ich dachte, Onkel Muhamed sitzt auch in einem anderen Truck ... und dann fing ich an zu weinen. Ohne ein Geräusch strömen die Tränen einfach weiter.',
          "",
          "Von dort gingen wir nur, weil wir mussten. Ich war nicht anwesend in mir. Ich kann mich nur blitzartig an den Fensterrahmen im Bus erinnern, der uns nach Dubrave brachte, und an den roten Himmel im Fensterrahmen.",
          "",
          "Als wir in Dubrava ankamen, war es bereits dunkel. Mutter und ich schlenderten durch ein Meer unbekannter Silhouetten. Einige rannten in Richtung Zisternen, andere in Richtung Zelte. Dann sah Mutter eine Straßenlaterne, und wir saßen dort auf einem Kieshaufen. Außerhalb des Lichtkreises der Glühbirne fielen alle in Dunkelheit, als gäbe es niemanden mehr auf der Welt, nur Mutter und ich, die auf einem Kieshaufen saßen und warteten. Mutter sagte mir: „Sie werden uns zuerst sehen, wenn wir hier sind.“ Wir wussten nicht, ob sie es wirklich tun würden.",
          "",
          "Ich habe mich schon verloren ... Plötzlich rief jemand von irgendwoher unseren Namen:",
          "",
          '"Husnija! Jasko! "',
          "",
          "Wir haben nicht zur Kenntnis genommen, wir haben nicht geglaubt.",
          "",
          "Und aus dieser Dunkelheit erschien uns das Gesicht meines Cousins ​​Emir.",
          "",
          '"Ich bin es, Husnija! Emir! Erkennst du mich nicht? "Sagte er und weinte.',
          "",
          "Er gab uns Wasser zu trinken. Ich war für einen Moment da und für den nächsten weg. Ich erinnere mich, wie ich in seinem Auto aufwachte und neben ihm vor uns eine andere Frau sah, die ich nicht erkennen konnte.",
          "",
          "Zwischen dem Hin- und Herfallen des Bewusstseins bemerkte ich, dass sie uns nach Hause brachten. Die Frau vom Vordersitz machte ein paar Schritte. Als mich das Licht aus dem Raum oben im Treppenhaus traf, sah ich zwei Jungen und erkannte, dass es meine Cousine Kada war, die mich an der Hand führte.",
          "",
          'Ich weiß nicht wie oder wann, aber ich fand mich in verschiedenen Kleidern wieder und setzte mich vor einen Tisch, auf dem sich ein gebackenes Huhn befand. Ich keuchte, ich verlor mich, ich wusste nicht, was ich tun sollte. Ich schaue auf Kata und dann auf das Huhn ... Als ich ein Stück Huhn nahm, legte ich es in meine Hände und sagte: "Komm schon, Huhn, iss!"',
          "",
          "Das gebackene Huhn und das Stück Fleisch in meinen Händen, mit dem ich nichts anfangen konnte, das waren die letzten Dinge, an die ich mich an diesem Tag erinnerte. Ich weiß, dass ich geschlafen habe, wo und wie ich eingeschlafen bin, ich erinnere mich nicht. Ich konnte an diesem Tag einfach nicht mehr auf dieser Welt sein.",
          "",
          '1999 kehrte ich zum ersten Mal nach Srebrenica zurück. Die "Kinder Gesandtschaft" organisierte einen Besuch, und ich befand mich plötzlich mit einem Mikrofon in der Hand vor dem Stadtrat und musste nach einem beleidigenden Zwischenruf einer Stadträtin etwas sagen.',
          "",
          'Ich sagte ihnen: "Schau mich an! Du hast uns nicht alle umgebracht! "',
          "",
          "Ich erinnere mich, dass Dusko Tomic, mit dem ich gekommen bin, blass geworden ist und die Journalisten und Kameras plötzlich auf mich gefallen sind. Also fügte ich hinzu:",
          "",
          '"Durch den Willen Gottes bin ich nach meinem Vater am Leben geblieben, um über meinen Vater und das Zeugnis zu geben, was uns angetan wurde, ob es dir gefällt oder nicht."',
          "",
          "Ich weiß nicht, woher ich den Mut habe.",
          "",
          "Danach haben sie mich gepackt und wir sind schneller gegangen als wir angekommen waren. Ich hatte keine Zeit zu fragen, ob ich noch ein bisschen bleiben könnte, ob noch Bücher übrig wären, irgendetwas von meinem Vater, ob jemand in unserem Haus war oder ob sie meinen Schneeschlitten verbrannten.",
          "",
          "",
          "Autor: Jasmin Jusuf Jusufović",
          "Übersetzung von: Samira Sultanić",
          "Vielen Dank an: Irma, Džana & Iman"
        ]
      },
      Norwegian: {
        title: "Jasmin Jusuf Jusufović",
        subtitle: "Den lille prinsen",
        language: "Norwegian",
        paragraphs: [
          "Ved daggry den 8. juli smalt det et sted oppe i fjellet, akkurat høyt nok til å vekke oss. Jeg bestemte meg for å skjule meg like ved fryseren. Det er der husveggen er på sitt tykkeste. Disse dagene søkte vi ly mange ganger bak denne veggen. Vi fikk tiden til å gå ved å telle hver eneste granat som slo ned. Til tider falt det 30 stykker samtidig. Folk våget seg ut på gaten, overbevist om at det kun er det første skuddet som dreper. Brødbakeren Ramo patruljerte gatene og hentet sårede med bilen sin.",
          "",
          "Ved 12-tiden ble alt stille igjen. Jeg gikk ut for å se etter Edo og Dado. Jeg visste at pappa også var et sted i nabolaget. Mens jeg gikk ned Milosh’s gate, begynte jeg å lure på om pappa hadde gått til smeden like over elven. Da jeg kom frem til den åpne delen ved Nassers elv eksploderte det, men min største bekymring var at jeg måtte løpe tilbake til mamma for å fortelle henne at jeg var ok. Jeg løp innom huset til tante mens jeg skrek «jeg må løpe til mamma så hun ikke bekymrer seg!». Tanten min greide så vidt å holde meg igjen der Edo og Dado lå gjemt under bordet, mens hun prøvde å overbevise meg om at mamma forstod at jeg var sammen med dem.",
          "",
          "På tross av alle farene var vi sta nok til å leve livene våre som normalt. Morgenen den 10.juli gikk vi opp til bestemor for å hjelpe henne med å rydde rommet hennes. Jeg gikk ut på verandaen for å riste en rye. Da falt en ny granat bak huset. Jord og steiner falt som regn på taket, og i ti sekunder hang til og med jeg fra veggen. Alt levenet etter granaten stilnet rundt 12. Mamma og pappa bestemte seg for at jeg måtte til tanten min for å gjennomføre et gammelt rituale, eller på bosnisk «saljeva stravu», for å dempe redselen min. Ritualet er gammelt fra Bosnia og skal hjelpe mot traumer og redsel. Det består av å smelte bly og deretter blande det med vann, for så å lage figurer i det. Etter granaten som falt på bestemors tak hadde jeg nemlig sluttet å snakke, jeg hadde ikke sagt et eneste ord.  Foreldrene mine var bekymret, og trodde at noe hadde hendt meg. Da vi kom til tanten min og jeg så ned på vannet og blyet, sa jeg bare «hvor i alle dager ser du løven, tante?»",
          "",
          "Tanten min bodde på Kazani, mens vi bodde i sentrum. Det var en lang tur, og den ble enda lenger med trusselen om nye granatangrep tilstede. Den dag i dag klarer jeg fortsatt ikke å forstå hvordan foreldrene mine, sammen med den ene tanten min, uten telefonforbindelse, avtalte med min andre tante at vi skulle møtes i nærheten av busstasjonen. Etter at vi hadde gjennomført ritualet følte jeg meg ikke mindre redd. Og så begynte granatene å falle igjen. Hjemme hos oss var Edo og Dado, Edo med meg bak fryseren, og Dado bak den andre tykke veggen. Noen skrek at det falt på gamlebyen, og jeg begynte å bekymre meg for vennen min Kemo.    ",
          "",
          "Morgenen den 11.juli begynte skytingen tidlig, og den var nærmere enn noen gang tidligere. Det var varmt. Mamma og pappa pakket tingene våre i bæreposer, så vi ikke skulle flykte tomhendt slik vi gjorde da vi flyktet fra Drinjaca. Jeg hoppet opp og gjemte «Den lille prinsen» og «Tusen spørsmål – tusen svar»-bøkene i sekken min. Granatangrepene fortsatte uten stopp hele morgenen. Nede på gata så vi stadig flere mennesker løpe fra sentrum. Vi ventet uforståelig lenge, helt til noen fra den store folkemengden ropte «Tsjetnikene har kommet seg ned til markedet!». Vi dro med oss ryggsekkene våre, til og med meg løftet pappa opp som om jeg var en ryggsekk,  og vi løp ned til flyktende folkemengden og ble en del av den. Bak oss kunne vi høre skyting. Mens vi løp nedover gata, ble ermet mitt revet opp i et av gjerdene som tidligere var blitt truffet av granaten som drepte barna som hadde samlet seg for å spille fotballturnering utenfor skolen vår. Kanskje jeg til og med rev opp litt av armen, men det merket jeg i så fall ikke. Det eneste jeg fikk med meg var at mamma rev seg bort fra gjerdet, og vi løp inn i tante Munevera sitt hus like ved busstasjonen. Av en eller annen grunn stoppet vi der. Pappa løp tilbake mot sentrum, og mamma tok av meg sekken min, ristet den tom før bøker og så på meg som trakk på skuldrene. Det var sånn jeg måtte forlate den lille prinsen min. Da mamma så meg stående trist over bøkene mine, prøvde hun å trøste meg med at de ville være for tunge når vi måtte flykte. Jeg sa ingenting, selv om jeg tenkte kanskje ville de ikke være for tunge, men jeg forstod at den kampen var tapt. ",
          "Senere kom pappa tilbake til huset, bærende på en pose mel på ryggen. Han fortalte at folk hadde brutt seg inn og ranet Naser sin butikk. Av melet bakte tante Munevera boller til oss. Jeg hadde glemt at jeg var sulten og at jeg ikke hadde spist noe på flere dager frem til disse bollene. Likevel fikk jeg så vidt i meg en eneste bolle. Raskt måtte vi skilles igjen. Pappa sa at han skulle hente noe mer. Det var ikke sant, sannheten var at han rømte inn i skogen. På veien møtte vi tante Sena som vi fortsatte videre med. Ved sykehuset hadde det allerede samlet seg en hel mengde mennesker. Jeg så noen hoppe fra balkongene i de omkringliggende bygningene, som vi løp inn i for å søke ly.",
          "Vi ble ikke igjen lenge i mørket i bygningen før vi bestemte oss for å fortsette videre, men det viste seg at det var umulig å komme videre fra sykehuset. Noen prøvde å komme inn i FN-leiren som var lokalisert like ved tekstilfabrikken Vezionica, men noen sa at Zulfo Tursun sendte tilbake guttene fra sykehuset. I alt oppstyret mistet vi tanten min også. Bestemor, mamma og jeg stoppet igjen ved et av bygningene i nærheten av sykehuset. Et sted i nærheten falt en granat. Først kastet bestemor seg over meg, så mor, og så glasset som var igjen i bygningen. Jeg husker fortsatt hvor tungt det var å puste under tyngden fra bestemor og mammas kropper som beskyttet meg. ",
          "",
          "Etter granaten ble kvinnene enda mer opptatt av å komme seg inn i leiren gjennom Vezionica-gjerdet. Gjennom alt støyet gjenkjente jeg en stemme som ropte «Husnija!». Pappa hadde kommet tilbake fra skogen. «Jeg blir med dere, uansett hva». Et sted langs gjerdet var det et lite hull, og pappa klarte å bære meg inn i leiren og inn i FN-garasjen. Så falt en ny granat like ved. Granatsplint boret seg gjennom garasjens metalltak og hjalp lyset å trenge gjennom. Vi hadde mistet mamma. Vi så ikke tanten min heller, og ikke Edo eller Dado, ikke Keka, og heller ikke onkel Kiki. Det var bare oss to. Pappa la merke til hvordan FNs soldater prøvde å flykte fra Vezioniza i lastebilene sine. Han grep tak i meg, løp mot en av lastebilene og kastet meg opp på dekket. ",
          "",
          "Han hjalp flere med å komme seg opp på lasteplanet. Mengden begynte å klemme meg mot enden av dekket, men jeg fant en åpning slik at jeg fikk puste. I mengden og alt kaoset mistet jeg hånden til pappa. Flere folk hoppet på lasteplanet langs veien, og gjorde at jeg mistet det lille pusterommet jeg hadde hatt. Lastebilen nærmet seg sakte en annen base, en gammel bilbatterifabrikk. Pappa og jeg gikk langs veien forbi en rekke soldater på siden av veiene, inn i hallens mørke. Gjennom en korridor av mennesker og FN-soldater kjørte lastebilen inn i hallens mørke. På veien innover fikk vi et glimt av onkel Ibro som lå på en båre. ",
          "",
          "Flere og flere kom til fabrikken. Pappa vant en kamp om en båre som han tok med til meg så jeg kunne sitte i den. Jeg satte meg ned mot en vegg like ved den ødelagte inngangen. Båren var klissvåt. Av blod. Vi satt på båren, men øynene mine så kun på døra vi hadde kommet gjennom, i håp om at også mamma skulle dukke opp. Jeg hadde mistet tidsbegrepet fullstendig. Plutselig dukket mamma opp av intet. Hun var helt rød og hoven i ansiktet, man så at hun hadde grått, og det så ut som om hun hadde vært i en slåsskamp. Hun fikk øye på oss med en gang. Hun sa at FNs soldater ikke hadde villet slippe henne inn i fabrikken, men hun hadde diskutert med en av dem og sneket seg forbi til slutt. Senere dro pappa for å finne vann til oss. Han kom tilbake med en flaske som luktet bensin, men den hadde litt vann i seg. Han sa at han hadde funnet tante, Edo og Dado og onkel Kika, men ikke bestemor. Mamma sa at bestemor hadde blitt igjen i den andre fabrikken sammen med gravide Keka. ",
          "",
          "Jeg orket ikke å høre mer på alt bråket, skrikingen, se på alle de blodige ansiktene og den grå fabrikkhallen. Jeg fant et hull i veggen bak ryggen, som jeg la hodet mitt i, og midt i alt sammen var det som om jeg sovnet. Noen minutter senere kom jeg til meg selv igjen, og mamma lurte på hvorfor jeg ikke sov. Jeg sa jeg ville til tante, men det måtte visst vente. “Da vil jeg på do”. Hun tok meg med gjennom inngangen og til baksiden av fabrikken, og igjen så jeg blodige mennesker i istykkerrevne klær, i tillegg til masse avføring. Jeg sa jeg ikke ville på do lenger, og etter det gjorde jeg det heller ikke. ",
          "",
          "På et tidspunkt begynte mamma å snu og vende på all bagasjen vår i panikk. Hun tok ut litt bestikk og løp for å kaste det. Folk hadde begynt å snakke om hvordan tsjetnikkene patruljerte fabrikken forkledd som FN-soldater, på leting en grunn til å henrette noen.",
          "",
          "Pappa så på mamma. «Hva skal du med alt det der kvinne?». «Hvordan skal jeg vite hvor vi skal videre og hvordan dette kommer til å ende? Kanskje vi får bruk for det». Hun hadde for lengst blitt kvitt alle smykkene sine, men nå tok hun alt vi eide av metall og kastet det og. Hun forstod at hun ikke ville bli kvitt meg med det første, så hun gikk med på å lete etter tante og onkel Kika da antallet som kom gjennom inngangen begynte å roe seg. Onkel Kiko var skummelt blek. Han var en spesiell mann, men aldri før hadde jeg sett ham så redd. Mamma spurte tante hvor min yngre onkel Senaid var. Hun sa at han hadde rømt gjennom skogen. Jeg gikk bort til Edo og Dado og satte meg ned ved de på et lite teppe, som var lagt ut på betonggulvet. Det var hardt og kaldt. Nå var det mange nye ansikter i hallen som vi ikke hadde sett før nå. Vi hadde aldri sett de før, men likevel føltes det som om vi alltid hadde kjent de. Gjennom hallen gikk det bølger av panikk. Det gikk stadig nye rykter om folk som ble tatt med ut av hallen, slaktet, voldtatt og henrettet. Mamma og pappa snakket med de rundt oss om hvordan det snart vill være over, at vi var trygge siden vi satt med FNs soldater, men da mørket falt gikk det opp for alle at vi faktisk satt i en fangeleir.",
          "",
          "På en eller annen måte kom vi oss gjennom natten. Igjen hadde jeg lent hodet mitt inn i hullet jeg hadde funnet i veggen, med mammas kropp lent hardt mot meg, som for at jeg skulle føle meg tryggere. Det eneste jeg hørte hele natten var dype sukk, barn som gråt, og et sted der ute bak hallens inngang - skrik. Om det var en drøm eller om jeg virkelig forlot hallen vet jeg ikke, men i hvert fall kunne jeg gjennom hele natten se Drinjaca, varm melk fra Pahljevica, bringebær fra Burnica, solnedgangen i Konjevic Polje... Alt gikk i surr i hodet mitt mens lukten fra hallen boret seg inn i hukommelsen min.",
          " ",
          "Grytidlig begynte bråket og skrikingen. Vi fikk ikke vite hva som foregikk, men folk gikk konstant frem og tilbake i hallen. Pappa reiste seg og gikk til inngangen for å se hva som foregikk. Senere kom han tilbake med en metalltallerken med litt suppe og et stykke gjennomsiktig brød. Suppa så mer ut som skittent vann, og tallerkenen var liten. Siden vi hadde kastet bestikket vårt måtte vi dyppe brødet i suppa, men det gikk ikke, for så fort brødet kom i kontakt med suppa smuldret det opp. ",
          "",
          "Mens jeg prøvde å finne en måte å spise suppa på, fortalte pappa mamma at han hadde gått forbi onkel Ibro, men at han fortsatt ikke kunne reise seg fra båren vi hadde sett han i dagen før. Pappa sa at han så blek og uttørket ut. «Det er ingen som har infusjon å gi han». Jeg spurte om lov til å gå ut en tur. Til høyre kunne jeg se en åpning i veggen det kom lys gjennom. Da jeg kom bort til åpningen, så jeg en slette full av mennesker, akkurat sånn som inne i hallen, som satt og brant i solsteken.  ",
          "",
          "Tilbake i hallen fikk jeg ingen mulighet til å fortelle det jeg hadde sett. Mamma beordret meg til å sette meg ned, og en mann satte seg på huk foran oss. Han spurte om navnene til pappa og meg. «FN-soldatene har bedt oss om å skrive ned navnene på kun menn». Pappa sa at jeg var Jasmin, sønnen til Jusuf Jusufovic, og mannen foran oss noterte.  Jeg var så stolt da pappa sa navnet mitt på den måten. Jeg tenkte, «ja jeg er pappa sin, og ingen kan gjøre meg noe». Da mannen foran oss gikk, hørte vi noen fra venstre rope «FN-soldater du liksom! Det er tsjetnikene!». ",
          "«Men pappa, hvorfor skrev han bare ned mitt og ditt navn?»",
          "«jeg vet ikke. Kanskje så han skal vite hvor mange mennesker som er her inne..»",
          "«Men, hvorfor skrev han ikke da ned mamma sitt? Skal han skrive ned Nico sitt? Har han skrevet ned Keka og bestemor?»",
          "«Jeg vet ikke barnet mitt, jeg vet ikke.»",
          "Mamma og pappa snakket med de rundt oss. Ved siden av oss i hjørnet vårt hadde det spredt seg et rykte om at FN-soldatene hadde sluppet inn tsjetniker blant oss, og at tsjetnikene tok med seg folk til et hvitt hus ved siden som de voldtok og drepte.  Etter dette fikk jeg ikke lenger lov til å gå bort til åpningen. «Vær så snill mamma, kan jeg ikke få litt frisk luft?» maste jeg. Men mamma nektet og ba meg sette meg ned. «Mamma, kan vi ikke gjøre det bare så jeg kan se solen litt?». «Nei, sett deg ned». «Men mamma, la oss gå til Nico så vi kan se Edo og Dado». Vi gikk dit sammen. Da vi kom frem spurte jeg om jeg ikke fikk lov til å gå bort til hjørnet, hun kunne jo til og med se meg derfra. To ganger i minuttet snudde jeg meg mot mamma og vinket for å forsikre meg om at hun så meg. Plutselig hørte vi skriking og hyling fra en kvinne. Jeg løp så fort tilbake til mamma at jeg trodde jeg ville snu opp ned på tiden.",
          "",
          "«Hva skjedde, hva var den skrikinga?»",
          "«Jeg vet ikke, det var en kvinne som sa at de hadde kjørt over barnet hennes!»",
          "«Hvem kjørte ned hvem sitt barn?»",
          "«Jeg vet ikke mamma, jeg bare hørte en dame rope «de kjørte over barnet hennes!»",
          "",
          "Plutselig, til tross for alle menneskene, ble det dødsstille. Alle var så stille de kunne, det var ikke noen som hvisket engang. Senere gikk ryktet fra øre til øre om at en tsjetnik hadde tatt babyen til en dame som nettopp hadde født, og kjørt over den. Jeg turte ikke lenger å spørre om lov til å gå noe som helst sted. Jeg turte ikke å spørre om vi skulle gå bort til tante. Det eneste jeg gjorde var å lete etter det lille hullet mitt i veggen for å gjemme hodet mitt og drømme om Drinjaca. Da det ble mørkt igjen spurte jeg meg selv «når skal vi ut herfra?». Pappa overhørte meg, og svarte «imorgen. Gå og legg deg nå». ",
          "",
          "Den 13.juli ble jeg vekket av panikken som hadde bredt seg i hallen. Halvparten hadde reist seg opp, og mamma pakket sammen det lille vi hadde igjen av ting og ga meg sekken min, som nå kun inneholdt flasken som luktet bensin. «Skal vi dra?» spurte jeg, men ingen hørte spørsmålet. ",
          "",
          "Murringen som jeg til nå hadde greid å ignorere var veldig høy den morgenen. Det kom stadig nye rykter. Noen prøvde å få en kvinne tilbake ved bevissthet igjen, et annet sted forsøkte de å hjelpe en gammel mann med å reise seg, en annen kvinne prøvde å trøste det gråtende barnet sitt. Jeg sa til meg selv at sånn skal ikke jeg være. Jeg er en god gutt, jeg skal ikke gråte og skrike sånn at mamma og pappa må bekymre seg. ",
          "",
          "Alle så klare ut til å dra videre, men vi kom liksom aldri i gang. Folk reiste seg opp og beveget på seg, men likevel kom de seg ikke av sted. Kort tid etterpå, i den innerste delen av hallen, der vi også hadde kommet inn, ble store dører åpnet og som et hav av mennesker veltet alle ut av hallen. I starten ble vi tatt av strømmen og dratt mot sentrum av hallen, mot stedet hvor tanten min hadde sittet tidligere, men ikke lenger satt, i stedet for ut gjennom åpningen. Plutselig stod vi ved en grønn lastebil, men likevel presset menneskene seg mot åpningen. Jeg dro pappa i hånden og prøvde å si at jeg ikke fikk puste. Han dro meg opp på skuldrene sine så jeg var på høyde med lastebilen. Da vi endelig kom oss ut ble vi blendet av solen. Jeg lukket øynene. Da pappa merket at jeg ikke holdt fast i han slapp han meg ned mellom seg og mamma og de tok tak i hver sin hånd. Jeg prøvde å holde blikket festet på føttene mine så jeg skulle bli vant til det sterke lyset. ",
          "",
          "Vi gikk langs en lang formasjon av soldater. Jeg så ordentlige støvler, uniformer og rifleåpninger. De fikk oss til å gå raskere, de skrek til oss så vi måtte begynne å småløpe. Jeg kjempet mot lyset helt til vi kom oss ut på veien. Pappa presset meg og mamma foran seg, mamma holdt meg fast så jeg ble viklet inn i de lange klærne hennes, og det ble vanskelig for meg å løpe. Vi løp forbi en lastebil, så en annen, helt til noen ropte «INN!» og jeg befant meg foran en stige av tre som var lent opp til lastebil-planet. ",
          "",
          "Jeg tenkte at jo raskere jeg kom meg opp langs stigen, dess raskere ville vi tre være på lastebilen, og dess raskere ville alt dette ta slutt. Halvveis opp ropte noen «Du der! Ned! Hit!». Jeg gikk opp den siste delen av trappa og snudde meg. Mamma gned seg nervøst i hendene, og pappa ble presset bort fra oss av en soldat sitt gevær. Jeg så ingenting bortsett fra oss tre og soldaten. Jeg stivnet helt. Pappa ble presset bortover av soldaten, mens en annen tvang mamma til å klatre opp på lastebilen. Jeg så fortsatt bort på pappa. Han stod i grøfta. Han snudde seg mot oss som for å si hade. Da han så at jeg så på han førte han fingeren over leppene som om han prøvde å si «shhhh», før han gestikulerte i en slags «gå!».",
          "",
          "Jeg hørte ingenting. Kanskje vi til og med skrek, men jeg hørte ingenting. Jeg husker at ingen ting kunne roe meg ned. Øynene mine hang igjen etter pappa. Jeg merket at jeg presset tennene mine sammen, men fra innsiden holdt jeg på å eksplodere. Så kom det tårer, de tåket til synet mitt og gjorde det umulig å se pappa. Jeg klarte å løfte den ene armen høyt nok til å liksom vinke til han. Han gjentok bare den samme «shhhh, gå»-gestikuleringa. Han hadde ikke lenger alpeluen sin på hodet, og jeg tenkte at det var sikkert alt for varmt til å ha den på nå. ",
          "",
          "På seg hadde han en t-skjorte, kremfarget og med brede striper i en slags blåfarge. En litt slitt skinnvest, svarte bukser og slitte sko, de eneste han hadde i Srebrenica. Under armen hadde han min røde hettegenser. Og der ble han stående og se etter oss, med det samme “shhhh”-uttrykket på leppene. Det føltes som om noen løftet meg opp fra planet på lastebilen og opp mot himmelen. Jeg så at det stod mange andre mennesker sammen med pappa langs grøfta. Og bak dem så jeg hveteplantene som danset i vinden og sola, mens et uendelig hav av mennesker strømmet ut av hallen, og noen av de inn i bussene. Jeg så på mamma i lastebilen, og hvordan hun holdt meg innsvøpt i den lange kjolen sin, gråtende.",
          "",
          "Fra ingensteds dukket det opp en ung mann som hoppet på lastebilplanet og gjemte seg bak noen fat helst innerst, og kvinnene rundt ham dekket han i det de hadde av sjal. Da lastebilen var fylt opp, lukket de presenningen og jeg ble blind av det plutselige mørket. Da lastebilen kjørte av gårde hvisket jeg stille for meg selv “pappa kommer nok med en annen lastebil…”",
          "",
          "",
          'Lastebilen kjørte lenge og sakte. Det var lite luft under presenningen, så det ble vanskelig å puste. Plutselig stoppet den. Alle ble stille. Jeg kunne høre døra til førersetet åpne seg, før noen løftet presenningen, og en mann skrek og bannet: "Hvorfor har dere presenningen over dere, dere kveles under den, gud måte .... dere". Gjennom den lille åpningen kunne vi ut i fra veien se hvor vi var, og hvordan avfyrte granater traff ulike steder i skogen. Jeg tenkte på onkelen min som hadde flyktet over skogen. Igjen snakket mannen som hadde åpnet presenningen. “Ja, det er bare å se, viktig at dere ikke kjeder dere.” I det lille som var av lys så jeg en gammel dame med hele hodet innpakket i en blodig bandasje, til og med øynene var pakket inn. Jeg så også veldig mange andre kvinner, men jeg kan ikke huske å ha sett et eneste barn. Presenningen kom over oss igjen, og lastebilen fortsatte å kjøre. Etter en lang stund stoppet den igjen. Mamma satte meg ned på gulvet og dekket meg til. Noen av de andre damene kastet også ting over meg, til og med noe som var litt tungt på toppen av det hele. Så hørte jeg en stemme. ',
          "",
          "“Er det noen menn om bord?” Kvinnene avkreftet. Så begynte bråket. “Nei, det er ingen flere her! Det var kun han! Det er ingen flere!”. Ikke lenge etter begynte lastebilen å kjøre igjen. Da de stoppet igjen, lastet de oss ned fra lastebilplanet og ned på veien. På den ene siden var det en elv og en dal. På den andre siden var det en annen elv, et hus og soldater. De pekte oppover veien og sa “gå oppover den veien, der er deres egne”. Mamma og jeg begynte å bevege oss i den retningen vi ble fortalt. Vi var alene. Så overhørte vi noen andre si at vi var i Tischa. Veien vi gikk førte til Kladanj. Vi gikk i flere timer, og bare noen få ganger stoppet vi for å komme oss til hektene. Det var trær langs hele veien, noen måtte vi krype under og andre måtte vi klatre over. Underveis hørte vi soldatene si “bare fortsett rett frem, rett frem til tunnelen, fortsett rett frem til tunnelen og deres egne”. Vi så ingen tunnel. For hver sving kom en annen. På den ene siden var elva, på den andre åssiden, og over oss kunne vi se en liten del av himmelen. Mørket hadde begynt å falle, men endelig kunne vi se tunnelen. ",
          "",
          "Da vi kom frem så vi tante Safeta, Niska og Niho. Da Niska så meg skrek hun, og jeg stivnet igjen. Jeg kan ikke huske at noen, noen som helst gang holdt jeg så tett, eller skalv så mye som hun gjorde da. Gjennom gråten var det eneste hun sa “pappa er ikke her, pappaen min er ikke her…”. “Da må onkel Muhamed også være i den andre lastebilen” tenkte jeg stille, før jeg også begynte å gråte. Tårene kom bare av seg selv. ",
          "",
          "",
          "Derfra gikk vi bare så langt som vi måtte. Jeg var ikke meg selv. I tåkete fragmenter minnes jeg bilder av vindusrammen på bussen som tok oss til Dubrave, og gjennom vindusrammen den røde himmelen. Da vi kom frem til Dubrave var det allerede bekmørkt. Mamma og jeg gikk rundt blant alle de ukjente ansiktene. Krasjet i andre.  Noen løp mot sisterne, andre løp mot teltene. Mamma fikk øye på en lysstolpe hvor vi satte oss på toppen av en grushaug. Det var som om der lyset ikke traff, eksisterte det heller ingenting. Det var kun mamma og meg, på denne grushaugen, som satt og ventet. Mamma fortsatte å si “her kommer de til å finne oss”. Vi visste ikke om det i det hele tatt ville komme noen. ",
          "",
          "Jeg nesten falt ut av bevissthet da noen ropte navnene våre. “Husnija! Jasko!”. Vi svarte ikke, vi trodde ikke at vi hadde hørt riktig. Plutselig, ut av mørket, dukket ansiktet til fetter Emir opp. “Husnija, det er meg! Emir! Kjenner dere meg ikke igjen?”. Han stod der og gråt. Han ga oss vann vi kunne drikke. Men jeg var bare halvveis tilstede, på randen av bevissthet. ",
          'Jeg sov allerede da jeg hørte noen ropte våre navn: Husnija! Jasko! Vi svarte ikke, vi trodde ikke det skjedde. Plutselig ut av mørket dukket det opp ansiktet til min fetter Emir. "Husnija, det er meg! Emir! Kjenner dere meg ikke?» Han sto der og gråt. Han ga oss vann å drikke. Men jeg var bare dels til stedet. Jeg var på randen av bevisstheten, plutselig tilstede, plutselig ikke. Jeg husker at jeg våknet opp i bilen hans, ved siden av han, foran oss, så jeg en kvinne som jeg ikke gjenkjente. I all kampen mot bevisstheten innså jeg at vi hadde kommet til huset vårt. Kvinnen som hadde sittet foran i bilen hjalp meg med opp til huset. Da vi stod på toppen av trappa kunne jeg se to gutter sitte inne, og jeg innså at kvinnen som holdt meg i hånden var kusinen min Kada. ',
          "",
          "Jeg kan ikke huske verken når eller hvordan, men plutselig var jeg ikledd andre klær ved et bord med stekt kylling. Jeg var helt borte, men munnen min var vidåpen. Jeg visste ikke hvor jeg skulle gjøre av meg. Først så jeg på Kada, så på kyllinge. Kada ga meg en bit av kyllingen, og sa “spis!”. ",
          "",
          "Den kyllingbiten er det siste jeg kan huske fra den dagen. Jeg vet at jeg sov, men jeg vet ikke hvor, når eller hvordan jeg hadde sovnet. Det var som om jeg ikke lenger aktet å være tilstede i verden den dagen. ",
          "",
          "",
          "Den første gangen jeg kom tilbake til Srebrenica var i 1999. Det var barnas ambassade som hadde arrangert besøket. I rådhuset stod jeg foran bystyrets medlemmer med en mikrofon i hånda. ",
          "Første gang jeg kom tilbake til Srebrenica, var i 1990. Barnas ambassade hadde arrangert det besøket. Jeg sto i rådhuset med en mikrofon i hånden foran bystyrets medlemmer, som om jeg måtte svare på den krenkende kommentaren fra en av kvinnene i bystyret. ",
          "",
          "Jeg sa “se på meg! Dere greide ikke å drepe oss alle!”. Jeg husker at Dusko Tomic, som jeg hadde kommet dit med, ble redd i det journalister og kameraene deres viet all sin oppmerksomhet mot meg. Jeg la til “Jeg er her på grunn av min far, etter Guds vilje, for å vitne om han og om alt det som ble gjort mot oss, enten dere liker det eller ikke”. Jeg vet ikke hvor det plutselige motet kom fra. ",
          "",
          "Etter dette måtte vi ut raskere enn vi hadde kommet. Jeg rakk ikke en gang be de om å gi meg litt tid, litt tid til å se om bøkene mine fortsatt var der, om det lå noe igjen etter pappa, om det var noen i huset vårt, eller om noen hadde brent akebrettet mitt. ",
          "Translation by: Lamija Delalić, @AdisDelalic & @AlijaTrako"
        ]
