      Russian: {
        title: "Jasmin Jusuf Jusufović",
        subtitle: "Маленький принц",
        language: "Russian",
        paragraphs: [
          "На рассвете, 8 июля, где-то в лесу гремело достаточно громко, чтобы разбудить нас. Тогда я переместился в свое укрытие, рядом с морозильной камерой, где была самая толстая стена. В течение этих нескольких дней мы  много раз бежали к самой толстой стене в доме, а во время бега мы считали гранаты. С того 9 июля, чтобы сократить время в моем убежище, рядом с морозильной камерой, я проводил время, подсчитывая снаряды, которые детонировали в течение всего дня. Временами их было тридцать. Люди выходили на улицы, но только с правилом «Первый убивает». Рамо Хлебара (продавец хлеба Рамо) прогремел по улицам с ранеными в своем грузовике ТАМ.",
          "",
          "Снаряды прекратились, и в полдень все стихло. Я вышел посмотреть, что делают Эдо и Дадо. Я знала, что мой баба (babo = папа) был где-то по соседству. Когда я спустился по улице Милошевой, я подумал, что бабо, возможно, пошел к кузнецу через реку. Взрыв снаряда разразился, когда я был в центре разминирования, рядом с плотиной Насера. В тот момент меня больше всего беспокоило возвращение к маме, чтобы она могла хотя бы увидеть, что со мной все в порядке. Возвращаясь домой, я вбежал в дом моей тети и закричал: «Я иду к маме, чтобы она не волновалась!» Моей тете едва удалось удержать меня в убежище под столом, где Эдин и Дадин уверяли меня, что моя мама наверняка знала, что я была у них.",
          "",
          "Несмотря на все, мы были достаточно упрямы, чтобы продолжать жить нормальной жизнью. Утром 10 июля мы поднялись наверх, чтобы помочь Нане убрать ее комнату. Я потянулся и вышел на террасу, чтобы подмести ее. Снаряд взорвался позади дома. Земля падала на крышу, как дождь, и я провел около десяти секунд, вися на стене. Когда в полдень все беспорядки среди обстрела утихли, мои родители решили пойти в дом моей тети, чтобы выполнить ритуал «заливки свинца», который, как полагают в старой балканской традиции, помогает при страхе и эмоциональной травме из-за того, что я не произнес ни слова с тех пор, как взорвался этот снаряд. Они думали, что со мной что-то случилось. Во время ритуала я заглядывал внутрь чаши со свинцом и водой и продолжал спрашивать мою тетю: «Где вы вообще видите льва?»",
          "",
          "Моя тетя жила в Казани, в то время как мы жили в городе, который был довольно далеко, чтобы быть под угрозой обстрела. До сегодняшнего дня мне неясно, как им удалось связаться без телефона или какой-либо другой связи, с которой мы должны были встретиться в доме моей другой тети рядом с автобусной станцией, на полпути к их дому. Когда ритуал литья свинца был закончен, я не почувствовал себя менее испуганным. Затем обстрел начался снова. Эдо и Дадо были в нашем доме. Эдо и я были в моем убежище у морозильной камеры и Дадо у другой толстой стены. Кто-то сказал, что снаряды взорвались по всему Старому городу, и я начал беспокоиться о своем друге Кемо.",
          "",
          "Утром 11 июля стрельба началась рано, ближе, чем когда-либо прежде. На улице был жаркий день. Мама и Бабо упаковывали наши вещи в мешки для покупок, так что, если мы уйдем, мы не пойдем с пустыми руками, как при выходе из Дриньячи. Я спрыгнул и упаковал свой школьный портфель с Маленьким принцем и энциклопедией «1000 zašto - 1000 zato» (1000 почему - 1000 потому что). Обстрел продолжался все утро. Все больше людей стали выезжать из города, бегать. Мы глупо чего-то ждали. Тогда кто-то из толпы закричал: «Четники пришли на рынок!» Мы схватили наши сумки, мой отец схватил меня, как сумку, и мы сбежали с холма, на котором был расположен наш дом, чтобы присоединиться к толпе людей, спасающихся бегством. Позади мы слышали взрыв.",
          "",
          "Когда мы бежали по улице, я разорвал свой правый рукав об забор. Тот же забор, который был взорван гранатой, которая также убила детей, играющих в футболь перед нашей школой. Возможно, я также порезал руку, но в тот момент я этого не чувствовал. Единственное, что я увидел, это то, как моя мама сорвала меня от этого забора, и мы вбежали в дом моей тети Муневеры на автобусной станции. Мы остановились по какой-то причине. Бабо побежал обратно в город, и мама взяла мой рюкзак за спину, вытряхнула мои книги и посмотрела на меня. Я только пожал плечами. Вот так мой маленький принц остался позади. Когда она увидела, что я застыл над моими книгами, она утешила меня, сказав, что они были бы слишком тяжелыми для моей спины, пока мы бежали. Я думал про себя, что, может быть, они не были бы слишком тяжелыми, но я молчал, казалось, ничто больше не имело смысла.",
          "",
          "Чуть позже мой отец пришел к дому с мешком белой муки на спине. Он сказал нам, что люди ворвались на склад Насера. Тетя Муневера сделала для нас оладьи. Я полностью забыл, что я не ел несколько дней и был голоден. Я едва съел один. Вскоре мы расстались. Отец ввел меня в заблуждение, сказав, что он уходит за какими-то вещами, а потом встретится с нами, но вместо этого он пошел через лес к Тузле. Мы встретились с тетей Сеной и пошли дальше. Вокруг больницы была толпа. Я видел людей, прыгающих с балконов в соседних зданиях. Мы вошли в один из них.",
          "",
          "Мы не оставались в темноте этого здания слишком долго. Мы продвинулись вперед, но мы не могли идти дальше, чем больница. Люди пытались попасть в лагерь ООН в Везионице, и кто-то сказал, что Зульфо Турсун отгоняет мальчиков от больницы. Вот так мы потеряли мою тетю. Мама, бабушка и я остановились под одним из зданий возле больницы, над Везионицей. Рядом взорвалась граната. Сначала моя бабушка, а затем и моя мама прикрывали меня, пытаясь защитить меня от осколков стекла, падающих со здания. Я помню, что едва мог дышать под ними.",
          "",
          "После того, как снаряд взорвался, женщины попытались прорваться через ворота и заборы Везионики. Мы втроем слились с толпой на улице. Вдруг сквозь шум я услышал знакомый голос: Хусния !!! Отец вернулся из леса. «Я пойду с тобой, будь что будет». Одна часть забора Везионика была наконец взломана. Отец каким-то образом отвел меня в лагерь, и мы пошли в гараж ООН для автомобилей и транспортеров. Еще один снаряд взорвался. Осколки прорвали маленькие отверстия в жестяных стенах гаража. Солнечный свет просочился внутрь. Мы потеряли мать. Мы потеряли тетю. Там не было ни тети, ни Эдо, ни Дадо, ни Кеки, ни дяди Кико. Только мы вдвоем. Отец видел, как солдаты ООН выходили из Везионицы на своих грузовиках. Он схватил меня и бросил в трейлер грузовика.",
          "",
          "Он помог многим забраться на грузовик. Толпа толкнула меня к бокам. Мне едва удалось повернуть голову наружу, чтобы я мог дышать. Я понял, что мы с Бабо больше не держались за руки. Толпа разлучила нас. Люди постоянно забирались на грузовик, и у меня было все меньше и меньше воздуха. Грузовик медленно прибыл на другую базу, завод «Акумулаторка». Бабо и я оказались в темноте склада. По пути мы увидели дядю Ибро. Он был на носилках.",
          "",
          "Люди начали заполнять завод. Бабо каким-то образом удалось найти носилки, чтобы я мог немного посидеть. Я сел у стены, рядом с разбитым входом. Носилки были мокрыми. Мокрыми от крови. Пока я сидел там, мои глаза были прикованы к двери, надеясь увидеть, как моя мама проходит сквозь них. Я потерял чувство времени. Внезапно появилась мама. Она была красной, как будто она только что боролась с кем-то. Она сразу заметила нас. Она сказала нам, что СООНО не хотели впускать ее, поэтому ей пришлось спорить и сражаться с каким-то солдатом. Каким-то образом ей удалось войти. Бабо пошел, чтобы найти нам немного воды. Он принес бутылку с запахом бензина, в которой было немного воды. Он сказал нам, что нашел тетю, Эдо и Дадо, но Наны нигде не было. Мама сказала, что Нана осталась на другом заводе с нашей беременной Кека.",
          "",
          "Я не мог больше слушать крики или смотреть на окровавленные лица и серость фабричного зала. Я откинул голову обратно в дыру в стене и, похоже, уснул. Я проснулся через несколько минут. Мать спросила, почему я не сплю. Я хочу видеть мою тетю. Мы пойдем позже. Я хочу пойти в туалет. Она взяла меня за фабрику. Там были окровавленные и рваные люди, везде много фекалий и бинтов, пропитанных кровью. Я сказал, что не могу пойти в туалет, и я больше не ходил.",
          "",
          "В какой-то момент мама в панике начала копаться в наших сумках. Она взяла сверток с серебром и убежала, чтобы выбросить его. Внутри фабрики распространились слухи о том, что четники, одетые как солдаты ООН, патрулировали фабрику, и просто искали оправдания, чтобы выводить людей и убивать их.",
          "",
          "Бабо спросил ее, почему, во имя Бога, у нее было это серебро.",
          "",
          "«Откуда я должна знать, куда мы пойдем после этого и где мы окажемся. Я думала, что оно могло бы нам понадобиться. «",
          "",
          "Все ее драгоценности давно исчезли, и теперь она выбросила все металлическое, что у нас было.",
          "",
          "Она поняла, что больше не может отказывать в моих просьбах, поэтому, когда люди перестали входить, мы пошли искать тетю и дядю Кику. Дядя Кика был пугающе бледен. Он был человеком с особыми потребностями, но он никогда не был так напуган, как тогда. Мать спросила тетю, где Сенэйд, мой младший дядя. Она сказала, что он ушел через лес. Я подошёл к Эдо и Дада, сел рядом с ними на одеяло, которое покрывало бетон, было твердо и холодно.",
          "",
          "Так много лиц, которых мы никогда раньше не видели, теперь были вокруг нас в зале. Мы никогда не видели их, но чувствовали, что всегда знали их. Сквозь залы отразились волны паники. Уши подхватили известие, что они забирают людей и убивают их, насилуют и расстреливают. Мама и папа роптали с окружающими нас людьми, что все скоро закончится, что с войсками СООНО все должно быть в порядке, но с наступлением темноты стало ясно, что, находясь на этих фабриках, мы фактически находились в лагере.",
          "",
          "Мы кое-как продержались ночь… Я снова втянул голову в дыру в стене, мама прислонилась ко мне своим телом, чтобы прикрыть меня, чтобы я чувствовал себя в большей безопасности, все, что я слышал всю ночь, - это стоны, крики детей, крики где-то на расстоянии позади фабрики через этот вход. Снился ли мне сон или мой дух просто покинул этот зал- я не знаю, но всю ночь перед моими глазами была Дриньяча, теплое молоко из Пахлевичей, малина из Буринки, закат в Коневич-Поле - все смешалось в моей голове в тот момент, когда лагерь отпечатывал в моих воспоминаниях запах и тьму.",
          "",
          "Суета и шум начались очень рано утром. Никто не подошёл, чтобы сказать, о чем идет речь, но люди гудели по всему залу, ходили туда-сюда. Отец прыгнул и прошел через двери, через которые мы прошли, чтобы увидеть, что происходит. Позже приехал отец с жестяной тарелкой, супом и кусочком прозрачного хлеба. Суп выглядел как грязная вода, тарелка была неглубокой, мы выбросили столовые приборы и даже не могли ее выпить. Мы хотели помакать хлеб в суп, но он растаял в нем.",
          "",
